import { useContext } from "react";
import { TargetCt, FixedCts } from "../../index";
import { Text } from "../../../../styledCpns/DataDisplay";
import { Select } from "../../../../styledCpns/Inputs";
import { SetterSelect, SetterCheckbox } from "../CmnCpns";
import monsters from "../../../../data/monsters";
import { deepCopy } from "../../../../helpers/misc";

export default function Monster() {
  const { monster } = useContext(TargetCt);
  const { setMonster } = useContext(FixedCts);
  const monData =
    monsters[monsters.findIndex(({ name }) => name === monster.name)];

  const changeMonster = (name) => {
    setMonster(() => {
      const data = monsters[monsters.findIndex((ms) => ms.name === name)];
      const inputs = [];
      if (data.inputTypes) {
        data.inputTypes.forEach((type, i) => {
          if (type === "variant") {
            inputs.push(data.inputs[i][0]);
          } else if (type === "check") {
            inputs.push(data.inputs[i]);
          }
        });
      }
      return { name, inputs };
    });
  };

  return (
    <div>
      <Select
        className="mb-3 p-1"
        borderRadius="0.25rem"
        bgColor="white"
        fontSize="1.125rem"
        fontWeight={700}
        value={monster.name}
        onChange={(e) => changeMonster(e.target.value)}
      >
        {monsters.map(({ name }, i) => (
          <option key={i}>{name}</option>
        ))}
      </Select>
      {monster.inputs.map((inp, i) => (
        <InputLine
          key={i}
          index={i}
          inp={inp}
          monData={monData}
          changeVariant={(e) =>
            setMonster((prev) => {
              const newMon = deepCopy(prev);
              newMon.inputs[i] = e.target.value;
              return newMon;
            })
          }
          check={() =>
            setMonster((prev) => {
              const newMon = deepCopy(prev);
              newMon.inputs[i] = !inp;
              return newMon;
            })
          }
        />
      ))}
    </div>
  );
}

function InputLine(props) {
  const { index, inp, monData } = props;
  const inpType = monData.inputTypes[index];
  let inpCpn;
  if (inpType === "variant") {
    inpCpn = (
      <SetterSelect value={inp} onChange={props.changeVariant}>
        {monData.inputs[0].map((opt, j) => (
          <option key={j}>{opt}</option>
        ))}
      </SetterSelect>
    );
  } else if (inpType === "check") {
    inpCpn = (
      <SetterCheckbox type="checkbox" checked={inp} onChange={props.check} />
    );
  }

  return (
    <div className="mb-3 flex justify-end align-center">
      <Text className="mr-3">{monData.labels[index]}</Text>
      {inpCpn}
    </div>
  );
}
