import { LGreen, Green } from "../../../../styledCpns/DataDisplay";

export const buffs = [
  {
    src: "Ascension 4 Passive Talent",
    desc: () => (
      <>
        A character that passes through the Jade Screen will gain a{" "}
        <Green>12%</Green> <LGreen>Geo DMG Bonus</LGreen> for 10s.
      </>
    ),
    isGranted: (char) => char.ascension >= 4,
    affect: "party",
    addBnes: ({ ATTRs }) => {
      ATTRs["Geo DMG Bonus"] += 12;
    }
  }
];
