import styled from "styled-components";
import { Collapse } from "@material-ui/core";
import { useMemo, useState } from "react";
import { Box, ModalCarpet } from "../../styledCpns/Layout";
import {
  Text,
  ColoredSpan,
  Green,
  List,
  CollapseHeading
} from "../../styledCpns/DataDisplay";
import { Button, IconBtn, TextArea } from "../../styledCpns/Inputs";
import artifacts from "../../data/artifacts";
import { colorCodes } from "../../data/misc";
import { useCloseModal } from "../../helpers/hooks";
import encodeExportedData from "../../helpers/encode";

export const CloseBtn = (props) => (
  <IconBtn icon="fas fa-times" variant="negative" size="1.75rem" {...props} />
);

export const InfoSign = (props) =>
  props.on ? (
    <CloseBtn size="1.5rem" fontSize="0.875rem" />
  ) : (
    <IconBtn
      className="info_sign"
      icon="fas fa-info-circle"
      size="1.5rem"
      fontSize="1.375rem"
    />
  );

export function ButtonBar(props) {
  const { availables, variants, distance = "ml-5" } = props;
  return (
    <div className={"flex justify-center " + (props.className || "")}>
      {props.texts.map((txt, i) => {
        const available = !availables || (!!availables && availables[i]);
        return (
          <Button
            key={i}
            className={
              (i ? distance + " " : "") +
              (available ? "glow-on-hover" : "unavailable")
            }
            variant={
              variants ? variants[i] : ["negative", "positive"][i] || "default"
            }
            onClick={props.handlers[i]}
          >
            {txt}
          </Button>
        );
      })}
    </div>
  );
}

export function EquippedChar({ user }) {
  return (
    <Box className="px-4 py-1" bgColor="#ffe7bb">
      <Text fontWeight={700} color="black">
        Equipped: {user}
      </Text>
    </Box>
  );
}

export function NoItems(props) {
  return (
    <div className="pt-5 flex-center">
      <Text variant="h5" fontWeight={700} color="lightRed">
        No {props.type} to display
      </Text>
    </div>
  );
}

const StyledSvg = styled.svg`
  width: 1.25rem;
  height: 1.25rem;
  fill: ${(props) => colorCodes[props.rarity]};
`;

export const Star = (props) => (
  <StyledSvg viewBox="0 0 24 24" rarity={props.rarity}>
    <path d="M12,17.27L18.18,21L16.54,13.97L22,9.24L14.81,8.62L12,2L9.19,8.62L2,9.24L7.45,13.97L5.82,21L12,17.27Z" />
  </StyledSvg>
);

export const StarLine = ({ rarity, className = "" }) => {
  const result = [];
  for (let i = 0; i < (rarity === "Gold" ? 5 : 4); i++) {
    result.push(<Star key={i} rarity={rarity} />);
  }
  return <div className={"flex align-center " + className}>{result}</div>;
};

export function SetBonus({ sets }) {
  return (
    <div>
      <Text variant="h6" color="orange" fontWeight={700}>
        Set Bonus
      </Text>
      {sets.length > 0 ? (
        sets.map((set, i) => {
          const content = [];
          for (let i = 0; i <= set.bonusLv; i++) {
            content.push(
              <li key={i} className="mt-1">
                <ColoredSpan color="orange">
                  {artifacts[set.name].setBnes[i].type}:
                </ColoredSpan>{" "}
                <ColoredSpan color="green">
                  {artifacts[set.name].setBnes[i].desc}
                </ColoredSpan>
              </li>
            );
          }
          return (
            <div key={i} className="mt-1">
              <Text variant="h6" fontWeight={700} color="green">
                {set.name}
              </Text>
              <List className="pl-4">{content}</List>
            </div>
          );
        })
      ) : (
        <Text color="rgb(185, 185, 185)" fontWeight={700}>
          No Set Bonus
        </Text>
      )}
    </div>
  );
}

export const TalentXtraLv = ({ xtraLv }) =>
  xtraLv ? (
    <Text className="ml-2" fontWeight={700} color="green">
      +{xtraLv}
    </Text>
  ) : null;

export const AmpRxnDesc = ({ elmt, mult }) => (
  <>
    Increases <ColoredSpan color={elmt}>{elmt} DMG</ColoredSpan> by{" "}
    <Green>{Math.round(mult * 1000) / 1000}</Green> times.
  </>
);

export function Confirmation(props) {
  const { close, otherBtn } = props;
  const ref = useCloseModal(close);
  return (
    <ModalCarpet>
      <Box
        ref={ref}
        className="p-3 relative"
        width="22.5rem"
        borderRadius="1rem"
        bgColor="cardBg3"
      >
        <div className="py-3">
          <Text style={{ fontSize: "1.375rem" }} align="center">
            {props.message}
          </Text>
        </div>
        <ButtonBar
          className="mt-3"
          texts={[otherBtn?.text || "Cancel", "Confirm"]}
          handlers={[
            () => {
              if (otherBtn?.handleClick) otherBtn.handleClick();
              close();
            },
            () => {
              props.confirm();
              close();
            }
          ]}
        />
      </Box>
    </ModalCarpet>
  );
}

const StyledCollapse = styled(Collapse)`
  padding: 0 1rem 0 0.5rem;
  & .mb-m1:first-child {
    margin-top: -0.5rem;
  }
`;

export function ExpandableList(props) {
  const [expanded, setExpanded] = useState([]);
  return (
    <div>
      {props.headingList.map((heading, i) => (
        <div key={i} className={expanded[i] ? "mb-4" : ""}>
          <CollapseHeading
            className={"mb-2p5" + (expanded[i] ? " active" : "")}
            onClick={() =>
              setExpanded((prev) => {
                const newEpd = [...prev];
                newEpd[i] = !newEpd[i];
                return newEpd;
              })
            }
          >
            {heading}
          </CollapseHeading>
          <StyledCollapse in={Boolean(expanded[i])}>
            {props.contentList[i]}
          </StyledCollapse>
        </div>
      ))}
    </div>
  );
}

export function ShareLayout(props) {
  const { close } = props;
  const [status, setStatus] = useState(0);
  const ref = useCloseModal(close);
  const encoded = useMemo(() => encodeExportedData(props.data), []);

  return (
    <ModalCarpet>
      <Box
        ref={ref}
        className="px-4 pt-3 pb-4 flex-col white-glow-shadow"
        width="21.75rem"
        borderRadius="0.5rem"
        bgColor="cardBg1"
      >
        <Text
          className="mb-2 px-2"
          variant="h5"
          color="orange"
          fontWeight={700}
          align="center"
        >
          Share "{props.setupName}"
        </Text>
        <TextArea
          id="shared_data"
          className="p-2"
          rows="15"
          value={encoded}
          readOnly
        />
        {Boolean(status) && (
          <Text
            className="mt-2"
            align="center"
            color={status === 1 ? "Green" : "lightRed"}
          >
            {status === 1
              ? "Successfully copied to Clipboard."
              : "We're sorry. Your browser does not allow/support this function."}
          </Text>
        )}
        <ButtonBar
          className="mt-3"
          texts={["Cancel", "Copy"]}
          handlers={[
            close,
            () => {
              navigator.clipboard
                .writeText(document.getElementById("shared_data").value)
                .then(
                  () => setStatus(1),
                  () => setStatus(2)
                );
            }
          ]}
        />
      </Box>
    </ModalCarpet>
  );
}

export const BetaMark = styled((props) => <div {...props}>BETA</div>)`
  border-radius: 0.25rem;
  padding: 0 0.25rem;
  background: white;
  color: red;
  border: 2px solid red;
  font-size: 0.75rem;
  font-weight: 700;
`;
