import styled from "styled-components";
import { Box } from "../../styledCpns/Layout";
import { Text, Img } from "../../styledCpns/DataDisplay";
import { wikia, colorCodes } from "../../data/misc";
import characters from "../../data/characters";

// also used in MyCharacters/Info/SectionGears/UpperOverview
export default function Thumb(props) {
  const { noDeco, item, chosen, handleMouseDown = () => {} } = props;
  const { refinement } = item;
  return (
    <StyledDiv
      onMouseDown={handleMouseDown}
      onMouseUp={props.choose}
      className={
        (props.clicked || noDeco ? "clicked" : "") + (chosen ? " chosen" : "")
      }
    >
      {Boolean(item.user) && !noDeco && (
        <div className="side_icon">
          <img
            src={wikia + characters[item.user].sideIcon + ".png"}
            alt=""
            draggable={false}
          />
        </div>
      )}
      <div
        className={
          "thumb flex-col pointer relative " +
          (chosen ? "glowing" : "cmn-shadow")
        }
      >
        {Boolean(refinement) && (
          <p className={"rfm" + (refinement === 5 ? " max" : "")}>
            {refinement}
          </p>
        )}
        <Box bgImg={props.rarity} borderRadius="0.25rem 0.25rem 1rem 0">
          <Img
            width="100%"
            src={wikia + props.icon + ".png"}
            alt=""
            draggable={false}
          />
        </Box>
        <Box
          className="flex-center"
          bgColor="#ebebeb"
          borderRadius="0 0 0.25rem 0.25rem"
        >
          <Text fontWeight={700} color="black">
            Lv. {refinement ? item.level.split("/")[0] : item.level}
          </Text>
        </Box>
      </div>
    </StyledDiv>
  );
}

let StyledDiv = styled.div`
  &:hover:not(.clicked),
  &.chosen:not(.clicked) {
    transform: scale(1.04);
  }
  .side_icon {
    position: absolute;
    top: 0.375rem;
    right: 0.375rem;
    z-index: 1;
    width: 1.75rem;
    height: 1.75rem;
    background-color: rgba(0, 0, 0, 0.5);
    border: 2px solid white;
    border-radius: 50%;
  }
  .side_icon > img {
    width: 2.5rem;
    transform: translate(-8px, -17px);
  }
  &:hover > .side_icon,
  &.chosen > .side_icon {
    top: -0.275rem;
    right: -0.275rem;
  }
  &:hover.clicked > .side_icon,
  &.chosen.clicked > .side_icon {
    top: 0.25rem;
    right: 0.25rem;
  }
  &:hover > .thumb {
    box-shadow: 0 0 0 2.5px white;
  }
  .thumb {
    border-radius: 0.25rem;
    background-color: #ebebeb;
  }
  .rfm {
    position: absolute;
    top: 0.125rem;
    left: 0.125rem;
    border-radius: 0.25rem;
    padding: 0 0.25rem;
    background-color: rgba(0, 0, 0, 0.6);
    color: rgba(235, 235, 235, 0.8);
    font-size: 0.875rem;
    font-weight: 700;
  }
  .rfm.max {
    background-color: black;
    color: ${colorCodes.orange};
  }
`;
