import styled from "styled-components";
import { colorCodes } from "../data/misc";

export const Text = styled.p`
  font-size: ${(props) =>
    ({
      h1: "2rem",
      h2: "1.75rem",
      h3: "1.5rem",
      h4: "1.375rem",
      h5: "1.25rem",
      h6: "1.125rem",
      p: "1rem",
      subtitle1: "0.875rem"
    }[props.variant || "p"])};
  font-weight: ${(props) =>
    props.fontWeight ||
    {
      h1: 900,
      h2: 700,
      h3: 700,
      h4: 700
    }[props.variant]};
  line-height: ${(props) =>
    props.lineHeight ||
    {
      h1: 1.167,
      h2: 1.2,
      h3: 1.167,
      h4: 1.235,
      h5: 1.334,
      h6: 1.6,
      p: 1.5,
      subtitle1: 1.43
    }[props.variant || "p"]};
  color: ${(props) => colorCodes[props.color] || props.color};
  text-align: ${(props) => props.align};
`;

export const ColoredSpan = styled(({ color, fontWeight, ...others }) => (
  <span {...others} />
))`
  color: ${(props) => colorCodes[props.color] || props.color};
  font-weight: ${(props) => props.fontWeight};
  position: relative;
  bottom: ${(props) => (props.color === "Pyro" ? "-2px" : "0")};
`;

export const LGreen = (props) => <ColoredSpan color="green" {...props} />;

export const Green = (props) => (
  <ColoredSpan color="green" fontWeight={700} {...props} />
);

export const Red = (props) => <ColoredSpan color="lightRed" {...props} />;

export const Gold = (props) => <ColoredSpan color="Gold" {...props} />;

export const List = styled.ul`
  list-style-position: ${(props) => props.position};
  list-style-type: ${(props) => props.type};
  li {
    line-height: 1.5;
  }
`;

export const Img = styled.img`
  width: ${(props) => props.width};
  height: ${(props) => props.height}};
  border-radius: ${(props) => props.borderRadius};
  background-color: ${(props) =>
    colorCodes[props.bgColor] || props.bgColor || "transparent"};
  display: block;
`;

export const CollapseHeading = styled.p`
  padding: 0.25rem 1rem 0;
  background: ${colorCodes.cardBg3};
  font-weight: 700;
  font-size: 1.125rem;
  line-height: 1.6;
  cursor: pointer;
  &.mb-2p5 {
    margin-bottom: 0.75rem;
  }
  &.active {
    background: #f5dc6e;
    color: black;
  }
`;

export const Link = styled.a`
  &:link,
  &:visited {
    color: ${colorCodes.green};
    text-decoration: none;
  }
  &:hover {
    color: ${colorCodes.lightRed};
  }
`;
