import { Text, ColoredSpan } from "../../../../styledCpns/DataDisplay";
import { Box } from "../../../../styledCpns/Layout";
import Notes from "../../../Calculator/Modifiers/Target/Notes";

export default function Target(props) {
  return (
    <Box className="full-h px-2">
      {Object.entries(props.target).map((pair, i) => (
        <Text key={i} className="mb-1" variant="h6">
          <ColoredSpan
            className="mr-2"
            color={pair[0] === "Level" ? "lightGold" : pair[0].split(" ")[0]}
          >
            {pair[0]}:
          </ColoredSpan>
          <b>{pair[1]}</b>
        </Text>
      ))}
      <div className="mt-4">
        <Notes char={props.char} infusion={props.infusion} />
      </div>
    </Box>
  );
}
