import { useState } from "react";
import { Box } from "../../../styledCpns/Layout";
import { Card, MainSelect } from "../styles";
import Buffs from "./Buffs";
import Debuffs from "./Debuffs";
import Target from "./Target";

export default function Modifiers({ char }) {
  const [tab, setTab] = useState("Buffs");
  if (!char) {
    return (
      <Card className="px-4 py-3" bgColor="cardBg1">
        <Box height="38.55rem" />
      </Card>
    );
  }
  return (
    <Card className="px-4 py-3" bgColor="cardBg1">
      <MainSelect
        tab={tab}
        setTab={setTab}
        options={["Buffs", "Debuffs", "Target"]}
      />
      <Box className="mt-3 custom-sb" height="35.35rem">
        {
          {
            Buffs: <Buffs char={char} />,
            Debuffs: <Debuffs char={char} />,
            Target: <Target char={char} />
          }[tab]
        }
      </Box>
    </Card>
  );
}
