import { LGreen, Green, Red } from "../../../../styledCpns/DataDisplay";
import data from "./index";

export const buffs = [
  {
    src: "Ascension 1 Passive Talent",
    desc: () => (
      <>
        When Rosaria strikes an opponent from behind using Ravaging Confession,
        her <LGreen>CRIT Rate</LGreen> increased by <Green>12%</Green> for 5s.
      </>
    ),
    isGranted: (char) => char.ascension >= 1,
    affect: "self",
    addBnes: ({ ATTRs }) => {
      ATTRs["CRIT Rate"] += 12;
    }
  },
  {
    src: "Ascension 4 Passive Talent",
    desc: ({ inputs }) => (
      <>
        Casting Rites of Termination increases <LGreen>CRIT Rate</LGreen> of all
        nearby party members by <Green>15%</Green> of Rosaria's{" "}
        <LGreen>CRIT Rate</LGreen> for 10s.{" "}
        <Red>CRIT Rate Bonus: {buffs[1].bnValue(inputs)}%.</Red>
      </>
    ),
    isGranted: (char) => char.ascension >= 4,
    affect: "teammates",
    labels: ["CRIT Rate"],
    inputs: [5],
    inputTypes: ["text"],
    maxs: [100],
    addBnes: ({ ATTRs, inputs }) => {
      ATTRs["CRIT Rate"] += buffs[1].bnValue(inputs);
    },
    bnValue: ([CR]) => Math.round(CR * 1.5) / 10
  },
  {
    src: "Constellation 1",
    desc: () => data.constellation[0].desc,
    isGranted: (char) => char.constellation >= 1,
    affect: "self",
    addBnes: ({ ATTRs, bonuses }) => {
      bonuses.NA.DMG += 10;
      ATTRs["Normal ATK SPD"] += 10;
      ATTRs["Charged ATK SPD"] += 10;
    }
  }
];

export const debuffs = [
  {
    src: "Constellation 6",
    desc: () => data.constellation[5].desc,
    isGranted: (char) => char.constellation >= 6,
    addPntes: ({ rdMult }) => {
      rdMult["Physical RES"] -= 20;
    }
  }
];
