import { Confirmation } from "../../CommonCpns";
import OverrideOpts from "./OverrideOpts";

export default function ImportCtrl(props) {
  const { type, importedSt, close } = props;
  if (type === 1 || type === 2) {
    return (
      <Confirmation
        message={
          (type === 1
            ? "We're calculating another Character."
            : "The number of Setups on Calculator has reach the limit of 4.") +
          " Do you wish to start a new session?"
        }
        confirm={props.initNewSession}
        close={close}
      />
    );
  }
  return (
    <OverrideOpts
      type={type}
      char={props.char}
      importedSt={importedSt}
      importSt={props.importSt}
      close={close}
    />
  );
}
