import { LGreen, Green, Red } from "../../../../styledCpns/DataDisplay";

export const buffs = [
  {
    src: "Ascension 1 Passive Talent",
    desc: () => (
      <>
        <LGreen>Transient Blossoms</LGreen> deal <Green>25%</Green>{" "}
        <LGreen>more DMG</LGreen> to opponents whose HP is below 50%.
      </>
    ),
    affect: "self",
    isGranted: (char) => char.ascension >= 1
  },
  {
    src: "Ascension 4 Passive Talent",
    desc: () => (
      <>
        Using Rite of Progeniture: Tectonic Tide increases the{" "}
        <LGreen>Elemental Mastery</LGreen> of nearby party members by{" "}
        <Green>125</Green> for 10s.
      </>
    ),
    affect: "party",
    isGranted: (char) => char.ascension >= 4,
    addBnes: ({ ATTRs }) => {
      ATTRs["Elemental Mastery"] += 125;
    }
  },
  {
    src: "Constellation 2",
    desc: ({ ATTRs, charBCs }) => (
      <>
        Transient Blossoms grant Albedo Fatal Reckoning for 30s: <br />•
        Unleashing Rite of Progeniture: Tectonic Tide consumes all stacks of
        Fatal Reckoning. Each stack consumed increases the DMG dealt by{" "}
        <LGreen>Fatal Blossoms</LGreen> and{" "}
        <LGreen>Rite of Progeniture: Tectonic Tide's burst DMG</LGreen> by{" "}
        <Green>30%</Green> of Albedo's <LGreen>DEF</LGreen>. <br />• This effect
        stacks up to <Green>4</Green> times.{" "}
        <Red>DMG Bonus: {buffs[2].bnValue(ATTRs, charBCs)} (base).</Red>
      </>
    ),
    isGranted: (char) => char.constellation >= 2,
    affect: "self",
    selfLabels: ["Stacks"],
    inputs: [1],
    inputTypes: ["select"],
    maxs: [4],
    addFinalBnes: ({ ATTRs, bonuses, charBCs }) => {
      bonuses.ES.Flat += buffs[2].bnValue(ATTRs, charBCs);
    },
    bnValue: (ATTRs, charBCs) =>
      Math.round(ATTRs.DEF * 0.3 * charBCs[2].inputs[0])
  },
  {
    src: "Constellation 4",
    desc: () => (
      <>
        Active party members within the Solar Isotoma field have their{" "}
        <LGreen>Plunging Attack DMG</LGreen> increased by <Green>30%</Green>.
      </>
    ),
    affect: "party",
    isGranted: (char) => char.constellation >= 4,
    addBnes: ({ bonuses }) => {
      bonuses.PA.DMG += 30;
    }
  },
  {
    src: "Constellation 6",
    desc: () => (
      <>
        Active party members within the Solar Isotoma field who are protected by
        a shield created by Crystallize have their <LGreen>DMG</LGreen>{" "}
        increased by <Green>17%</Green>.
      </>
    ),
    affect: "party",
    isGranted: (char) => char.constellation >= 6,
    addBnes: ({ bonuses }) => {
      bonuses.AllDmg += 17;
    }
  }
];
