import { LGreen, Green } from "../../../../styledCpns/DataDisplay";
import actvTalents from "./talents";
import { getXtraTalentLv } from "../../../../calculators/helpers";

export const buffs = [
  {
    src: "Elemental Burst",
    desc: ({ char, party }) => (
      <>
        Raises Razor's <LGreen>ATK SPD</LGreen> by{" "}
        <Green>{buffs[0].bnValue(char, party)}%</Green>.
      </>
    ),
    isGranted: () => true,
    affect: "self",
    addBnes: ({ ATTRs, char, party }) => {
      ATTRs["Normal ATK SPD"] += buffs[0].bnValue(char, party);
    },
    bnValue: (char, party) => {
      const level =
        char["Elemental Burst"] + getXtraTalentLv(actvTalents[2], char, party);
      return Math.min(24 + level * 2 - Math.max(level - 6, 0), 40);
    }
  },
  {
    src: "Constellation 1",
    desc: () => (
      <>
        Picking up an Elemental Orb or Particle increases Razor's{" "}
        <LGreen>DMG</LGreen> by <Green>10%</Green> for 8s.
      </>
    ),
    isGranted: (char) => char.constellation >= 1,
    affect: "self",
    addBnes: ({ bonuses }) => {
      bonuses.AllDmg += 10;
    }
  },
  {
    src: "Constellation 2",
    desc: () => (
      <>
        Increases <LGreen>CRIT Rate</LGreen> against opponents with less than
        30% HP by <Green>10%</Green>.
      </>
    ),
    isGranted: (char) => char.constellation >= 2,
    affect: "self",
    addBnes: ({ ATTRs }) => {
      ATTRs["CRIT Rate"] += 10;
    }
  }
];

export const debuffs = [
  {
    src: "Constellation 4",
    desc: () => (
      <>
        Claw and Thunder (Press) decreases opponents' <LGreen>DEF</LGreen> by{" "}
        <Green>15%</Green> for 7s.
      </>
    ),
    isGranted: (char) => char.constellation >= 4,
    addPntes: ({ rdMult }) => {
      rdMult.DEF += 15;
    }
  }
];
