import { LGreen, Green } from "../../../../styledCpns/DataDisplay";

export const buffs = [
  {
    src: "Constellation 2",
    desc: () => (
      <>
        During Let the Show Begin's duration, your active character gains a{" "}
        <Green>15%</Green> <LGreen>Hydro DMG Bonus</LGreen>.
      </>
    ),
    isGranted: (char) => char.constellation >= 2,
    affect: "party",
    addBnes: ({ ATTRs }) => {
      ATTRs["Hydro DMG Bonus"] += 15;
    }
  }
];
