import { Setter, SetterInput, SetterCheckbox, SetterSelect } from "../CmnCpns";
import { swirlables } from "../../../../data/elements";

export default function BuffSetters(props) {
  const { buff, inputs } = props;
  return props.labels.map((label, i) => {
    const type = buff.inputTypes[i];
    let inputCpn;
    if (type === "text") {
      inputCpn = (
        <SetterInput
          type="text"
          value={inputs[i]}
          onChange={(e) => props.handleText(e.target.value, i)}
        />
      );
    } else if (type === "check") {
      inputCpn = (
        <SetterCheckbox
          type="checkbox"
          checked={inputs[i]}
          onChange={() => props.handleCheck(i)}
        />
      );
    } else {
      const isSelect = type === "select"; // or swirl
      const options = isSelect ? [...Array(buff.maxs[i]).keys()] : swirlables;
      inputCpn = (
        <SetterSelect
          value={inputs[i]}
          onChange={(e) => props.handleSelect(e.target.value, i)}
        >
          {options.map((opt) => (
            <option key={opt}>{isSelect ? opt + 1 : opt}</option>
          ))}
        </SetterSelect>
      );
    }
    return (
      <Setter key={i}>
        <p className="label">{label}</p>
        {inputCpn}
      </Setter>
    );
  });
}
