import ElmtBuffs from "./ElmtBuffs";
import SelfBuffs from "./SelfBuffs";
import PartyBuffs from "./PartyBuffs";
import WpBuffs from "./WpBuffs";
import ArtBuffs from "./ArtBuffs";
import { ExpandableList } from "../../../CommonCpns";

export default function Buffs({ char }) {
  return (
    <ExpandableList
      headingList={[
        "Resonance & Reactions",
        "Self",
        "Party",
        "Weapons",
        "Artifacts"
      ]}
      contentList={[
        <ElmtBuffs char={char} />,
        <SelfBuffs char={char} />,
        <PartyBuffs char={char} />,
        <WpBuffs />,
        <ArtBuffs />
      ]}
    />
  );
}
