import characters from "../data/characters";
import weapons from "../data/weapons";
import artifacts from "../data/artifacts";
import artifactBuffs from "../data/artifacts/artifactBuffs";
import artifactDebuffs from "../data/artifacts/artifactDebuffs";
import { allStatsTypes } from "../data/artifacts/config";
import {
  swirlables,
  crystallizables,
  ampRxnTrigElmts,
  rsnNames,
  elmts
} from "../data/elements";
import { levels, artTypes } from "../data/misc";
import {
  initSelfMCs,
  getMainWpBCs,
  initParty,
  getMainArtBCs,
  getSubArtBCs,
  getSubArtDCs
} from "../components/Calculator/CalcChar/helpers";
import {
  initTeammate,
  getWpRange,
  getSubWpBCs,
  visionCount
} from "../components/Calculator/SetupManager/helpers";
import { getArtSets } from "./processors";
import { specialCodes } from "./encode";

export default function decodeImportedData(code) {
  try {
    let [
      char,
      selfMCs,
      weapon,
      subWpMCs,
      art,
      party,
      elmtMCs,
      target
    ] = code.split("?");

    char = getChar(char);
    const charData = characters[char.name];
    selfMCs = getSelfMCs(selfMCs, char, charData);
    // console.log(selfMCs);
    weapon = getWeapon(weapon);
    party = getParty(party);
    subWpMCs = getSubWpMCs(subWpMCs, party, weapon.name); // just BCs
    art = getArt(art);
    elmtMCs = getElmtMCs(elmtMCs, visionCount(char, party));
    target = getTarget(target);

    const result = {
      char,
      selfMCs,
      weapon,
      subWpMCs,
      art,
      party,
      elmtMCs,
      target
    };
    return result;
  } catch (err) {
    console.log(err.message);
    if (err.message === "1") return "1";
    return "2";
  }
}

function getChar(code) {
  let [name, level, constellation, ...talents] = code.split("&");
  name = searchChar(name);
  if (!isNaN(name)) throw new Error("1");
  level = levels[level];
  if (!level) throw new Error("1");
  constellation = revertNum(constellation, 0, 6);
  talents = talents.map((tl) => revertNum(tl, 1, 10));
  const maxLv = +level.slice(-2);
  return {
    name,
    level,
    ascension: maxLv === 20 ? 0 : (maxLv - 30) / 10,
    constellation,
    "Normal Attack": talents[0],
    "Elemental Skill": talents[1],
    "Elemental Burst": talents[2]
  };
}

function getSelfMCs(code, char, charData) {
  const selfMCs = initSelfMCs(char.name);
  const [BCs, DCs] = code.split("&");
  const { buffs, debuffs } = charData;
  if (BCs.length && buffs) adjustMCs(BCs.split("!"), selfMCs.BCs, buffs);
  if (DCs.length && debuffs) adjustMCs(DCs.split("!"), selfMCs.DCs, debuffs);
  return selfMCs;
}

function getWeapon(code) {
  let [nameC, level, rfm, BCs] = code.split("&");
  let { type, name } = searchWp(nameC);
  level = levels[level];
  if (!level) throw new Error("1");
  const wpBCs = getMainWpBCs(type, name);
  if (BCs.length) {
    if (!wpBCs.length) throw new Error("1");
    adjustMCs(BCs.split("!"), wpBCs, weapons[type][name].buffs);
  }
  return { type, name, level, refinement: revertNum(rfm, 1, 5), BCs: wpBCs };
}

function getParty(code) {
  const party = initParty();
  code.split("&").forEach((tmC, tmI) => {
    if (tmI > 2) throw new Error("1");
    if (tmC.length) {
      let [name, BCs, DCs] = tmC.split("#");
      const tm = initTeammate(searchChar(name));
      const { buffs, debuffs } = characters[tm.name];
      if (BCs.length && buffs) adjustMCs(BCs.split("!"), tm.BCs, buffs);
      if (DCs.length && debuffs) adjustMCs(DCs.split("!"), tm.DCs, debuffs);
      party[tmI] = tm;
    }
  });
  return party;
}

function getSubWpMCs(code, party, mainWpN) {
  let BCs = getWpRange(party);
  for (let type in BCs) {
    BCs[type] = getSubWpBCs(type, mainWpN);
  }
  if (code.length) {
    for (let ctrlC of code.split("!")) {
      let [nameC, rfm, inputs] = ctrlC.split(":");
      const { type, name } = searchWp(nameC);
      const ctrlI = BCs[type].findIndex((ctrl) => ctrl.name === name);
      if (ctrlI === -1) throw new Error("1");
      BCs[type][ctrlI].activated = true;
      BCs[type][ctrlI].refinement = revertNum(rfm, 1, 5);
      if (inputs) {
        const { inputTypes } = weapons[type][name].buffs[0]; // index always 0 (v2.3)
        adjustInputs(inputs.split("/"), BCs[type][ctrlI], inputTypes); // no maxs yet
      }
    }
  }
  return { BCs, DCs: {} };
}

function getArt(code) {
  let [pieces, BCsC, subBCs, subDCs] = code.split("&");

  pieces = pieces.split("!");
  if (pieces.length !== 5) throw new Error("1");
  pieces = pieces.map((pCode, i) => {
    if (pCode === "X") return null;
    let [set, level, mainSType, subS] = pCode.split("#");
    mainSType = getSType(mainSType);
    subS = subS.split("/");
    if (subS.length !== 4) throw new Error("1");
    subS = subS.map((statC) => {
      let [type, val] = statC.split(":");
      if (!type.length || !val.length) throw new Error("1");
      return { type: getSType(type), val: revertNum(val, 0, 9999) };
    });
    return {
      set: searchArtSet(set),
      type: artTypes[i],
      level: revertNum(level, 0, 20),
      mainSType,
      subS
    };
  });
  const sets = getArtSets(pieces);
  const setN = sets[0] && sets[0].bonusLv === 1 && sets[0].name;

  const BCs = getMainArtBCs(setN);
  if (BCsC.length && BCs.length)
    adjustMCs(BCsC.split("!"), BCs, artifactBuffs[setN]);
  const subMCs = { subBCs: getSubArtBCs(setN), subDCs: getSubArtDCs() };
  const subMCsCode = { subBCs, subDCs };
  for (let type of ["subBCs", "subDCs"]) {
    if (subMCsCode[type].length) {
      for (let ctrlC of subMCsCode[type].split("!")) {
        let [set, inputs] = ctrlC.split(":");
        set = searchArtSet(set);
        const ctrlI = subMCs[type].findIndex((ctrl) => ctrl.set === set);
        if (ctrlI === -1) throw new Error("1");
        subMCs[type][ctrlI].activated = true;
        if (inputs) {
          const mod =
            type === "subBCs" ? artifactBuffs[set][0] : artifactDebuffs[set][0]; // index always 0 (v2.3)
          adjustInputs(
            inputs.split("/"),
            subMCs[type][ctrlI],
            mod.inputTypes,
            mod.maxs
          );
        }
      }
    }
  }
  return { pieces, sets, BCs, ...subMCs };
}

function getElmtMCs(code, visions) {
  let [naAmpRxn, ampRxn, scCode, rsnC] = code.split("&");
  if (naAmpRxn.length) {
    naAmpRxn = Object.keys(ampRxnTrigElmts)[+naAmpRxn];
    if (!naAmpRxn) throw new Error("1");
  } else naAmpRxn = null;

  if (ampRxn.length) {
    ampRxn = Object.keys(ampRxnTrigElmts)[+ampRxn];
    if (!ampRxn) throw new Error("1");
  } else ampRxn = null;

  const resonance = [];
  for (let type in visions) {
    if (visions[type] >= 2)
      resonance.push({ name: rsnNames[type], activated: false });
  }
  rsnC.split("#").forEach((rsnI, i) => {
    if (i > 1) throw new Error("1");
    if (rsnI.length) {
      const name = Object.values(rsnNames)[+rsnI];
      if (!name) throw new Error("1");
      const index = resonance.findIndex((rsn) => rsn.name === name);
      if (index === -1) throw new Error("1");
      resonance[index].activated = true;
    }
  });
  return { naAmpRxn, ampRxn, superconduct: revertInp(scCode), resonance };
}

function getTarget(code) {
  let [lvC, ...res] = code.split("&");
  const target = { Level: revertNum(lvC, 0, 100) };
  ["Physical", ...elmts].forEach((type, i) => {
    target[type + " RES"] = revertNum(res[i], 0, 999);
  });
  return target;
}

function searchChar(charC) {
  for (let name in characters) {
    if (characters[name].code === +charC) {
      return name;
    }
  }
  throw new Error("1");
}

function searchWp(wpCode) {
  for (let type in weapons) {
    for (let name in weapons[type]) {
      if (weapons[type][name].code === +wpCode) {
        return { type, name };
      }
    }
  }
  throw new Error("1");
}

function searchArtSet(pCode) {
  for (let set in artifacts) {
    if (artifacts[set].code === +pCode) {
      return set;
    }
  }
  throw new Error("1");
}

function getSType(code) {
  if (!code.length) throw new Error("1");
  const type = allStatsTypes[+code];
  if (!type) throw new Error("1");
  return type;
}

function adjustMCs(newMCs, oldMCs, mods) {
  for (let ctrl of newMCs) {
    let [index, inputs] = ctrl.split(":");
    if (!index.length) throw new Error("1");
    const i = oldMCs.findIndex((c) => c.index === +index);
    if (i === -1) throw new Error("1");
    oldMCs[i].activated = true;
    const oldInputs = oldMCs[i].inputs;
    if (oldInputs && !inputs) throw new Error("1");
    if (inputs) {
      if (!oldInputs) throw new Error("1");
      const mod = mods[+index];
      adjustInputs(inputs.split("/"), oldMCs[i], mod.inputTypes, mod.maxs);
    }
  }
}

function adjustInputs(newInputs, ctrl, inputTypes, maxs) {
  newInputs.forEach((inp, j) => {
    const input = revertInp(inp);
    if (typeof input === typeof ctrl.inputs[j]) {
      const type = inputTypes[j];
      if (
        (["text", "select", "stacks"].includes(type) &&
          maxs &&
          input > maxs[j]) ||
        (["swirl", "absorption"].includes(type) &&
          swirlables.indexOf(input) === -1) ||
        (type === "crystallize" && crystallizables.indexOf(input) === -1)
      ) {
        throw new Error("1");
      }
      ctrl.inputs[j] = input;
    } else throw new Error("1");
  });
}

function revertInp(inp) {
  if (!inp.length) throw new Error("1");
  if (inp === "T") {
    return true;
  } else if (inp === "F") {
    return false;
  } else if (!isNaN(inp)) {
    if (+inp < 0) throw new Error("1");
    return +inp;
  }
  const str = revertSpecialCode(inp);
  if (str) return str;
  throw new Error("1");
  // return inp;
}

function revertSpecialCode(code) {
  for (let str in specialCodes) {
    if (specialCodes[str] === code) {
      return str;
    }
  }
  return null;
}

function revertNum(code, min, max) {
  if (!code.length) throw new Error("1");
  const result = +code;
  if (isNaN(result) || result < min || (max && result > max))
    throw new Error("1");
  return result;
}
