import { ColoredSpan, LGreen, Green } from "../../../../styledCpns/DataDisplay";
import data from "./index";

export const buffs = [
  {
    src: "Alternate Sprint",
    desc: () => (
      <>
        When Ayaka reappears from Kamisato Art: Senho, her attacks are{" "}
        <LGreen>infused</LGreen> with{" "}
        <ColoredSpan color="Cryo">Cryo</ColoredSpan> for a brief period.
      </>
    ),
    isGranted: () => true,
    affect: "self",
    canInfuse: () => true,
    infuseRange: ["NA", "CA", "PA"],
    canBeOverrided: true
  },
  {
    src: "Ascension 1 Passive Talent",
    desc: () => (
      <>
        After using Kamisato Art: Hyouka, Ayaka's{" "}
        <LGreen>Normal and Charged attacks</LGreen> deal <Green>30%</Green>{" "}
        increased <LGreen>DMG</LGreen> for 6s.
      </>
    ),
    isGranted: (char) => char.ascension >= 1,
    affect: "self",
    addBnes: ({ bonuses }) => {
      bonuses.NA.DMG += 30;
      bonuses.CA.DMG += 30;
    }
  },
  {
    src: "Ascension 4 Passive Talent",
    desc: () => (
      <>
        When the Cryo application at the end of Kamisato Art: Senho hits an
        opponent, Ayaka gains <Green>18%</Green> <LGreen>Cryo DMG Bonus</LGreen>{" "}
        for 10s.
      </>
    ),
    isGranted: (char) => char.ascension >= 4,
    affect: "self",
    addBnes: ({ ATTRs }) => {
      ATTRs["Cryo DMG Bonus"] += 18;
    }
  },
  {
    src: "Constellation 6",
    desc: () => data.constellation[5].desc,
    isGranted: (char) => char.constellation >= 6,
    affect: "self",
    addBnes: ({ bonuses }) => {
      bonuses.CA.DMG += 298;
    }
  }
];

export const debuffs = [
  {
    src: "Constellation 4",
    desc: () => data.constellation[3].desc,
    isGranted: (char) => char.constellation >= 4,
    addPntes: ({ rdMult }) => {
      rdMult.DEF += 30;
    }
  }
];
