import { Text } from "../../../../styledCpns/DataDisplay";
import { NoContent } from "../../../Calculator/Modifiers/CmnCpns";
import { ExpandableList, AmpRxnDesc } from "../../../CommonCpns";
import ModReader from "./ModReader";
import characters from "../../../../data/characters";
import weapons from "../../../../data/weapons";
import artifactBuffs from "../../../../data/artifacts/artifactBuffs";
import { rsnBuffDesc } from "../../../../data/elements";

export default function Buffs(props) {
  const headingList = [
    "Resonance & Reactions",
    "Self",
    "Party",
    "Weapons",
    "Artifacts"
  ];
  return (
    <ExpandableList
      headingList={headingList}
      contentList={headingList.map((cate, i) => (
        <Inner key={i} cate={cate} {...props} />
      ))}
    />
  );
}

function Inner(props) {
  const { cate, char, party, ATTRs } = props;
  const content = [];
  if (cate === "Resonance & Reactions") {
    const { resonance, ampRxn, naAmpRxn } = props.elmtMCs;
    resonance.forEach((rsn, i) => {
      if (rsn.activated) {
        content.push(
          <ModReader
            key={"rsn" + i}
            heading={rsn.name}
            desc={rsnBuffDesc[rsn.name]}
          />
        );
      }
    });
    if (ampRxn) {
      content.push(
        <ModReader
          key="ampRxn"
          heading={ampRxn}
          desc={
            <AmpRxnDesc
              elmt={characters[char.name].vision}
              mult={props.rxnBnes[ampRxn]}
            />
          }
        />
      );
    }
    if (naAmpRxn) {
      content.push(
        <ModReader
          key="naAmpRxn"
          heading={naAmpRxn + " (external infusion)"}
          desc={
            <AmpRxnDesc
              elmt={props.infusion.NA}
              mult={props.rxnBnes["na" + naAmpRxn]}
            />
          }
        />
      );
    }
  } else if (cate === "Self") {
    const { selfBCs } = props;
    selfBCs.forEach((ctrl, i) => {
      const buff = characters[char.name].buffs[ctrl.index];
      if (ctrl.activated && buff.isGranted(char)) {
        const { inputs } = ctrl;
        content.push(
          <ModReader
            key={"self" + i}
            heading={buff.src}
            desc={buff.desc({
              toSelf: true,
              ATTRs,
              char,
              charBCs: selfBCs,
              inputs,
              party
            })}
            labels={buff.selfLabels}
            inputTypes={buff.inputTypes}
            inputs={inputs}
          />
        );
      }
    });
  } else if (cate === "Party") {
    for (let { name, BCs } of party) {
      const tmData = characters[name];
      if (name && BCs.some((ctrl) => ctrl.activated)) {
        content.push(
          <Text
            key={name}
            className="pt-2 mb-m1"
            variant="h6"
            fontWeight={700}
            color={tmData.vision}
            align="center"
          >
            {name.toUpperCase()}
          </Text>
        );
      }
      BCs.forEach((ctrl, i) => {
        if (ctrl.activated) {
          const buff = tmData.buffs[ctrl.index];
          const { inputs } = ctrl;
          content.push(
            <ModReader
              key={name + i}
              heading={buff.src}
              desc={buff.desc({
                toSelf: false,
                char,
                charBCs: BCs,
                inputs,
                party
              })}
              labels={buff.labels}
              inputTypes={buff.inputTypes}
              inputs={inputs}
            />
          );
        }
      });
    }
  } else if (cate === "Weapons") {
    const { type, name, refinement: wpRfm, BCs } = props.weapon;
    BCs.forEach((ctrl, i) => {
      if (ctrl.activated) {
        const buff = weapons[type][name].buffs[ctrl.index];
        content.push(
          <ModReader
            key={name + i}
            heading={name}
            desc={buff.desc({ wpRfm, ATTRs })}
            labels={buff.labels}
            inputTypes={buff.inputTypes}
            inputs={ctrl.inputs}
          />
        );
      }
    });
    const { subWpBCs } = props;
    for (let type in subWpBCs) {
      subWpBCs[type].forEach((ctrl, i) => {
        if (ctrl.activated) {
          const { name, refinement: wpRfm } = ctrl;
          const wpData = weapons[type][name];
          const buff = wpData.buffs[ctrl.index];
          content.push(
            <ModReader
              key={name + i}
              heading={name}
              desc={wpData.pasvDescs({ wpRfm })[0].buff}
              labels={["Refinement", ...(buff.labels || [])]}
              inputTypes={["text", ...(buff.inputTypes || [])]}
              inputs={[wpRfm, ...(ctrl.inputs || [])]}
            />
          );
        }
      });
    }
  } else if (cate === "Artifacts") {
    const { sets, BCs, subBCs } = props.arts;
    const name = sets[0]?.name;
    BCs.forEach((ctrl, i) => {
      if (ctrl.activated) {
        const buff = artifactBuffs[name][ctrl.index];
        content.push(
          <ModReader
            key={name + i}
            heading={name + " (self)"}
            desc={buff.desc}
            labels={buff.labels}
            inputTypes={buff.inputTypes}
            inputs={ctrl.inputs}
          />
        );
      }
    });
    subBCs.forEach((ctrl, i) => {
      if (ctrl.activated) {
        const { set } = ctrl;
        const buff = artifactBuffs[set][ctrl.index];
        content.push(
          <ModReader
            key={set + i}
            heading={set}
            desc={buff.desc}
            labels={buff.labels}
            inputTypes={buff.inputTypes}
            inputs={ctrl.inputs}
          />
        );
      }
    });
  }
  return content.length ? content : <NoContent type="buffs" />;
}
