import { TalentLvMults } from "../data/characters/config";

export function getRxnBnesFromEM(EM = 0) {
  return {
    transformative: Math.round((16000 * EM) / (EM + 2000)) / 10,
    amplifying: Math.round((2780 * EM) / (EM + 1400)) / 10,
    shield: Math.round((4440 * EM) / (EM + 1400)) / 10
  };
}

export function getDefMult(charLv, targetLv, defRdMult) {
  return (
    (charLv + 100) / ((1 - defRdMult / 100) * (targetLv + 100) + charLv + 100)
  );
}

export function getResMult(RES) {
  return RES < 0 ? 1 - RES / 2 : RES >= 0.75 ? 1 / (4 * RES + 1) : 1 - RES;
}

export function getXtraTalentLv(talent, char, party) {
  const { type, getXtraLv } = talent;
  let result = 0;
  if (
    type === "Normal Attack" &&
    (char.name === "Tartaglia" || party.find((tm) => tm.name === "Tartaglia"))
  ) {
    result++;
  } else if (getXtraLv) {
    result += getXtraLv(char);
  }
  return result;
}

export function calcBase(ATTRs, dmgPiece, level, xtraMult) {
  const { baseSType, baseMult, multType, baseFlat, flatType } = dmgPiece;
  let result =
    ATTRs[baseSType || "ATK"] *
    ((baseMult * TalentLvMults[multType][level] + xtraMult) / 100);
  if (baseFlat) {
    result += baseFlat * TalentLvMults[flatType][level];
  }
  return result;
}

export function isInfused(atkType, elmtType, infusion) {
  return (
    ["NA", "CA", "PA"].includes(atkType) &&
    elmtType === "Physical" &&
    infusion !== "Physical"
  );
}
