import styled from "styled-components";
import { useState, useEffect } from "react";

const StyledScroll = styled.div`
  height: 0;
  transition: ${(props) => props.transition};
  &.open {
    height: ${(props) => props.openHeight};
  }
`;

export default function HiddenScroll(props) {
  const { on, className, duration } = props;
  const [open, setOpen] = useState(false);
  const [showing, setShowing] = useState(false);

  useEffect(() => {
    if (on) {
      setOpen(true);
      setShowing(true);
    } else {
      setOpen(false);
      setTimeout(() => setShowing(false), duration);
    }
  }, [on]);

  return (
    <StyledScroll
      className={(className ? className + " " : "") + (open ? "open" : "close")}
      openHeight={props.openHeight}
      transition={`${duration}ms ${props.timingFunc}`}
      style={props.style}
    >
      {showing && props.children}
    </StyledScroll>
  );
}
