import { useContext } from "react";
import { SelfMCsCt, PartiesCt, ResultCt, FixedCts, SetupCt } from "../../index";
import { Section, NoContent } from "../CmnCpns";
import BuffSetters from "./BuffSetters";
import characters from "../../../../data/characters";
import { deepCopy } from "../../../../helpers/misc";
import { processNumInp } from "../../../../helpers/processors";

export default function SelfBuffs({ char }) {
  const selfMCs = useContext(SelfMCsCt);
  const parties = useContext(PartiesCt);
  const { allATTRs } = useContext(ResultCt);
  const { setSelfMCs } = useContext(FixedCts);
  const curSetupI = useContext(SetupCt);
  const content = [];

  selfMCs[curSetupI].BCs.forEach((ctrl, bcIndex) => {
    const { activated, index, inputs } = ctrl;
    const buff = characters[char.name].buffs[index];
    if (buff.isGranted(char)) {
      content.push(
        <Section
          key={bcIndex}
          checked={activated}
          handleCheck={() => {
            setSelfMCs((prev) => {
              const MCs = deepCopy(prev);
              MCs[curSetupI].BCs[bcIndex].activated = !activated;
              return MCs;
            });
          }}
          heading={buff.src}
          desc={buff.desc({
            toSelf: true,
            ATTRs: allATTRs[curSetupI],
            char,
            charBCs: selfMCs[curSetupI].BCs,
            inputs,
            party: parties[curSetupI]
          })}
          setters={
            <SetterSection
              buff={buff}
              inputs={inputs}
              setSelfMCs={setSelfMCs}
              curSetupI={curSetupI}
              bcIndex={bcIndex}
            />
          }
        />
      );
    }
  });
  return content.length ? content : <NoContent type="buffs" />;
}

function SetterSection(props) {
  if (!props.buff.selfLabels) {
    return null;
  }
  const { buff, inputs, setSelfMCs, curSetupI, bcIndex } = props;
  return (
    <BuffSetters
      labels={buff.selfLabels}
      buff={buff}
      inputs={inputs}
      handleText={(value, i) =>
        setSelfMCs((prev) => {
          const MCs = deepCopy(prev);
          MCs[curSetupI].BCs[bcIndex].inputs[i] = processNumInp(
            value,
            inputs[i],
            buff.maxs[i]
          );
          return MCs;
        })
      }
      handleCheck={(i) =>
        setSelfMCs((prev) => {
          const MCs = deepCopy(prev);
          MCs[curSetupI].BCs[bcIndex].inputs[i] = !inputs[i];
          return MCs;
        })
      }
      handleSelect={(value, i) =>
        setSelfMCs((prev) => {
          const MCs = deepCopy(prev);
          MCs[curSetupI].BCs[bcIndex].inputs[i] = isNaN(value) ? value : +value;
          return MCs;
        })
      }
    />
  );
}
