import { useContext, useState, useEffect, useCallback } from "react";
import { PartiesCt } from "../../index";
import { ManagerCts, FixedCts } from "../../index";
import { Button } from "../../../../styledCpns/Inputs";
import { Text } from "../../../../styledCpns/DataDisplay";
import { Box, AbsoluteBox } from "../../../../styledCpns/Layout";
import { CloseBtn, ButtonBar } from "../../../CommonCpns";
import HiddenScroll from "../../../CommonCpns/HiddenScroll";
import SetupCtrl from "./SetupCtrl";
import { SaveCtrl, SharedCtrl } from "./ExtraCtrls";
import characters from "../../../../data/characters";
import { colorCodes } from "../../../../data/misc";
import { getSetupCtrl } from "../../helpers";
import { applySettings, getNewSetupName } from "./helpers";

export default function HiddenSettings({ settingsOn, ...others }) {
  return (
    <AbsoluteBox className="full-w" bottom={0} left={0}>
      <HiddenScroll
        on={settingsOn}
        style={{
          borderRadius: "0.625rem",
          backgroundColor: colorCodes.cardBg3
        }}
        openHeight="40.55rem"
        duration={250}
        timingFunc="ease-in-out"
      >
        <Settings {...others} />
      </HiddenScroll>
    </AbsoluteBox>
  );
}

function Settings(props) {
  const { char, wpType, close } = props;
  const { setups, stdSetupI, cmpSetupIs } = useContext(ManagerCts);
  const {
    setSelfMCs,
    setWpRack,
    setSubWpMCs,
    setArtChest,
    setParties,
    setElmtMCs,
    setSetups,
    setStdSetupI,
    setCurSetupI,
    setCmpSetupIs
  } = useContext(FixedCts);
  const [tempSetups, setTempSetups] = useState([]);
  const [err, setErr] = useState(false);
  const [savedI, setSavedI] = useState(null);
  const [sharedI, setSharedI] = useState(null);

  useEffect(() => {
    setTempSetups(
      setups.map((st, index) => ({
        ...st,
        index,
        isStandard: index === stdSetupI,
        checked: cmpSetupIs.includes(index),
        expanded: false
      }))
    );
  }, [setups]);

  const charName = char.name;
  const tryApply = useCallback(() => {
    let ok = true;
    let standardI;
    const names = [];
    for (let i in tempSetups) {
      const { name, isStandard } = tempSetups[i];
      if (!name.length || names.includes(name)) {
        ok = false;
        break;
      } else {
        names.push(name);
      }
      if (isStandard) standardI = +i;
    }
    if (ok) {
      applySettings(tempSetups, charName, wpType, {
        setSetups,
        setCmpSetupIs,
        setSelfMCs,
        setWpRack,
        setSubWpMCs,
        setArtChest,
        setParties,
        setElmtMCs
      });
      setStdSetupI(standardI);
      setCurSetupI(standardI);
      close();
    } else {
      setErr(true);
    }
  }, [tempSetups, charName, wpType]);

  const parties = useContext(PartiesCt);
  return (
    <div className="p-3">
      <AbsoluteBox top="0.75rem" right="0.75rem">
        <CloseBtn className="glow-on-hover" onClick={close} />
      </AbsoluteBox>
      <Text className="mt-2" variant="h3" align="center" color="orange">
        SETTINGS
      </Text>
      <Box height="33.45rem" className="flex-col hide-sb">
        {tempSetups.map((setup, index) => (
          <SetupCtrl
            key={index}
            setup={setup}
            index={index}
            belowLimit={tempSetups.length < 4}
            setTempSetups={setTempSetups}
            savable={
              !characters[char.name]?.beta &&
              parties[index].every((tm) => !characters[tm.name]?.beta)
            }
            save={() => setSavedI(index)}
            share={() => setSharedI(index)}
          />
        ))}
        {tempSetups.length < 4 && (
          <div className="mt-3">
            <Box className="mb-4" height="2rem">
              <Button
                className="full-h full-w glow-on-hover"
                bgColor="#2269d6"
                color="white"
                borderRadius="1rem"
                onClick={() => {
                  setTempSetups((prev) => {
                    const newTSs = [...prev];
                    newTSs.push({
                      ...getSetupCtrl({ name: getNewSetupName(prev) }),
                      index: null,
                      isStandard: false,
                      checked: false,
                      expanded: false
                    });
                    return newTSs;
                  });
                }}
              >
                <i className="fas fa-plus" />
              </Button>
            </Box>
            {props.importBtn}
          </div>
        )}
        {err && (
          <Text className="mt-3" color="lightRed" align="center">
            <i>Please choose a unique name for each Setup.</i>
          </Text>
        )}
      </Box>
      <ButtonBar
        className="mt-3"
        variants={["positive"]}
        texts={["Apply"]}
        handlers={[tryApply]}
      />
      {savedI !== null && (
        <SaveCtrl
          char={char}
          tempSetups={tempSetups}
          savedI={savedI}
          closeSettings={close}
          close={() => setSavedI(null)}
        />
      )}
      {sharedI !== null && (
        <SharedCtrl
          char={char}
          tempSetups={tempSetups}
          sharedI={sharedI}
          close={() => setSharedI(null)}
        />
      )}
    </div>
  );
}
