import { flatSTypes, pctSTypes } from "../data/misc";
import { artMS } from "../data/artifacts/config";
import weapons from "../data/weapons";
import artifacts from "../data/artifacts";

export function initiateATTRs(charData, char, wpData, weapon) {
  const ATTRs = {};
  for (let type of flatSTypes.concat(pctSTypes)) {
    ATTRs[type] = 0;
  }
  ATTRs["CRIT Rate"] = 5;
  ATTRs["CRIT DMG"] = 50;
  ATTRs["Energy Recharge"] = 100;
  ATTRs["Normal ATK SPD"] = ATTRs["Charged ATK SPD"] = 100;
  const stats = charData.stats[char.level];
  for (let key in stats) {
    ATTRs[key] = (ATTRs[key] || 0) + stats[key];
  }
  ATTRs["Base ATK"] += wpData.mainStat.value[weapon.level];
  return ATTRs;
}

export function addArtStats(pieces, ATTRs) {
  const artSBnes = { HP: 0, ATK: 0, DEF: 0 };
  for (let artP of pieces) {
    if (artP) {
      const { type, level, mainSType, subS } = artP;
      artSBnes[mainSType] =
        (artSBnes[mainSType] || 0) + artMS[type][mainSType][level];
      for (let ss of subS) {
        artSBnes[ss.type] = (artSBnes[ss.type] || 0) + Number(ss.val);
      }
    }
  }
  for (let type of ["HP", "ATK", "DEF"]) {
    const pctStats = artSBnes[type + "%"];
    if (pctStats) {
      artSBnes[type] += Math.round((ATTRs["Base " + type] * pctStats) / 100);
    }
    delete artSBnes[type + "%"];
  }
  for (let type in artSBnes) {
    ATTRs[type] += artSBnes[type];
  }
  return artSBnes;
}

export function addWpSubStats(wpData, ATTRs, wpLevel) {
  if (wpData.subStats) {
    for (let stat of wpData.subStats) {
      ATTRs[stat.type] += stat.value[wpLevel];
    }
  }
}

export function addArtBonuses(
  isFinal,
  sets,
  ATTRs,
  charData,
  bonuses,
  rxnBnes
) {
  for (let set of sets) {
    for (let i = 0; i <= set.bonusLv; i++) {
      const { addBnes, addFinalBnes } = artifacts[set.name].setBnes[i];
      if (!isFinal && addBnes) {
        addBnes({ ATTRs, bonuses, rxnBnes, charData });
      } else if (isFinal && addFinalBnes) {
        addFinalBnes({ ATTRs, bonuses });
      }
    }
  }
}

export function addWpBonuses(
  isFinal,
  wpData,
  ATTRs,
  bonuses,
  wpRfm,
  charData,
  party
) {
  const { addBnes, addFinalBnes } = wpData;
  if (!isFinal && addBnes) {
    addBnes({ ATTRs, bonuses, wpRfm, charData, party });
  } else if (isFinal && addFinalBnes) {
    // v2.3 - Homa, Jade Cutter, EL, Moonglow
    addFinalBnes({ ATTRs, bonuses, wpRfm });
  }
}

export function calcFinalATTRs(ATTRs) {
  ["HP", "ATK", "DEF"].forEach((type) => {
    ATTRs[type] += Math.round(
      ATTRs["Base " + type] * (1 + ATTRs[type + "%"] / 100)
    );
    delete ATTRs[type + "%"];
  });
}

export default function getBaseStats(charData, char, weapon, art) {
  if (!weapon) return { ATTRs: {}, artSBnes: {} };

  const wpData = weapons[weapon.type][weapon.name];
  const ATTRs = initiateATTRs(charData, char, wpData, weapon);
  const artSBnes = addArtStats(art.pieces, ATTRs);
  addWpSubStats(wpData, ATTRs, weapon.level);
  addArtBonuses(false, art.sets, ATTRs, charData);
  addWpBonuses(false, wpData, ATTRs, null, weapon.refinement, charData);
  calcFinalATTRs(ATTRs);
  return { ATTRs, artSBnes };
}
