import { useContext } from "react";
import { PartiesCt, FixedCts, SetupCt } from "../../index";
import { Text } from "../../../../styledCpns/DataDisplay";
import { Section, NoContent } from "../CmnCpns";
import BuffSetters from "./BuffSetters";
import characters from "../../../../data/characters";
import { deepCopy } from "../../../../helpers/misc";
import { processNumInp } from "../../../../helpers/processors";

export default function PartyBuffs({ char }) {
  const parties = useContext(PartiesCt);
  const curSetupI = useContext(SetupCt);
  const content = [];

  parties[curSetupI].forEach((teammate, tmI) => {
    if (teammate.BCs.length) {
      content.push(
        <TeammateBuffs
          key={tmI}
          teammate={teammate}
          curSetupI={curSetupI}
          tmI={tmI}
          char={char}
          party={parties[curSetupI]}
        />
      );
    }
  });
  return content.length ? content : <NoContent type="buffs" />;
}

function TeammateBuffs(props) {
  const { setParties } = useContext(FixedCts);
  const { teammate, curSetupI, tmI, char } = props;
  const { buffs, vision } = characters[teammate.name];
  const subContent = [];

  teammate.BCs.forEach((ctrl, bcIndex) => {
    const { activated, index, inputs } = ctrl;
    const buff = buffs[index];
    subContent.push(
      <Section
        key={bcIndex}
        checked={activated}
        handleCheck={() => {
          setParties((prev) => {
            const pts = deepCopy(prev);
            pts[curSetupI][tmI].BCs[bcIndex].activated = !activated;
            return pts;
          });
        }}
        heading={buff.src}
        desc={buff.desc({
          toSelf: false,
          char,
          charBCs: teammate.BCs,
          inputs,
          party: props.party
        })}
        setters={
          <SetterSection
            buff={buff}
            inputs={inputs}
            setParties={setParties}
            curSetupI={curSetupI}
            tmI={tmI}
            bcIndex={bcIndex}
          />
        }
      />
    );
  });
  return (
    <>
      <Text
        className="pt-2 mb-m1"
        variant="h6"
        fontWeight={700}
        color={vision}
        align="center"
      >
        {teammate.name.toUpperCase()}
      </Text>
      {subContent}
    </>
  );
}

function SetterSection(props) {
  if (!props.buff.labels) {
    return null;
  }
  const { buff, inputs, setParties, curSetupI, tmI, bcIndex } = props;
  return (
    <BuffSetters
      labels={buff.labels}
      buff={buff}
      inputs={inputs}
      handleText={(value, i) =>
        setParties((prev) => {
          const pts = deepCopy(prev);
          pts[curSetupI][tmI].BCs[bcIndex].inputs[i] = processNumInp(
            value,
            inputs[i],
            buff.maxs[i]
          );
          return pts;
        })
      }
      handleCheck={(i) =>
        setParties((prev) => {
          const pts = deepCopy(prev);
          pts[curSetupI][tmI].BCs[bcIndex].inputs[i] = !inputs[i];
          return pts;
        })
      }
      handleSelect={(value, i) =>
        setParties((prev) => {
          const pts = deepCopy(prev);
          pts[curSetupI][tmI].BCs[bcIndex].inputs[i] = isNaN(value)
            ? value
            : +value;
          return pts;
        })
      }
    />
  );
}
