import {
  ColoredSpan,
  LGreen,
  Green,
  Red
} from "../../../../styledCpns/DataDisplay";
import actvTalents from "./talents";
import { TalentLvMults } from "../../config";
import { getXtraTalentLv } from "../../../../calculators/helpers";

export const buffs = [
  {
    src: "Elemental Burst",
    desc: ({ toSelf, inputs }) => (
      <>
        The character within its AoE gains an <LGreen>ATK Bonus</LGreen> that
        scales based on Bennett's <LGreen>Base ATK</LGreen>.{" "}
        {!toSelf && <Red>ATK Bonus: {buffs[0].bnValue(...inputs)}.</Red>}
      </>
    ),
    isGranted: () => true,
    affect: "party",
    labels: ["Base ATK", "Elemental Burst Level", "Constellation 1"],
    inputs: [0, 1, false],
    inputTypes: ["text", "text", "check"],
    maxs: [9999, 13, null],
    addBnes: ({ ATTRs, inputs, toSelf, char, charBCs, party }) => {
      const { bnValue } = buffs[0];
      ATTRs.ATK += toSelf
        ? bnValue(
            ATTRs["Base ATK"],
            char["Elemental Burst"] +
              getXtraTalentLv(actvTalents[2], char, party),
            buffs[1].isGranted(char) && charBCs[1].activated
          )
        : bnValue(...inputs);
    },
    bnValue: (baseATK, level, boosted) =>
      Math.round(
        baseATK * ((56 * TalentLvMults[2][level] + (boosted ? 20 : 0)) / 100)
      )
  },
  {
    src: "Constellation 1",
    desc: () => (
      <>
        Fantastic Voyage's <LGreen>ATK increase</LGreen> gains an additional{" "}
        <Green>20%</Green> of Bennett's <LGreen>Base ATK</LGreen>.
      </>
    ),
    isGranted: (char) => char.constellation >= 1,
    affect: "self"
  },
  {
    src: "Constellation 6",
    desc: () => (
      <>
        Sword, Claymore, Polearm characters inside Fantastic Voyage's radius
        gain a <Green>15%</Green> <LGreen>Pyro DMG Bonus</LGreen> and their
        weapons are <LGreen>infused</LGreen> with{" "}
        <ColoredSpan color="Pyro">Pyro</ColoredSpan>.
      </>
    ),
    isGranted: (char) => char.constellation >= 6,
    affect: "party",
    addBnes: ({ ATTRs }) => {
      ATTRs["Pyro DMG Bonus"] += 15;
    },
    canInfuse: (charData) =>
      ["Sword", "Claymore", "Polearm"].includes(charData.weapon),
    infuseElmt: "Pyro",
    infuseRange: ["NA", "CA", "PA"],
    canBeOverrided: true
  }
];
