import styled from "styled-components";
import { useState, useContext } from "react";
import { SetDBCt } from "../../../App";
import { ModalCarpet, AbsoluteBox } from "../../../styledCpns/Layout";
import { Text, ColoredSpan } from "../../../styledCpns/DataDisplay";
import { CloseBtn, ButtonBar } from "../../CommonCpns";
import SortBtns from "./SortBtns";
import { colorCodes } from "../../../data/misc";
import characters from "../../../data/characters";
import { deepCopy } from "../../../helpers/misc";
import { useCloseModal } from "../../../helpers/hooks";

export default function CharSorting(props) {
  const processedChars = props.myChars.map((unit, order) => {
    const { name, level } = unit;
    return { name, level, rarity: characters[unit.name].rarity, order };
  });
  const [list, setList] = useState(processedChars);
  const [dragIndex, setDragIndex] = useState(null);
  const [dropIndex, setDropIndex] = useState(null);
  const { setMyChars } = useContext(SetDBCt);

  const handleDragStart = (e) => {
    setDragIndex(+e.target.id);
  };
  const handleDragEnter = (e) => {
    setDropIndex(+e.target.id);
  };
  const handleDragOver = (e) => {
    e.preventDefault();
  };
  const handleDrop = () => {
    if (dropIndex !== dragIndex) {
      setList((prev) => {
        const newList = deepCopy(prev);
        newList.splice(
          dragIndex < dropIndex ? dropIndex - 1 : dropIndex,
          0,
          newList.splice(dragIndex, 1)[0]
        );
        return newList;
      });
    }
    setDropIndex(null);
  };

  const ref = useCloseModal(props.close);
  return (
    <ModalCarpet>
      <StyledDiv ref={ref} className="px-2 py-3 relative white-glow-shadow">
        <AbsoluteBox top="0.5rem" right="0.5rem">
          <CloseBtn className="glow-on-hover" onClick={props.close} />
        </AbsoluteBox>
        <Text variant="h4" color="orange" align="center">
          Sort by
        </Text>
        <SortBtns setList={setList} />
        <div className="list_wrapper mt-3 custom-sb">
          <div>
            {list.map((unit, i) => (
              <div
                key={i}
                id={i}
                className={
                  "line px-2 py-1 flex align-center" +
                  (dropIndex === i ? " dragged-over" : "")
                }
                draggable="true"
                onDragStart={handleDragStart}
                onDragOver={handleDragOver}
                onDragEnter={handleDragEnter}
                onDrop={handleDrop}
              >
                <button className="grab_btn mr-2 no-pointer-events">
                  <i className="fas fa-sort" />
                </button>
                <Text className="no-pointer-events">
                  <ColoredSpan color={unit.rarity}>
                    <strong>{unit.name}</strong>
                  </ColoredSpan>{" "}
                  (Lv. {unit.level})
                </Text>
              </div>
            ))}
          </div>
        </div>
        <ButtonBar
          className="mt-3"
          texts={["Confirm"]}
          variants={["positive"]}
          handlers={[
            () => {
              setMyChars((prev) => {
                const newChars = deepCopy(prev);
                return list.map((char) => newChars[char.order]);
              });
              props.close();
            }
          ]}
        />
      </StyledDiv>
    </ModalCarpet>
  );
}

let StyledDiv = styled.div`
  width: 90%;
  max-width: 22rem;
  border-radius: 0.5rem;
  background-color: ${colorCodes.cardBg2};
  .list_wrapper {
    height: 60vh;
  }
  .line {
    cursor: default;
    user-select: none;
  }
  .line:hover {
    background-color: ${colorCodes.cardBg1};
  }
  .grab_btn {
    width: 2rem;
    height: 2rem;
    border: none;
    color: ${colorCodes.defaultBg};
    background-color: transparent;
    font-size: 1.25rem;
  }
  .no-pointer-events {
    pointer-events: none;
  }
  .dragged-over {
    border-top: 1px solid white;
  }
`;
