import Buffs from "./Buffs";
import Debuffs from "./Debuffs";
import Target from "./Target";
import { Box } from "../../../../styledCpns/Layout";
import { Text } from "../../../../styledCpns/DataDisplay";

const Case = (props) => {
  return (
    <Box className={"py-3 flex-col " + props.padding}>
      <Text
        className="mb-2"
        variant="h5"
        fontWeight={700}
        align="center"
        color="orange"
      >
        {props.type}
      </Text>
      <Box
        className="full-h custom-sb"
        width={
          props.type === "TARGET"
            ? "17rem"
            : window.innerWidth <= 1050
            ? "18.75rem"
            : "20.75rem"
        }
      >
        {props.children}
      </Box>
    </Box>
  );
};

export default function Modifiers(props) {
  const { setup } = props;
  const { char, selfMCs, art, elmtMCs, party } = setup;
  const { ATTRs, rxnBnes, infusion } = props.result;
  return (
    <div className="full-h flex" styled={{ maxWidth: "90%" }}>
      <Case type="BUFFS USED" padding="pl-3 pr-2">
        <Buffs
          char={char}
          selfBCs={selfMCs.BCs}
          weapon={setup.weapon}
          subWpBCs={setup.subWpMCs.BCs}
          arts={art}
          elmtMCs={elmtMCs}
          infusion={infusion}
          party={party}
          ATTRs={ATTRs}
          rxnBnes={rxnBnes}
        />
      </Case>
      <Case type="DEBUFFS USED" padding="px-2">
        <Debuffs
          char={char}
          selfDCs={selfMCs.DCs}
          artSubDCs={art.subDCs}
          elmtMCs={elmtMCs}
          party={party}
        />
      </Case>
      <Case type="TARGET" padding="pl-2 pr-3">
        <Target char={char} target={setup.target} infusion={infusion} />
      </Case>
    </div>
  );
}
