import { ColoredSpan, LGreen, Green } from "../../../../styledCpns/DataDisplay";
import characters from "../../index";
import data from "./index";

export const buffs = [
  {
    src: "Elemental Skill",
    desc: () => (
      <>
        Within Chonghua Frost Field, all Sword, Claymore and Polearm-wielding
        characters' weapons will be <LGreen>infused</LGreen> with{" "}
        <ColoredSpan color="Cryo">Cryo</ColoredSpan>.
      </>
    ),
    isGranted: () => true,
    affect: "party",
    canInfuse: ({ weapon }) =>
      ["Sword", "Claymore", "Polearm"].includes(weapon),
    infuseElmt: "Cryo",
    infuseRange: ["NA", "CA", "PA"],
    canBeOverrided: true
  },
  {
    src: "Ascension 1 Passive Talent",
    desc: () => (
      <>
        Sword, Claymore, or Polearm-wielding characters within the field created
        by Spirit Blade: Chonghua's Layered Frost have their{" "}
        <LGreen>Normal ATK SPD</LGreen> increased by <Green>8%</Green>.
      </>
    ),
    isGranted: (char) => char.ascension >= 1,
    affect: "party",
    addBnes: ({ ATTRs, char }) => {
      const { weapon } = characters[char.name];
      if (["Sword", "Claymore", "Polearm"].includes(weapon))
        ATTRs["Normal ATK SPD"] += 8;
    }
  },
  {
    src: "Constellation 6",
    desc: () => data.constellation[5].desc,
    isGranted: (char) => char.constellation >= 6,
    affect: "self",
    addBnes: ({ bonuses }) => {
      bonuses.EB.DMG += 15;
    }
  }
];

export const debuffs = [
  {
    src: "Ascension 4 Passive Talent",
    desc: () => (
      <>
        When the field created by Spirit Blade: Chonghua's Layered Frost
        disappears, another spirit blade will be summoned to strike nearby
        opponents and decrease their <LGreen>Cryo RES</LGreen> by{" "}
        <Green>10%</Green> for 8s.
      </>
    ),
    isGranted: (char) => char.ascension >= 4,
    addPntes: ({ rdMult }) => {
      rdMult["Cryo RES"] -= 10;
    }
  }
];
