import { useState, useContext } from "react";
import { ArtChestCt, SetupCt, FixedCts, ManagerCts } from "../index";
import { Img } from "../../../styledCpns/DataDisplay";
import { Box } from "../../../styledCpns/Layout";
import { Pedestal, CopySection } from "./styles";
import ArtPicker from "../../Picker/ArtPicker";
import { FilledCard, EmptyCard } from "./ArtifactCard";
import artifacts from "../../../data/artifacts";
import artifactBuffs from "../../../data/artifacts/artifactBuffs";
import { wikia, artifactIcons, artTypes } from "../../../data/misc";
import { getMainArtBCs } from "../CalcChar/helpers";
import { deepCopy } from "../../../helpers/misc";
import { processNumInp, getArtSets } from "../../../helpers/processors";

export default function SectionArts() {
  const artChest = useContext(ArtChestCt);
  const curSetupI = useContext(SetupCt);
  const { setArtChest } = useContext(FixedCts);
  const [tabNo, setTabNo] = useState(null);
  const { pieces } = artChest[curSetupI];
  const [pickerOn, setPickerOn] = useState(false);
  const { setups } = useContext(ManagerCts);

  const copyArts = (name) => {
    setArtChest((prev) => {
      const chest = deepCopy(prev);
      const index = setups.findIndex((st) => st.name === name);
      chest[curSetupI] = deepCopy(chest[index]);
      return chest;
    });
  };
  const copyOpts = [];
  if (pieces.every((artP) => artP === null)) {
    for (let i in artChest) {
      if (artChest[i].pieces.some((artP) => artP !== null)) {
        copyOpts.push(setups[i].name);
      }
    }
  }
  return (
    <Pedestal>
      {copyOpts.length ? (
        <CopySection options={copyOpts} copy={copyArts} />
      ) : null}
      <div className="flex">
        {artTypes.map((type, index) => {
          const artP = pieces[index];
          return (
            <Box
              key={index}
              border={
                index === tabNo ? "2px solid white" : "0.5px solid transparent"
              }
            >
              <Box
                className="pointer"
                bgImg={artP ? "Gold" : "Gray"}
                onClick={() => setTabNo(index === tabNo ? null : index)}
              >
                <Img
                  width="100%"
                  src={
                    wikia +
                    (artP
                      ? artifacts[artP.set][type].icon
                      : artifactIcons[type]) +
                    ".png"
                  }
                  alt={type}
                  draggable={false}
                />
              </Box>
            </Box>
          );
        })}
      </div>
      {tabNo !== null ? (
        pieces[tabNo] ? (
          <FilledCard
            artP={pieces[tabNo]}
            switch={() => setPickerOn(true)}
            remove={() => setArtPiece(curSetupI, tabNo, null, setArtChest)}
            enhance={(e) =>
              setArtChest((prev) => {
                const chest = deepCopy(prev);
                chest[curSetupI].pieces[tabNo].level = +e.target.value.substr(
                  1
                );
                return chest;
              })
            }
            changeMSType={(e) =>
              setArtChest((prev) => {
                const chest = deepCopy(prev);
                chest[curSetupI].pieces[tabNo].mainSType = e.target.value;
                return chest;
              })
            }
            changeSSType={(value, ssIndex) =>
              setArtChest((prev) => {
                const chest = deepCopy(prev);
                chest[curSetupI].pieces[tabNo].subS[ssIndex].type = value;
                return chest;
              })
            }
            changeSSVal={(value, ssIndex, oldValue) =>
              setArtChest((prev) => {
                const chest = deepCopy(prev);
                chest[curSetupI].pieces[tabNo].subS[
                  ssIndex
                ].val = processNumInp(value, oldValue, 9999);
                return chest;
              })
            }
          />
        ) : (
          <EmptyCard switch={() => setPickerOn(true)} />
        )
      ) : null}
      {pickerOn && (
        <ArtPicker
          artType={artTypes[tabNo]}
          close={() => setPickerOn(false)}
          pick={(item) => setArtPiece(curSetupI, tabNo, item, setArtChest)}
        />
      )}
    </Pedestal>
  );
}

// also used in ./index
export function setArtPiece(curSetupI, pIndex, newPiece, setArtChest) {
  setArtChest((prev) => {
    const chest = deepCopy(prev);
    const art = chest[curSetupI];
    let oldBonusLv = art.sets[0]?.bonusLv;

    art.pieces[pIndex] = newPiece;
    art.sets = getArtSets(art.pieces);
    const set = art.sets[0];
    if (set) {
      if (oldBonusLv === 0 && set.bonusLv) {
        art.BCs = getMainArtBCs(set.name);
        let pos = art.subBCs.findIndex((BC) => BC.set === set.name);
        if (pos !== -1) {
          art.subBCs.splice(pos, 1);
        }
      } else if (oldBonusLv && !set.bonusLv) {
        art.BCs = [];
        const oldSetN = prev[curSetupI].sets[0].name;
        if (artifactBuffs[oldSetN]) {
          for (let index in artifactBuffs[oldSetN]) {
            const buff = artifactBuffs[oldSetN][index];
            if (buff.affect !== "self") {
              const node = { set: oldSetN, activated: false, index };
              if (buff.inputTypes) {
                node.inputs = [];
                buff.inputTypes.forEach((type, j) => {
                  if (type === "crystallize") {
                    node.inputs.push("Pyro");
                  }
                });
              }
              art.subBCs.push(node);
            }
          }
        }
      }
    }
    return chest;
  });
}
