import { useState } from "react";
import { Img } from "../../styledCpns/DataDisplay";
import { Box } from "../../styledCpns/Layout";
import { colorCodes, wikia, weaponIcons, artifactIcons } from "../../data/misc";

export default function useTypeFilter(forWp, initialType) {
  const [types, setTypes] = useState(initialType || []);
  const typeFilter = (
    <div className="mx-1 flex align-center">
      {Object.entries(forWp ? weaponIcons : artifactIcons).map((pair, i) => {
        const index = types.indexOf(pair[0]);
        const isOn = index !== -1;
        return (
          <Box
            key={i}
            className={(forWp ? "" : "p-1 ") + "mr-3 pointer glow-on-hover"}
            borderRadius="50%"
            boxShadow={
              forWp && isOn ? "0 0 3px 3px " + colorCodes.green : "none"
            }
            bgColor={!forWp && isOn ? "green" : "transparent"}
            onClick={() =>
              setTypes((prev) => {
                const newTypes = [...prev];
                if (isOn) {
                  newTypes.splice(index, 1);
                } else {
                  newTypes.push(pair[0]);
                }
                return newTypes;
              })
            }
          >
            <Img
              width={forWp ? "2.5rem" : "2rem"}
              src={wikia + pair[1] + ".png"}
              alt=""
              draggable={false}
            />
          </Box>
        );
      })}
    </div>
  );
  return [types, typeFilter, setTypes];
}
