import ElmtDebuffs from "./ElmtDebuffs";
import SelfDebuffs from "./SelfDebuffs";
import PartyDebuffs from "./PartyDebuffs";
import ArtDebuffs from "./ArtDebuffs";
import { ExpandableList } from "../../../CommonCpns";

export default function Buffs({ char }) {
  return (
    <ExpandableList
      headingList={["Resonance & Reactions", "Self", "Party", "Artifacts"]}
      contentList={[
        <ElmtDebuffs char={char} />,
        <SelfDebuffs char={char} />,
        <PartyDebuffs char={char} />,
        <ArtDebuffs />
      ]}
    />
  );
}
