import { useContext } from "react";
import { FixedCts, SetupCt, ResultCt } from "../../index";
import { Section, Setter, SetterSelect, SetterCheckbox } from "../CmnCpns";
import weapons from "../../../../data/weapons";
import { deepCopy } from "../../../../helpers/misc";

export default function WpBuffsMain(props) {
  const curSetupI = useContext(SetupCt);
  const { setWpRack } = useContext(FixedCts);
  const { allATTRs } = useContext(ResultCt);
  const { type, name, refinement: wpRfm, BCs } = props.weapon;

  return BCs.map((ctrl, bcIndex) => {
    const { activated, index } = ctrl;
    const buff = weapons[type][name].buffs[index];
    return (
      <Section
        key={name + bcIndex}
        checked={activated}
        handleCheck={() =>
          setWpRack((prev) => {
            const newRack = deepCopy(prev);
            newRack[curSetupI].BCs[bcIndex].activated = !activated;
            return newRack;
          })
        }
        heading={name + " (self)"}
        desc={buff.desc({ wpRfm, ATTRs: allATTRs[curSetupI] })}
        setters={
          <SetterSection
            buff={buff}
            inputs={ctrl.inputs}
            setWpRack={setWpRack}
            curSetupI={curSetupI}
            bcIndex={bcIndex}
          />
        }
      />
    );
  });
}

function SetterSection(props) {
  if (!props.buff.labels) {
    return null;
  }
  const { buff, inputs, setWpRack, curSetupI, bcIndex } = props;
  return buff.labels.map((label, i) => {
    const type = buff.inputTypes[i];
    let inputCpn;
    if (type === "stacks") {
      inputCpn = (
        <SetterSelect
          value={inputs[i]}
          onChange={(e) =>
            setWpRack((prev) => {
              const newRack = deepCopy(prev);
              newRack[curSetupI].BCs[bcIndex].inputs[i] = +e.target.value;
              return newRack;
            })
          }
        >
          {[...Array(buff.values[i]).keys()].map((opt) => (
            <option key={opt}>{opt + 1}</option>
          ))}
        </SetterSelect>
      );
    } else if (type === "check") {
      inputCpn = (
        <SetterCheckbox
          type="checkbox"
          checked={inputs[i]}
          onChange={() =>
            setWpRack((prev) => {
              const newRack = deepCopy(prev);
              newRack[curSetupI].BCs[bcIndex].inputs[i] = !inputs[i];
              return newRack;
            })
          }
        />
      );
    } else if (type === "choices") {
      inputCpn = (
        <SetterSelect
          value={inputs[i]}
          onChange={(e) =>
            setWpRack((prev) => {
              const newRack = deepCopy(prev);
              newRack[curSetupI].BCs[bcIndex].inputs[i] = e.target.value;
              return newRack;
            })
          }
        >
          {buff.values[i].map((opt) => (
            <option key={opt}>{opt}</option>
          ))}
        </SetterSelect>
      );
    }
    return (
      <Setter key={i}>
        <p className="label">{label}</p>
        {inputCpn}
      </Setter>
    );
  });
}
