import { useState, useContext, useEffect, useCallback } from "react";
import { SetDBCt } from "../../../../App";
import { Card } from "../../../../styledCpns/Database";
import { Box } from "../../../../styledCpns/Layout";
import WpInventory from "../../../Readers/WpInventory";
import ArtInventory from "../../../Readers/ArtInventory";
import { HiddenSpace } from "./styles";
import { Weapon, ArtPiece, ArtIcon } from "./UpperOverview";
import { SetBonus, StatsBonus } from "./LowerOverview";
import HiddenGear from "./HiddenGear";
import { deepCopy } from "../../../../helpers/misc";

export default function SectionGears(props) {
  const { owner, ownerIndex, weapon, art, artIDs } = props;
  const { setMyChars, setMyWps, setMyArts } = useContext(SetDBCt);
  const [opened, setOpened] = useState(null);
  const [showing, setShowing] = useState(false);
  const [wpInvOn, setWpInvOn] = useState(false);
  const [artInvType, setArtInvType] = useState(null);

  const toggleHiddenCard = useCallback(
    (type) => {
      if (opened === type) {
        setShowing(false);
        setTimeout(() => setOpened(null), 200);
      } else {
        setShowing(true);
        setOpened(type);
      }
    },
    [opened]
  );

  useEffect(() => {
    setShowing(false);
    setOpened(null);
  }, [owner]);

  return (
    <div className="ml-2 flex">
      <Card>
        <Box className="flex-wrap" width="18.75rem">
          {Boolean(weapon) &&
            [weapon].map((wp, i) => (
              <Weapon
                key={i}
                weapon={wp}
                chosen={opened === "weapon"}
                choose={() => toggleHiddenCard("weapon")}
              />
            ))}
          {art.pieces.map((artP, i) =>
            artP ? (
              <ArtPiece
                key={i}
                artP={artP}
                chosen={opened === artP.type}
                choose={() => toggleHiddenCard(artP.type)}
              />
            ) : (
              <ArtIcon key={i} index={i} setArtInvType={setArtInvType} />
            )
          )}
        </Box>
        <SetBonus
          sets={art.sets}
          chosen={opened === "set bonus"}
          toggleHiddenCard={toggleHiddenCard}
        />
        <StatsBonus
          chosen={opened === "stats bonus"}
          toggleHiddenCard={toggleHiddenCard}
        />
      </Card>
      <HiddenSpace className={"hide-sb " + (showing ? "show" : "")}>
        {opened !== null && (
          <HiddenGear
            opened={opened}
            setOpened={setOpened}
            weapon={weapon}
            setWpInvOn={setWpInvOn}
            art={art}
            setArtInvType={setArtInvType}
            ownerIndex={ownerIndex}
            artSBnes={props.artSBnes}
            setShowing={setShowing}
          />
        )}
      </HiddenSpace>

      {wpInvOn && (
        <WpInventory
          owner={owner}
          wpType={weapon.type}
          weaponID={weapon.ID}
          close={() => setWpInvOn(false)}
          btnText="Switch"
          btnClick={(targetWp) => {
            setMyWps((prev) => {
              const newWps = deepCopy(prev);
              newWps[
                newWps.findIndex((wp) => wp.ID === targetWp.ID)
              ].user = owner;
              newWps[props.weaponI].user = targetWp.user;
              return newWps;
            });
            setMyChars((prev) => {
              const newChars = deepCopy(prev);
              newChars[ownerIndex].weaponID = targetWp.ID;
              if (targetWp.user) {
                newChars[
                  newChars.findIndex((char) => char.name === targetWp.user)
                ].weaponID = weapon.ID;
              }
              return newChars;
            });
          }}
        />
      )}
      {Boolean(artInvType) && (
        <ArtInventory
          artType={artInvType}
          owner={owner}
          close={() => setArtInvType(null)}
          btnText="Switch"
          btnClick={(targetArt) => {
            setMyArts((prev) => {
              const newArts = deepCopy(prev);
              newArts[
                newArts.findIndex((art) => art.ID === targetArt.ID)
              ].user = owner;
              const index = newArts.findIndex(
                (art) => art.ID === artIDs[artInvType]
              );
              if (index !== -1) newArts[index].user = targetArt.user;
              return newArts;
            });
            setMyChars((prev) => {
              const newChars = deepCopy(prev);
              newChars[ownerIndex].artIDs[artInvType] = targetArt.ID;
              if (targetArt.user) {
                for (let unit of newChars) {
                  if (unit.name === targetArt.user) {
                    unit.artIDs[artInvType] = artIDs[artInvType];
                    break;
                  }
                }
              }
              return newChars;
            });
          }}
        />
      )}
    </div>
  );
}
