import { Box, AbsoluteBox } from "../../styledCpns/Layout";
import { Img, Text } from "../../styledCpns/DataDisplay";
import { BetaMark } from "../CommonCpns";
import { Cell } from "./Body.styles";
import { wikia, visionIcons } from "../../data/misc";

export default function Body(props) {
  const { forChar } = props;
  return (
    <div className="pr-3 full-h overflow-auto custom-sb">
      <div className="flex-wrap">
        {props.data.map((item, i) => {
          const hasCons = item.constellation !== undefined;
          return (
            <Cell key={i} className={forChar ? "w-vision" : "wo-vision"}>
              <Thumb
                item={item}
                forChar={forChar}
                each={props.each?.[i]}
                pick={() => props.pick(item, i)}
              />
              {forChar && (
                <div className={"badge" + (hasCons ? " w-cons" : "")}>
                  {hasCons && <p className="cons">C{item.constellation}</p>}
                  <Img
                    width="20px"
                    src={wikia + visionIcons[item.vision] + ".png"}
                    alt="vision"
                    draggable={false}
                  />
                </div>
              )}
            </Cell>
          );
        })}
      </div>
    </div>
  );
}

function Thumb(props) {
  const { item, forChar, each } = props;
  return (
    <div className="pointer zoomin-on-hover relative" onClick={props.pick}>
      {item.beta && (
        <AbsoluteBox top={0} left={0} zIndex={1}>
          <BetaMark />
        </AbsoluteBox>
      )}
      <div className="relative">
        <Box
          className={"overflow-hidden flex " + (forChar ? "pt-3" : "p-1")}
          borderRadius="0.5rem 0.5rem 0 0"
          bgImg={item.rarity}
        >
          <Img
            src={wikia + item.icon + ".png"}
            alt=""
            width="100%"
            height="100%"
            draggable={false}
          />
        </Box>
        {Boolean(each) && (
          <AbsoluteBox bottom={0} right="0.25rem">
            <Text color="black" fontWeight={700}>
              {each}
            </Text>
          </AbsoluteBox>
        )}
      </div>
      <Box borderRadius="0 0 0.5rem 0.5rem" bgColor="defaultBg">
        <Text
          className="mx-2 ellipsis"
          color="black"
          fontWeight={700}
          align="center"
        >
          {item.name}
        </Text>
      </Box>
    </div>
  );
}
