import styled from "styled-components";
import { Text } from "../../../styledCpns/DataDisplay";
import { Select } from "../../../styledCpns/Inputs";
import { colorCodes } from "../../../data/misc";

export function Section(props) {
  return (
    <div className="pt-2">
      <div className="mb-1 flex align-center">
        {!props.immutable && (
          <BuffCheckbox
            type="checkbox"
            checked={props.checked}
            onChange={props.handleCheck}
          />
        )}
        <Text fontWeight={700} color="lightGold">
          {props.heading}
        </Text>
      </div>
      <Text>{props.desc}</Text>
      {props.setters}
    </div>
  );
}

export const Setter = styled.div`
  margin: 0.5rem 0.5rem 0 0;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  :not(:last-child) {
    margin-bottom: 1.25rem;
  }
  .label {
    margin-right: 1rem;
    font-size: 1rem;
    line-height: 1.5;
    text-align: right;
  }
`;

export const SetterInput = styled.input`
  padding: 0.25rem 0.5rem;
  width: 4rem;
  border: none;
  border-radius: 0.25rem;
  font-size: 1rem;
  text-align: right;
  outline: none;
  &:focus {
    box-shadow: 0 0 0 3px ${colorCodes.lightGold};
  }
`;

export const SetterCheckbox = styled.input`
  transform: scale(1.6);
  margin-right: 0.2rem;
`;

export const SetterSelect = styled((props) => (
  <Select bgColor="white" borderRadius="0.25rem" fontWeight={700} {...props} />
))`
  padding: 0 0.5rem;
`;

export const BuffCheckbox = styled.input`
  margin-right: 0.75rem;
  transform: scale(1.6);
`;

export const NoContent = (props) => (
  <Text className="mt-4 mb-3" align="center">
    No {props.type} found
  </Text>
);
