import styled from "styled-components";
import { useState, useContext } from "react";
import { SetDBCt } from "../App";
import { Text } from "../styledCpns/DataDisplay";
import { Box, ModalCarpet } from "../styledCpns/Layout";
import { IconBtn } from "../styledCpns/Inputs";
import Download from "./LoadControl/Download";
import Upload from "./LoadControl/Upload";
import { colorCodes } from "../data/misc";
import { useCloseModal } from "../helpers/hooks";

export default function NavBar({ setIntroOn, ...others }) {
  const { setTab } = useContext(SetDBCt);
  function IntroBtn(props) {
    return (
      <NavBtn
        className={
          props.padding +
          (props.border ? " border" : "") +
          " pointer contain-info_sign"
        }
        onClick={() => setIntroOn(true)}
      >
        <IconBtn
          className="mr-2 info_sign"
          icon="fas fa-info-circle"
          size="1.125rem"
          fontSize="1.125rem"
        />
        <Text fontWeight={700}>Introduction</Text>
      </NavBtn>
    );
  }
  return (
    <Box bgColor="rgba(0, 0, 0, 0.6)">
      {window.innerWidth >= 1050 ? (
        <PcInner setTab={setTab} IntroBtn={IntroBtn} {...others} />
      ) : (
        <MobileInner setTab={setTab} IntroBtn={IntroBtn} {...others} />
      )}
    </Box>
  );
}

export const NavBtn = styled.div`
  display: flex;
  align-items: center;
  background-color: ${colorCodes.cardBg3};
  &:hover {
    color: ${colorCodes.lightGold};
  }
  &.current {
    background-color: ${colorCodes.cardBg1};
    color: ${colorCodes.orange};
  }
  &.border {
    border-bottom: 1px solid rgba(255, 255, 255, 0.4);
  }
`;

function PcInner(props) {
  const { IntroBtn } = props;
  return (
    <div className="flex justify-between">
      <div className="flex">
        {["Characters", "Weapons", "Artifacts", "Setups", "Calculator"].map(
          (item, i) => (
            <NavBtn
              key={i}
              className={
                "px-2 py-1" + (item === props.current ? " current" : "")
              }
              onClick={() => props.setTab(item)}
            >
              <Text className="pointer" fontWeight={700}>
                {i < 4 && "My"} {item}
              </Text>
            </NavBtn>
          )
        )}
      </div>
      <Box className="px-1 flex" bgColor="cardBg3">
        <IntroBtn padding="px-2 py-1" border={false} />
        <Download padding="px-2 py-1" border={false} />
        <Upload padding="px-2 py-1" loadData={props.loadData} />
      </Box>
    </div>
  );
}

const BarsBtn = styled.button`
  width: 2.5rem;
  height: 2.5rem;
  border: none;
  background-color: ${colorCodes.cardBg3};
  color: white;
  &.showing {
    background-color: ${colorCodes.cardBg1};
    color: ${colorCodes.lightGold};
  }
`;

function MobileInner(props) {
  const [showing, setShowing] = useState(false);
  return (
    <div className="flex">
      <div className="mr-auto relative">
        <BarsBtn
          className={showing ? "showing" : ""}
          onClick={() => setShowing(!showing)}
        >
          <i className="fas fa-bars" style={{ fontSize: "1.5rem" }} />
        </BarsBtn>
        {showing && <Menu {...props} close={() => setShowing(false)} />}
      </div>
    </div>
  );
}

function Menu(props) {
  const { IntroBtn, close } = props;
  const ref = useCloseModal(close);
  return (
    <ModalCarpet>
      <Box
        ref={ref}
        className="white-glow-shadow overflow-hidden"
        borderRadius="0.25rem"
      >
        <IntroBtn padding="px-4 py-2" border={true} />
        {["Characters", "Weapons", "Artifacts", "Setups", "Calculator"].map(
          (item, i) => (
            <NavBtn
              key={i}
              className={
                "px-4 py-2 border" + (item === props.current ? " current" : "")
              }
              onClick={() => {
                props.setTab(item);
                close();
              }}
            >
              <Text className="pointer" fontWeight={700}>
                {i < 4 && "My"} {item}
              </Text>
            </NavBtn>
          )
        )}
        <Download padding="px-4 py-2" border={true} />
        <Upload padding="px-4 py-2" loadData={props.loadData} closeDD={close} />
      </Box>
    </ModalCarpet>
  );
}
