import { Img } from "../../../styledCpns/DataDisplay";
import { Box, AbsoluteBox } from "../../../styledCpns/Layout";
import { IconBtn } from "../../../styledCpns/Inputs";
import characters from "../../../data/characters";
import { wikia } from "../../../data/misc";

// also used in MySetups
export function TeammateIcon(props) {
  const { mutable } = props;
  return (
    <>
      <Box
        className="zoomin-on-hover overflow-hidden"
        borderRadius="50%"
        bgColor="cardBg3"
      >
        <Img
          className={mutable ? "pointer" : ""}
          src={wikia + characters[props.name].icon + ".png"}
          alt={props.name}
          width="100%"
          borderRadius="inherit"
          draggable={false}
          onClick={props.handleClick}
        />
      </Box>
      {mutable && (
        <AbsoluteBox bottom="-0.25rem" right="-0.625rem">
          <IconBtn
            icon="fas fa-times"
            className="glow-on-hover"
            variant="negative"
            size="1.75rem"
            onClick={props.removeTeammate}
          />
        </AbsoluteBox>
      )}
    </>
  );
}

export function TeammateSlot(props) {
  return (
    <IconBtn
      icon="fas fa-plus"
      color="rgb(255, 255, 255, 0.7)"
      bgColor="cardBg3"
      size="100%"
      fontSize="1.5rem"
      className="glow-on-hover"
      onClick={props.handleClick}
    />
  );
}
