import styled from "styled-components";
import { Collapse } from "@material-ui/core";
import { TextInput, IconBtn } from "../../../../styledCpns/Inputs";
import { Text } from "../../../../styledCpns/DataDisplay";
import { Box } from "../../../../styledCpns/Layout";
import { deepCopy } from "../../../../helpers/misc";
import { getSetupCtrl } from "../../helpers";

const CheckBox = styled.input`
  position: absolute;
  right: 0.5rem;
  transform: scale(2);
`;

export default function SetupCtrl(props) {
  const { setup, index, setTempSetups, savable } = props;
  const isNew = setup.index === null;
  const { expanded, isStandard } = setup;
  return (
    <Box className="my-2 p-2" borderRadius="0.5rem" bgColor="cardBg1">
      <TextInput
        type="text"
        className="full-w px-3 pt-2 pb-1"
        borderRadius="0.375rem"
        fontSize="1.125rem"
        align="center"
        value={setup.name}
        onChange={(e) => {
          if (e.target.value.length <= 16) {
            setTempSetups((prev) => {
              const newTSs = deepCopy(prev);
              newTSs[index].name = e.target.value;
              return newTSs;
            });
          }
        }}
      />
      <div className="mt-3 flex justify-between">
        <div className="ml-1 flex">
          <IconBtn
            icon="fas fa-trash-alt"
            className={
              "p-2 mr-4 " + (isStandard ? "unavailable" : "glow-on-hover")
            }
            variant="negative"
            onClick={() => {
              if (!isStandard) {
                setTempSetups((prev) => {
                  const newTSs = deepCopy(prev);
                  newTSs.splice(index, 1);
                  return newTSs;
                });
              }
            }}
          />
          <IconBtn
            icon="fas fa-copy"
            className={"mr-4 " + (isNew ? "unavailable" : "glow-on-hover")}
            variant="positive"
            onClick={() => {
              if (!isNew && props.belowLimit) {
                setTempSetups((prev) => {
                  const newTSs = deepCopy(prev);
                  newTSs.push({
                    ...getSetupCtrl({ name: setup.name + " (copy)" }),
                    index: setup.index,
                    isStandard: false,
                    checked: false,
                    expanded: false
                  });
                  return newTSs;
                });
              }
            }}
          />
          <IconBtn
            icon="fas fa-ellipsis-h"
            className={!isNew ? "glow-on-hover" : "unavailable"}
            variant={expanded ? "neutral" : "default"}
            onClick={() => {
              if (!isNew)
                setTempSetups((prev) => {
                  const newTSs = deepCopy(prev);
                  newTSs[index].expanded = !newTSs[index].expanded;
                  return newTSs;
                });
            }}
          />
        </div>
        <div className="flex align-center relative">
          {isStandard ? (
            <Text className="mr-1" fontWeight={700}>
              Standard
            </Text>
          ) : (
            <>
              <Text className="mr-5 pr-2">Compared</Text>
              <CheckBox
                type="checkbox"
                checked={setup.checked}
                onChange={() => {
                  if (!isStandard) {
                    setTempSetups((prev) => {
                      const newTSs = deepCopy(prev);
                      newTSs[index].checked = !newTSs[index].checked;
                      return newTSs;
                    });
                  }
                }}
              />
            </>
          )}
        </div>
      </div>
      <Collapse in={expanded}>
        <div className="ml-1 mt-4 flex">
          <IconBtn
            icon="fas fa-save"
            className={"mr-4 " + (savable ? "glow-on-hover" : "unavailable")}
            variant="positive"
            onClick={() => {
              if (savable) props.save();
            }}
          />
          <IconBtn
            icon="fas fa-share-alt"
            className="mr-4 glow-on-hover"
            variant="positive"
            onClick={props.share}
          />
        </div>
      </Collapse>
    </Box>
  );
}
