import { useContext, useState } from "react";
import { Collapse } from "@material-ui/core";
import { TargetCt, FixedCts, ResultCt, SetupCt } from "../../index";
import {
  Text,
  Link,
  CollapseHeading
} from "../../../../styledCpns/DataDisplay";
import Line from "./Line";
import Monster from "./Monster";
import Notes from "./Notes";
import { deepCopy } from "../../../../helpers/misc";

export default function Target(props) {
  const { target } = useContext(TargetCt);
  const { setTarget } = useContext(FixedCts);
  const { allInfusion } = useContext(ResultCt);
  const curSetupI = useContext(SetupCt);
  const [dropped, setDropped] = useState(false);
  const resTypes = Object.keys(target).slice(1);

  return (
    <div>
      <div className="mt-2 px-3">
        <Line
          label="Level"
          color="lightGold"
          value={target.Level}
          handleChange={(e) => {
            const { value } = e.target;
            if (!isNaN(value) && value >= 0 && value <= 100) {
              setTarget((prev) => {
                const newTg = deepCopy(prev);
                newTg.Level = Math.round(+value);
                return newTg;
              });
            }
          }}
        />
        <Monster />
      </div>

      <CollapseHeading
        className={dropped ? "active" : ""}
        onClick={() => setDropped(!dropped)}
      >
        Resistances
      </CollapseHeading>
      <Collapse in={dropped}>
        <div className="pt-3 px-3">
          {resTypes.map((type, i) => {
            const elmt = type.split(" ")[0];
            return (
              <Line
                key={i}
                label={type}
                color={elmt === "Physical" ? "White" : elmt}
                value={target[type]}
                handleChange={(e) => {
                  const res = +e.target.value;
                  if (!isNaN(res) && res <= 999) {
                    setTarget((prev) => {
                      const newTg = deepCopy(prev);
                      newTg[type] = Math.round(res);
                      return newTg;
                    });
                  }
                }}
              />
            );
          })}
          <Text>
            You can search for your target's Resistances on{" "}
            <Link
              href="https://genshin-impact.fandom.com/wiki/Resistance#Base_Enemy_Resistances"
              rel="noreferrer"
              target="_blank"
            >
              this page of the Genshin Impact Wiki
            </Link>
            .
          </Text>
        </div>
      </Collapse>
      <Notes char={props.char} infusion={allInfusion[curSetupI]} />
    </div>
  );
}
