import { LGreen, Green, Red } from "../../../../styledCpns/DataDisplay";

const ZhongliA4 = (val) => <Red>DMG Bonus: {val} (base).</Red>;

export const buffs = [
  {
    src: "Ascension 1 Passive Talent",
    desc: () => (
      <>
        When the Jade Shield takes DMG, it will Fortify: Fortified characters
        have <Green>5%</Green> increased <LGreen>Shield Strength</LGreen>. Can
        stack up to <Green>5</Green> times, and lasts until the Jade Shield
        disappears.
      </>
    ),
    isGranted: (char) => char.ascension >= 1,
    affect: "party",
    selfLabels: ["Stacks"],
    labels: ["Stacks"],
    inputs: [1],
    inputTypes: ["select"],
    maxs: [5],
    addBnes: ({ ATTRs, inputs }) => {
      ATTRs["Shield Strength"] += inputs[0] * 5;
    }
  },
  {
    src: "Ascension 4 Passive Talent",
    desc: ({ ATTRs }) => {
      const result = buffs[1].bnValue(ATTRs);
      return (
        <>
          Zhongli deals bonus DMG based on his <LGreen>Max HP</LGreen>: <br />•{" "}
          <LGreen>
            Normal Attack, Charged Attack, and Plunging Attack DMG
          </LGreen>{" "}
          increased by <Green>1.39%</Green>. {ZhongliA4(result[0])}
          <br />•{" "}
          <LGreen>
            Dominus Lapidis Stone Stele, resonance, and hold DMG
          </LGreen>{" "}
          increased by <Green>1.9%</Green>. {ZhongliA4(result[1])}
          <br />• <LGreen>Planet Befall DMG</LGreen> increased by{" "}
          <Green>33%</Green>. {ZhongliA4(result[2])}
        </>
      );
    },
    isGranted: (char) => char.ascension >= 4,
    affect: "self",
    addFinalBnes: ({ ATTRs, bonuses }) => {
      const result = buffs[1].bnValue(ATTRs);
      bonuses.NA.Flat += result[0];
      bonuses.CA.Flat += result[0];
      bonuses.PA.Flat += result[0];
      bonuses.ES.Flat += result[1];
      bonuses.EB.Flat += result[2];
    },
    bnValue: (ATTRs) =>
      [1.39, 1.9, 33].map((mult) => Math.round((ATTRs.HP * mult) / 100))
  }
];

export const debuffs = [
  {
    src: "Jade Shield",
    desc: () => (
      <>
        Characters protected by the Jade Shield will decrease the{" "}
        <LGreen>Elemental RES</LGreen> and <LGreen>Physical RES</LGreen> of
        opponents in a small AoE by <Green>20%</Green>.
      </>
    ),
    isGranted: () => true,
    addPntes: ({ rdMult }) => {
      const { DEF, ...RESes } = rdMult;
      for (let res in RESes) {
        rdMult[res] -= 20;
      }
    }
  }
];
