import styled from "styled-components";
import { useState, useContext, useEffect } from "react";
import { MyCharsCt } from "../../App";
import { Text } from "../../styledCpns/DataDisplay";
import { Box, AbsoluteBox } from "../../styledCpns/Layout";
import { IconBtn } from "../../styledCpns/Inputs";
import Picker from "../Picker";
import CharSorting from "./CharSorting";
import characters from "../../data/characters";
import { wikia } from "../../data/misc";

export default function TopBar(props) {
  const { chosenI, setChosenI, charListRef } = props;
  const [sorting, setSorting] = useState(false);
  const [gridview, setGridview] = useState(false);
  const myChars = useContext(MyCharsCt);

  useEffect(() => {
    const thisCpn = charListRef.current;
    const scrollHorizontally = (e) => {
      const delta = Math.max(-1, Math.min(1, e.wheelDelta || -e.detail));
      thisCpn.scrollLeft -= delta * 50;
      e.preventDefault();
    };
    thisCpn.addEventListener("wheel", scrollHorizontally);
    return () => thisCpn.removeEventListener("wheel", scrollHorizontally);
  }, [charListRef]);

  return (
    <Box className="full-w flex justify-center" bgColor="cardBg2">
      <StyledDiv>
        <div ref={charListRef} className="mt-2 flex full-w hide-sb">
          {myChars.length ? (
            myChars.map((char, i) => (
              <SideIconBox
                key={i}
                chosen={chosenI === i}
                choose={() => setChosenI(i)}
                sideIcon={characters[char.name].sideIcon}
              />
            ))
          ) : (
            <Box
              className="full-w flex justify-end align-center"
              height="4.875rem"
            >
              <Text variant="h2" color="lightGold">
                Add New Characters {">>"}
              </Text>
            </Box>
          )}
        </div>
        {Boolean(myChars.length) && (
          <AbsoluteBox top="2rem" right="100%" className="flex">
            <IconBtn
              className="mr-3 glow-on-hover"
              icon="fas fa-sort"
              variant="positive"
              fontSize="1.25rem"
              onClick={() => setSorting(true)}
            />
            <IconBtn
              icon="fas fa-th"
              className="mr-3 glow-on-hover"
              variant="positive"
              onClick={() => setGridview(true)}
            />
          </AbsoluteBox>
        )}
        <img
          src={wikia + "4/48/System_Wish.png"}
          alt="wish"
          className="wish_btn pointer"
          draggable={false}
          onClick={() => props.setPickerOn(true)}
        />
      </StyledDiv>
      {sorting && (
        <CharSorting close={() => setSorting(false)} myChars={myChars} />
      )}
      {gridview && (
        <CharPicker
          myChars={myChars}
          close={() => setGridview(false)}
          setChosenI={setChosenI}
          charListRef={charListRef}
        />
      )}
    </Box>
  );
}

function SideIconBox(props) {
  return (
    <div
      className={"mx-1 cell pointer" + (props.chosen ? " chosen" : "")}
      onClick={props.choose}
    >
      <div className="icon_wrapper m-2">
        <img
          className="side_icon"
          src={wikia + props.sideIcon + ".png"}
          alt="side-icon"
          draggable={false}
        />
      </div>
    </div>
  );
}

function CharPicker(props) {
  const { myChars, charListRef } = props;
  const data = myChars.map((char) => {
    const { name, constellation } = char;
    const { icon, rarity, vision, weapon } = characters[name];
    return { name, constellation, icon, rarity, vision, weapon };
  });
  return (
    <Picker
      data={data}
      dataType="Character"
      close={props.close}
      pick={({ name }) => {
        const index = myChars.findIndex((char) => char.name === name);
        props.setChosenI(index);
        if (charListRef.current) charListRef.current.scrollLeft = index * 84;
      }}
    />
  );
}

let StyledDiv = styled.div`
  position: relative;
  width: 70%;
  max-width: 60%;
  .cell {
    border-bottom: 3px solid transparent;
  }
  .cell.chosen {
    border-bottom-color: rgb(92, 188, 212);
  }
  .icon_wrapper {
    width: 60px;
    min-width: 60px;
    height: 60px;
    border-radius: 50%;
    border: 3.5px solid rgba(185, 185, 185, 0.3);
    background: rgba(0, 0, 0, 0.3);
  }
  .cell.chosen > .icon_wrapper {
    border-color: rgb(92, 188, 212);
    background-color: rgb(92, 188, 212);
  }
  .side_icon {
    width: 85px;
    transform: translate(-15px, -32px);
  }
  .wish_btn {
    position: absolute;
    top: 1rem;
    left: calc(100% + 1.5rem);
    width: 60px;
    border-radius: 50%;
  }
  .wish_btn:hover {
    box-shadow: 0 0 3px 3px #fff;
  }
  @media (min-width: 768px) {
    max-width: 70%;
  }
`;
