import { useContext, useState } from "react";
import { SetDBCt, MyCharsCt } from "../../App";
import { Box } from "../../styledCpns/Layout";
import { Card } from "../../styledCpns/Database";
import { ButtonBar, EquippedChar, Confirmation } from "../CommonCpns";
import MyArtCard from "../Readers/MyArtCard";
import Picker from "../Picker";
import characters from "../../data/characters";
import artifacts from "../../data/artifacts";
import { deepCopy } from "../../helpers/misc";

export default function RightSection(props) {
  const { art } = props;
  const myChars = useContext(MyCharsCt);
  const { setMyChars, setMyArts } = useContext(SetDBCt);
  const [pickerOn, setPickerOn] = useState(false);
  const [removing, setRemoving] = useState(false);
  const [swapTarget, setSwapTarget] = useState(null);

  const swap = (name) => {
    if (!art) return;
    const { ID, type, user } = art;
    const targetCharI = myChars.findIndex((char) => char.name === name);
    setMyArts((prev) => {
      const arts = deepCopy(prev);
      const artI_new = arts.findIndex((item) => item.ID === ID);
      arts[artI_new].user = name;
      const artID = myChars[targetCharI].artIDs[type];
      if (artID) {
        const artI_old = arts.findIndex((item) => item.ID === artID);
        arts[artI_old].user = user;
      }
      return arts;
    });
    setMyChars((prev) => {
      const newChars = deepCopy(prev);
      if (user) {
        const currentCharI = newChars.findIndex((char) => char.name === user);
        newChars[currentCharI].artIDs[type] =
          newChars[targetCharI].artIDs[type];
      }
      newChars[targetCharI].artIDs[type] = ID;
      return newChars;
    });
  };

  return (
    <div className="flex-col justify-between">
      <Card>
        <Box className="hide-sb" width="18.75rem" height="25rem">
          {art ? <MyArtCard art={art} mutable={true} /> : null}
        </Box>
        {art ? (
          <ButtonBar
            className="mt-3"
            texts={["Remove", "Equip"]}
            handlers={[() => setRemoving(true), () => setPickerOn(true)]}
          />
        ) : null}
      </Card>
      {art?.user ? <EquippedChar user={art.user} /> : null}
      {pickerOn && (
        <CharPicker
          user={art.user}
          myChars={myChars}
          setSwapTarget={setSwapTarget}
          swap={swap}
          close={() => setPickerOn(false)}
        />
      )}
      {removing && (
        <Confirmation
          message={
            <>
              Do you wish to remove "<b>{artifacts[art.set][art.type].name}</b>
              "?{" "}
              {Boolean(art.user) && (
                <>
                  It is currently used by <b>{art.user}</b>.
                </>
              )}
            </>
          }
          close={() => setRemoving(false)}
          confirm={() => {
            const { ID, type, user } = art;
            setMyArts((prev) => {
              const arts = deepCopy(prev);
              const index = arts.findIndex((item) => item.ID === ID);
              arts.splice(index, 1);
              return arts;
            });
            if (user) {
              setMyChars((prev) => {
                const newChars = deepCopy(prev);
                newChars[
                  newChars.findIndex((unit) => unit.name === user)
                ].artIDs[type] = null;
                return newChars;
              });
            }
          }}
        />
      )}
      {swapTarget ? (
        <Confirmation
          message={
            <>
              <b>{art.user}</b> is currently using "
              <b>{artifacts[art.set][art.type].name}</b>". Swap?
            </>
          }
          close={() => setSwapTarget(null)}
          confirm={() => swap(swapTarget)}
        />
      ) : null}
    </div>
  );
}

function CharPicker(props) {
  const { user } = props;
  const data = [];
  for (let { name, constellation } of props.myChars) {
    if (name !== user) {
      const { icon, rarity, vision, weapon } = characters[name];
      data.push({ name, icon, rarity, vision, weapon, constellation });
    }
  }
  return (
    <Picker
      data={data}
      dataType="Character"
      close={props.close}
      pick={({ name }) => {
        if (user) {
          props.setSwapTarget(name);
        } else {
          props.swap(name);
        }
      }}
    />
  );
}
