import styled from "styled-components";
import { ModalCarpet, Box, AbsoluteBox } from "../../styledCpns/Layout";
import { Text, Img } from "../../styledCpns/DataDisplay";
import { CloseBtn } from "../CommonCpns";
import { wikia, colorCodes } from "../../data/misc";
import { useCloseModal } from "../../helpers/hooks";

export default function PrePicker({ close, choices, pick }) {
  const ref = useCloseModal(close);
  return (
    <ModalCarpet>
      <Box
        ref={ref}
        className="p-3 relative white-glow-shadow"
        borderRadius="1rem"
        bgColor="cardBg3"
      >
        <AbsoluteBox top="0.5rem" right="0.5rem">
          <CloseBtn onClick={close} />
        </AbsoluteBox>
        <Text className="mt-2" variant="h3" align="center">
          Choose a Type
        </Text>
        <div className="mt-3 mb-1 flex">
          {Object.entries(choices).map((pair, i) => (
            <IconWrapper
              key={i}
              className="mx-1 p-1 pointer"
              onClick={() => pick(pair[0])}
            >
              <Img
                width="100%"
                src={wikia + pair[1] + ".png"}
                alt={pair[0]}
                draggable={false}
              />
            </IconWrapper>
          ))}
        </div>
      </Box>
    </ModalCarpet>
  );
}

const IconWrapper = styled.div`
  width: 3.5rem;
  border-radius: 50%;
  &:hover {
    background-color: ${colorCodes.lightGold};
  }
  @media (min-width: 440px) {
    .big-icon {
      width: 4.5rem;
    }
  }
`;
