import { ColoredSpan, LGreen, Green } from "../../../../styledCpns/DataDisplay";
import { TalentLvMults } from "../../config";
import { incAllNAs } from "../../../../helpers/calculators";

export const buffs = [
  {
    src: "Elemental Burst",
    desc: ({ char }) => (
      <>
        Increases Xiao's <LGreen>Normal / Charged / Plunge Attack DMG</LGreen>{" "}
        by <Green>{buffs[0].bnValue(char)}%</Green> and converts them to{" "}
        <ColoredSpan color="Anemo">Anemo DMG</ColoredSpan>.
      </>
    ),
    isGranted: () => true,
    affect: "self",
    addBnes: ({ bonuses, char }) => {
      incAllNAs(bonuses, buffs[0].bnValue(char));
    },
    bnValue: (char) => {
      const level = char["Elemental Burst"] + (char.constellation >= 3 ? 3 : 0);
      return Math.round(58.45 * TalentLvMults[5][level] * 100) / 100;
    },
    canInfuse: () => true,
    infuseRange: ["NA", "CA", "PA"],
    canBeOverrided: false
  },
  {
    src: "Ascension 1 Passive Talent",
    desc: () => (
      <>
        With the effects of Bane of All Evil, <LGreen>all DMG</LGreen> dealt by
        Xiao increases by <Green>5%</Green>. DMG increases by a further{" "}
        <Green>5%</Green> for every 3s the ability persists. The maximum DMG
        Bonus is <Green>25%</Green>.
      </>
    ),
    isGranted: (char) => char.ascension >= 1,
    affect: "self",
    selfLabels: ["Stacks"],
    inputs: [1],
    inputTypes: ["select"],
    maxs: [5],
    addBnes: ({ bonuses, inputs }) => {
      bonuses.AllDmg += 5 * inputs[0];
    }
  },
  {
    src: "Ascension 4 Passive Talent",
    desc: () => (
      <>
        Using Lemniscatic Wind Cycling increases the DMG of subsequent{" "}
        <LGreen>Lemniscatic Wind Cycling</LGreen> by <Green>15%</Green> for 10s.
        This effect lasts for 7s, and has a maximum of <Green>3</Green> stacks.
      </>
    ),
    isGranted: (char) => char.ascension >= 4,
    affect: "self",
    selfLabels: ["Stacks"],
    inputs: [1],
    inputTypes: ["select"],
    maxs: [3],
    addBnes: ({ bonuses, inputs }) => {
      bonuses.ES.DMG += 15 * inputs[0];
    }
  }
];
