import styled from "styled-components";
import { Collapse } from "@material-ui/core";
import { useState } from "react";
import { Text } from "../../styledCpns/DataDisplay";
import { IconBtn } from "../../styledCpns/Inputs";
import { AbsoluteBox, Box } from "../../styledCpns/Layout";
import { StyledHeading } from "../../styledCpns/Database";
import { CloseBtn } from "../CommonCpns";
import CharFilter from "./CharFilter";
import { artNames } from "../../data/misc";

export default function Heading(props) {
  const [filterOn, setFilterOn] = useState(false);
  const { dataType, massAdd, needMassAdd } = props;
  return (
    <StyledHeading>
      {(window.innerWidth >= 769 || !needMassAdd) && (
        <p className="heading_text">{artNames[dataType] || dataType}</p>
      )}
      {dataType === "Character" && (
        <>
          <IconBtn
            icon="fas fa-filter"
            className={"filter_btn glow-on-hover" + (filterOn ? " on" : "")}
            onClick={() => setFilterOn(!filterOn)}
          />
          <AbsoluteBox className="full-w" top="100%" left={0} zIndex={3}>
            <Box
              borderRadius="0 0 0.5rem 0.5rem"
              bgColor="cardBg3"
              boxShadow="var(--cmn-shadow)"
            >
              <Collapse in={filterOn}>
                <CharFilter
                  setFilterOn={setFilterOn}
                  filter={props.filter}
                  setFilter={props.setFilter}
                />
              </Collapse>
            </Box>
          </AbsoluteBox>
        </>
      )}
      {needMassAdd && (
        <MassAddArea className="flex align-center">
          <input
            type="checkbox"
            value={massAdd}
            onChange={() => props.setMassAdd(!massAdd)}
          />
          <Text className="mt-1 ml-2" fontWeight={700} color="black">
            Mass Add ({props.count})
          </Text>
        </MassAddArea>
      )}
      <CloseBtn className="close_btn" onClick={props.close} />
    </StyledHeading>
  );
}

const MassAddArea = styled.div`
  position: absolute;
  right: 4rem;
  input {
    transform: scale(1.6);
  }
`;
