import { LGreen, Green } from "../../../../styledCpns/DataDisplay";
import data from "./index";

export const buffs = [
  {
    src: "Constellation 4",
    desc: () => data.constellation[3].desc,
    isGranted: (char) => char.constellation >= 4,
    affect: "self",
    addBnes: ({ ATTRs }) => {
      ATTRs["Anemo DMG Bonus"] += 25;
    }
  }
];

export const debuffs = [
  {
    src: "Constellation 2",
    desc: () => (
      <>
        Skyward Sonnet decreases opponents' <LGreen>Anemo RES</LGreen> and{" "}
        <LGreen>Physical RES</LGreen> by <Green>12%</Green> for 10s.
      </>
    ),
    isGranted: (char) => char.constellation >= 2,
    addPntes: ({ rdMult }) => {
      rdMult["Physical RES"] -= 12;
      rdMult["Anemo RES"] -= 12;
    }
  },
  {
    src: "Constellation 2",
    desc: () => (
      <>
        Opponents launched by Skyward Sonnet suffer an additional{" "}
        <Green>12%</Green> <LGreen>Anemo RES</LGreen> and{" "}
        <LGreen>Physical RES</LGreen> decrease while airborne.
      </>
    ),
    isGranted: (char) => char.constellation >= 2,
    addPntes: ({ rdMult }) => {
      rdMult["Physical RES"] -= 12;
      rdMult["Anemo RES"] -= 12;
    }
  },
  {
    src: "Constellation 6",
    desc: () => data.constellation[5].desc,
    isGranted: (char) => char.constellation >= 6,
    selfLabels: ["Element Absorbed"],
    labels: ["Element Absorbed"],
    inputTypes: ["absorption"],
    addPntes: ({ rdMult, inputs }) => {
      rdMult["Anemo RES"] -= 20;
      rdMult[inputs[0] + " RES"] -= 20;
    }
  }
];
