export const LiyueSeries = {
  addBnes: ({ ATTRs, wpRfm }) => {
    ATTRs["Shield Strength"] += 15 + wpRfm * 5;
  },
  buffs: [
    {
      affect: "self",
      labels: ["Stacks", "Protected by a Shield"],
      inputTypes: ["stacks", "check"],
      values: [5],
      addBnes: ({ ATTRs, wpRfm, inputs }) => {
        ATTRs["ATK%"] += (3 + wpRfm) * inputs[0] * (inputs[1] ? 2 : 1);
      },
      desc: ({ wpRfm }) => LiyueSeries.pasvDescs({ wpRfm })[0].buff
    }
  ],
  pasvName: "Golden Majesty",
  pasvDescs: ({ wpRfm }) => [
    {
      bonus: <>Increases Shield Strength by {15 + wpRfm * 5}%</>,
      buff: (
        <>
          Scoring hits on opponents increases <LGreen>ATK</LGreen> by{" "}
          <Green>{3 + wpRfm}%</Green> for 8s. Max <Green>5</Green> stacks. Can
          only occur once every 0.3s. While protected by a shield, this ATK
          increase effect is increased by <LGreen>100%</LGreen>.
        </>
      )
    }
  ]
};

export const LithicSeries = {
  addBnes: ({ ATTRs, wpRfm, charData, party }) => {
    if (party) {
      let stacks = charData.nation === "Liyue" ? 1 : 0;
      for (let { name } of party) {
        if (name && characters[name].nation === "Liyue") {
          stacks++;
        }
      }
      ATTRs["ATK%"] += (6 + wpRfm) * stacks;
      ATTRs["CRIT Rate"] += (2 + wpRfm) * stacks;
    }
  },
  pasvName: "Lithic Axiom - Unity",
  pasvDescs: ({ wpRfm }) => [
    {
      buff: (
        <>
          For every character in the party who hails from Liyue, the character
          who equips this weapon gains <Green>{6 + wpRfm}%</Green>{" "}
          <LGreen>ATK</LGreen> increase and <Green>{2 + wpRfm}%</Green>{" "}
          <LGreen>CRIT Rate</LGreen> increase. This effect stacks up to{" "}
          <Green>4</Green> times.
        </>
      )
    }
  ]
};

