export const WatatsumiSeries = {
  addBnes: ({ bonuses, wpRfm, charData, party }) => {
    if (bonuses && party) {
      bonuses.EB.DMG += WatatsumiSeries.bnValue(charData, party, wpRfm);
    }
  },
  bnValue: (charData, party, wpRfm) => {
    let result = 0;
    for (let tl of charData.actvTalents) {
      if (tl.type === "Elemental Burst") {
        result = tl.energyCost;
      }
    }
    for (let teammate of party) {
      result += getEBcost(teammate.name);
    }
    const bonus = Math.round(result * (9 + wpRfm * 3)) / 100;
    return Math.min(bonus, 30 + wpRfm * 10);
  },
  pasvName: "Watatsumi Wavewalker",
  pasvDescs: ({ wpRfm }) => [
    {
      bonus: (
        <>
          For every point of the entire party's combined maximum Energy
          capacity, the <LGreen>Elemental Burst DMG</LGreen> of the character
          equipping this weapon is increased by{" "}
          <Green>{Math.round(9 + wpRfm * 3) / 100}%</Green>, up to a maximum of{" "}
          <Green>{30 + wpRfm * 10}%</Green>.
        </>
      ),
      calcBonus: ({ charData, party }) => (
        <Red>
          Total Elemental Burst DMG:{" "}
          {WatatsumiSeries.bnValue(charData, party, wpRfm)}%.
        </Red>
      )
    }
  ]
};
