import { useState } from "react";
import { AbsoluteBox } from "../../styledCpns/Layout";
import { ButtonBar } from "../CommonCpns";
import HiddenScroll from "../CommonCpns/HiddenScroll";
import { hasDupStat } from "../MyArtifacts/Filter";
import { colorCodes } from "../../data/misc";
import { initArtStatFilter } from "../../helpers/misc";
import useArtStatsFilter from "../../helpers/hooks/useArtStatsFilter";
import useArtSetFilter from "../../helpers/hooks/useArtSetFilter";

export default function HiddenFilter({ filterOn, ...others }) {
  return (
    <AbsoluteBox top="100%" left={0} zIndex={3} className="full-w">
      <HiddenScroll
        on={filterOn}
        className="flex justify-center"
        style={{
          borderRadius: "0 0 0.5rem 0.5rem",
          boxShadow: "var(--cmn-shadow)",
          backgroundColor: colorCodes.cardBg3
        }}
        openHeight="28.85rem"
        duration={150}
        timingFunc="linear"
      >
        <ArtFilter {...others} />
      </HiddenScroll>
    </AbsoluteBox>
  );
}

function ArtFilter(props) {
  const { filter, artType, close } = props;
  const [err, setErr] = useState(false);
  const [stats, statsFilter, setStats] = useArtStatsFilter(
    filter.stats,
    err,
    artType
  );
  const [sets, setFilter] = useArtSetFilter(props.arts, filter.sets, artType);
  const resetable = stats.main !== "All" || stats.subs.some((s) => s !== "All");

  return (
    <div className="p-3 flex hide-sb">
      <div>
        <ButtonBar
          className="mb-2"
          texts={["Reset Stats"]}
          availables={[resetable]}
          handlers={[
            () => {
              if (resetable) setStats(initArtStatFilter());
            }
          ]}
        />
        {statsFilter}
        <ButtonBar
          className="mt-3"
          texts={["Cancel", "Confirm"]}
          handlers={[
            close,
            () => {
              if (hasDupStat(stats)) {
                setErr(true);
                return;
              }
              filter.setStats(stats);
              filter.setSets(sets);
              close();
            }
          ]}
        />
      </div>
      {setFilter}
    </div>
  );
}
