import { LGreen, Green, Red } from "../../../styledCpns/DataDisplay";
import { baseAtkType, subStatsType } from "../config";
import { LiyueSeries } from "../series";
import { incAllElmtDmgBnes } from "../../../helpers/calculators";

const GoldCatalysts = {
  "Kagura's Verity": {
    code: 122,
    beta: true,
    icon: "c/cf/Weapon_Apprentice%27s_Notes",
    rarity: "Gold",
    mainStat: {
      type: "Base ATK",
      value: baseAtkType[46]
    },
    subStats: [
      {
        type: "CRIT DMG",
        value: subStatsType["14.4%"]
      }
    ],
    buffs: [
      {
        affect: "self",
        labels: ["Stacks"],
        inputTypes: ["stacks"],
        values: [3],
        addBnes: ({ ATTRs, bonuses, wpRfm, inputs }) => {
          bonuses.ES.DMG += (9 + wpRfm * 3) * inputs[0];
          if (inputs[0] === 3) incAllElmtDmgBnes(ATTRs, 9 + wpRfm * 3);
        },
        desc: ({ wpRfm }) =>
          GoldCatalysts["Kagura's Verity"].pasvDescs({ wpRfm })[0].buff
      }
    ],
    pasvName: "Kagura Dance of the Sacred Sakura",
    pasvDescs: ({ wpRfm }) => [
      {
        buff: (
          <>
            The wielder gains the Kagura Dance effect when using an Elemental
            Skill, causing their <LGreen>Elemental Skill DMG</LGreen> to
            increase by <Green>{9 + wpRfm * 3}%</Green> for 12s. Max 3 stacks.
            This character will gain <Green>{9 + wpRfm * 3}%</Green>{" "}
            <LGreen>All Elemental DMG Bonus</LGreen> when they possess 3 stacks.
          </>
        )
      }
    ]
  },
  "Everlasting Moonglow": {
    code: 34,
    icon: "e/e1/Weapon_Everlasting_Moonglow",
    rarity: "Gold",
    mainStat: {
      type: "Base ATK",
      value: baseAtkType[46]
    },
    subStats: [
      {
        type: "HP%",
        value: subStatsType["10.8%"]
      }
    ],
    addBnes: ({ ATTRs, wpRfm }) => {
      ATTRs["Healing Bonus"] += 7.5 + wpRfm * 2.5;
    },
    addFinalBnes: ({ ATTRs, bonuses, wpRfm }) => {
      if (bonuses) {
        bonuses.NA.Flat += GoldCatalysts["Everlasting Moonglow"].bnValue(
          ATTRs,
          wpRfm
        );
      }
    },
    bnValue: (ATTRs, wpRfm) =>
      Math.round(((ATTRs["Base HP"] + ATTRs.HP) * (0.75 + wpRfm * 0.25)) / 100),
    pasvName: "Byakuya Kougetsu",
    pasvDescs: ({ wpRfm }) => [
      {
        bonus: (
          <>
            <LGreen>Healing Bonus</LGreen> increased by{" "}
            <Green>{7.5 + wpRfm * 2.5}%</Green>,{" "}
            <LGreen>Normal Attack DMG</LGreen> is increased by{" "}
            <Green>{0.75 + wpRfm * 0.25}%</Green> of the <LGreen>Max HP</LGreen>{" "}
            of the character equipping this weapon.
          </>
        ),
        calcBonus: ({ ATTRs }) => (
          <Red>
            Normal Attack DMG Bonus:{" "}
            {GoldCatalysts["Everlasting Moonglow"].bnValue(ATTRs, wpRfm)}{" "}
            (base).
          </Red>
        ),
        others: (
          <>
            For 12s after using an Elemental Burst, Normal Attacks that hit
            opponents will restore 0.6 Energy. Energy can be restored this way
            once every 0.1s.
          </>
        )
      }
    ]
  },
  "Skyward Atlas": {
    code: 31,
    icon: "3/33/Weapon_Skyward_Atlas",
    rarity: "Gold",
    mainStat: {
      type: "Base ATK",
      value: baseAtkType[48]
    },
    subStats: [
      {
        type: "ATK%",
        value: subStatsType["7.2%"]
      }
    ],
    addBnes: ({ ATTRs, wpRfm }) => {
      incAllElmtDmgBnes(ATTRs, 9 + wpRfm * 3);
    },
    pasvProcD: [
      {
        name: "Clouds DMG",
        baseSType: "ATK",
        multiplier: [0, 1.6, 2, 2.4, 2.8, 3.2],
        dmgType: "Physical"
      }
    ],
    pasvName: "Wandering Clouds",
    pasvDescs: ({ wpRfm }) => [
      {
        bonus: (
          <>
            Increases <LGreen>Elemental DMG Bonus</LGreen> by{" "}
            <Green>{9 + wpRfm * 3}%</Green>. Normal Attack hits have a{" "}
            <LGreen>50% chance</LGreen> to earn the favor of the clouds. which
            actively seek out nearby opponents to attack for 15s, dealing{" "}
            <Green>{120 + wpRfm * 40}%</Green> <LGreen>ATK</LGreen> DMG. Can
            only occur once every <LGreen>30s</LGreen>.
          </>
        )
      }
    ]
  },
  "Lost Prayer to the Sacred Winds": {
    code: 32,
    icon: "9/98/Weapon_Lost_Prayer_to_the_Sacred_Winds",
    rarity: "Gold",
    mainStat: {
      type: "Base ATK",
      value: baseAtkType[46]
    },
    subStats: [
      {
        type: "CRIT Rate",
        value: subStatsType["7.2%"]
      }
    ],
    buffs: [
      {
        affect: "self",
        labels: ["Stacks"],
        inputTypes: ["stacks"],
        values: [5],
        addBnes: ({ ATTRs, wpRfm, inputs }) => {
          incAllElmtDmgBnes(ATTRs, (6 + wpRfm * 2) * inputs[0]);
        },
        desc: ({ wpRfm }) =>
          GoldCatalysts["Lost Prayer to the Sacred Winds"].pasvDescs({
            wpRfm
          })[0].buff
      }
    ],
    pasvName: "Boundless Blessing",
    pasvDescs: ({ wpRfm }) => [
      {
        bonus: <>Increases Movement SPD by 10%.</>,
        buff: (
          <>
            When in battle, gain an <Green>{6 + wpRfm * 2}%</Green>{" "}
            <LGreen>Elemental DMG Bonus</LGreen> every 4s. Max <Green>4</Green>{" "}
            stacks.
          </>
        ),
        others: <>Lasts until the character falls or leaves combat.</>
      }
    ]
  },
  "Memory of Dust": {
    code: 33,
    icon: "c/ca/Weapon_Memory_of_Dust",
    rarity: "Gold",
    mainStat: {
      type: "Base ATK",
      value: baseAtkType[46]
    },
    subStats: [
      {
        type: "ATK%",
        value: subStatsType["10.8%"]
      }
    ],
    ...LiyueSeries
  }
};

export default GoldCatalysts;
