import { ColoredSpan, LGreen, Green } from "../../../../styledCpns/DataDisplay";
import { TalentLvMults } from "../../config";
import actvTalents from "./talents";
import { getXtraTalentLv } from "../../../../calculators/helpers";

export const buffs = [
  {
    src: "Elemental Skill",
    desc: ({ char, party }) => (
      <>
        Arrows fired by Yoimiya's <LGreen>Normal Attack</LGreen> will be Blazing
        Arrows, and their <LGreen>DMG</LGreen> will be increased by{" "}
        <Green>{buffs[0].bnValue(char, party)}%</Green> and converted to{" "}
        <ColoredSpan color="Pyro">Pyro DMG</ColoredSpan>.
      </>
    ),
    isGranted: () => true,
    affect: "self",
    addBnes: ({ bonuses, char, party }) => {
      bonuses.NA.DMG += buffs[0].bnValue(char, party);
    },
    bnValue: (char, party) => {
      const level =
        char["Elemental Skill"] + getXtraTalentLv(actvTalents[1], char, party);
      return Math.round(37.91 * TalentLvMults[5][level] * 100) / 100;
    },
    canInfuse: () => true,
    infuseRange: ["NA"],
    canBeOverrided: false
  },
  {
    src: "Ascension 1 Passive Talent",
    desc: () => (
      <>
        During Niwabi Fire-Dance, shots from Yoimiya's Normal Attack will
        increase her <LGreen>Pyro DMG Bonus</LGreen> by <Green>2%</Green> on
        hit. This effect lasts for 3s and can have a maximum of{" "}
        <Green>10</Green> stacks.
      </>
    ),
    isGranted: (char) => char.ascension >= 1,
    affect: "self",
    selfLabels: ["Stacks"],
    inputs: [1],
    inputTypes: ["select"],
    maxs: [10],
    addBnes: ({ ATTRs, inputs }) => {
      ATTRs["Pyro DMG Bonus"] += 2 * inputs[0];
    }
  },
  {
    src: "Ascension 4 Passive Talent",
    desc: () => (
      <>
        Using Ryuukin Saxifrage causes nearby party members to gain a{" "}
        <Green>10%</Green> <LGreen>ATK</LGreen> increase for 15s. Additionally,
        a further ATK Bonus will be added on based on the number of "Tricks of
        the Trouble-Maker" stacks Yoimiya possesses when using Ryuukin
        Saxifrage. Each stack increases this <LGreen>ATK Bonus</LGreen> by{" "}
        <Green>1%</Green>.
      </>
    ),
    isGranted: (char) => char.ascension >= 4,
    affect: "teammates",
    labels: ["Stacks"],
    inputs: [1],
    inputTypes: ["select"],
    maxs: [10],
    addBnes: ({ ATTRs, inputs }) => {
      ATTRs["ATK%"] += 10 + inputs[0];
    }
  },
  {
    src: "Constellation 1",
    desc: () => (
      <>
        When an opponent affected by Aurous Blaze is defeated within its
        duration, Yoimiya's <LGreen>ATK</LGreen> is increased by{" "}
        <Green>20%</Green> for 20s.
      </>
    ),
    isGranted: (char) => char.constellation >= 1,
    affect: "self",
    addBnes: ({ ATTRs }) => {
      ATTRs["ATK%"] += 20;
    }
  },
  {
    src: "Constellation 2",
    desc: () => (
      <>
        When Yoimiya's <ColoredSpan color="Pyro">Pyro DMG</ColoredSpan> scores a
        CRIT Hit, she will gain a <Green>25%</Green>{" "}
        <LGreen>Pyro DMG Bonus</LGreen> for 6s. (Can be triggered off-field.)
      </>
    ),
    isGranted: (char) => char.constellation >= 2,
    affect: "self",
    addBnes: ({ ATTRs }) => {
      ATTRs["Pyro DMG Bonus"] += 25;
    }
  }
];
