import { useState, useContext } from "react";
import { MyCharsCt, MyWpsCt, MyArtsCt, MyStsCt } from "../../App";
import { ModalCarpet } from "../../styledCpns/Layout";
import { Text } from "../../styledCpns/DataDisplay";
import { Confirmation } from "../CommonCpns";
import { NavBtn } from "../NavBar";
import { StyledInner, CloseBtn, Divider } from "./styles";

export default function Download(props) {
  const [optionsOn, setOptionsOn] = useState(false);
  return (
    <div>
      <NavBtn
        className={props.padding + (props.border ? " border" : "") + " pointer"}
        onClick={() => setOptionsOn(true)}
      >
        <i className="fas fa-download mr-2" />
        <Text fontWeight={700}>Download</Text>
      </NavBtn>
      {optionsOn && <Options close={() => setOptionsOn(false)} />}
    </div>
  );
}

function Options(props) {
  const { close } = props;
  const [msType, setMsType] = useState(null);
  const data = JSON.stringify({
    Characters: useContext(MyCharsCt),
    Weapons: useContext(MyWpsCt),
    Artifacts: useContext(MyArtsCt),
    Setups: useContext(MyStsCt)
  });

  const download = () => {
    const textBlob = new Blob([data], { type: "text/plain" });
    let newLink = document.createElement("a");
    newLink.download = "GDC_Data";
    if (window.webkitURL != null) {
      newLink.href = window.webkitURL.createObjectURL(textBlob);
    } else {
      newLink.href = window.URL.createObjectURL(textBlob);
      newLink.style.display = "none";
      document.body.appendChild(newLink);
    }
    newLink.click();
  };

  const saveToLocalStorage = () => {
    localStorage.setItem("GDC_Data", data);
    setMsType("success");
  };

  const trySaveToLocalStorage = () => {
    if (localStorage.getItem("GDC_Data")) {
      setMsType("warning");
    } else {
      saveToLocalStorage();
    }
  };

  return (
    <ModalCarpet>
      <StyledInner className="pt-2 pb-3">
        <CloseBtn onClick={close} />
        <div className="option" onClick={trySaveToLocalStorage}>
          <Text variant="h6" fontWeight={700}>
            Save to Local Storage
          </Text>
          <Text>
            • Your Database stays in this Browser only and will be removed if
            you clear the Local Storage.
          </Text>
        </div>
        <Divider />
        <div className="option" onClick={download}>
          <Text variant="h6" fontWeight={700}>
            Download a .TXT file
          </Text>
          <Text>
            • Please DO NOT modify this file if you don't understand how it
            works.
          </Text>
        </div>
      </StyledInner>
      {Boolean(msType) && (
        <Confirmation
          message={
            msType === "success"
              ? "Successfully save to Local Storage!"
              : "We detect old data in your Local Storage. Do you want to replace it?"
          }
          confirm={() => {
            if (msType === "success") setMsType(null);
            else saveToLocalStorage();
          }}
          close={() => setMsType(null)}
        />
      )}
    </ModalCarpet>
  );
}
