import WeaponCard from "./WeaponCard";
import { SetDBCt } from "../../App";
import { useContext } from "react";
import { deepCopy } from "../../helpers/misc";

export default function MyWeaponCard({ weapon, mutable }) {
  const { setMyWps } = useContext(SetDBCt);
  return (
    <WeaponCard
      weapon={weapon}
      mutable={mutable}
      upgrade={(level) => {
        setMyWps((prev) => {
          const newWps = deepCopy(prev);
          newWps[
            newWps.findIndex((item) => item.ID === weapon.ID)
          ].level = level;
          return newWps;
        });
      }}
      refine={(rfm) => {
        setMyWps((prev) => {
          const newWps = deepCopy(prev);
          newWps[
            newWps.findIndex((item) => item.ID === weapon.ID)
          ].refinement = +rfm;
          return newWps;
        });
      }}
    />
  );
}
