import styled from "styled-components";
import { colorCodes } from "../data/misc";

// Characters > sections | Weapons, Artifacts > RightSection
// Setups > Setup > index | Readers > WpInventory, ArtInventory
export const Card = styled.div`
  padding: 1rem;
  border-radius: 0.5rem;
  background-color: ${colorCodes.cardBg1};
`;

// Artifacts, Weapons, Setups > index
export const StyledInv = styled.div`
  position: relative;
  width: calc(100% - 3rem);
  .btn_bar {
    position: absolute;
    bottom: calc(100% + 1rem);
    left: 0;
    padding-left: 0.5rem;
    display: flex;
    align-items: center;
  }
  .body {
    padding-bottom: 1.5rem;
    display: flex;
    overflow: auto;
  }
  @media (min-width: 1050px) {
    width: 1020px;
  }
`;

// Artifacts, Weapons > LeftSection
export const StyledRack = styled.div`
  width: 43rem;
  min-width: 20.75rem;
  height: 33rem;
  padding-right: 1rem;
  .cell {
    flex-grow: 0;
    max-width: 33.33%;
    flex-basis: 33.33%;
    padding: 0.5rem;
    @media (min-width: 768px) {
      max-width: 25%;
      flex-basis: 25%;
    }
    @media (min-width: 896px) {
      max-width: 20%;
      flex-basis: 20%;
    }
    @media (min-width: 992px) {
      max-width: 16.66%;
      flex-basis: 16.66%;
    }
  }
`;

// Readers > ArtInventory, WpInventory | Pickers > index
export const StyledHeading = styled.div`
  position: relative;
  height: 100%;
  padding-left: 1rem;
  display: flex;
  align-items: center;
  border-radius: 0.5rem 0.5rem 0 0;
  background-color: ${colorCodes.orange};
  .heading_text {
    width: 100%;
    font-size: 1.375rem;
    font-weight: 700;
    line-height: 1.235;
    color: ${colorCodes.black};
    text-align: center;
  }
  .close_btn {
    position: absolute;
    top: 0.5rem;
    right: 0.5rem;
    background-color: ${colorCodes.black};
  }
  .filter_btn {
    position: absolute;
    top: 50%;
    left: 20px;
    width: 1.75rem;
    height: 1.75rem;
    margin-top: -0.825rem;
    font-size: 0.825rem;
    background-color: ${colorCodes.black};
    color: ${colorCodes.white};
    &.on {
      color: ${colorCodes.lightGold};
    }
  }
`;

// Readers > ArtInventory, WpInventory
export const Cell = styled.div`
  position: relative;
  flex-grow: 0;
  max-width: 33.33%;
  flex-basis: 33.33%;
  padding: 0.5rem;
  @media (min-width: 769px) {
    max-width: 25%;
    flex-basis: 25%;
  }
  @media (min-width: 1025px) {
    max-width: 16.66%;
    flex-basis: 16.66%;
  }
`;
