import { Green, LGreen } from "../../styledCpns/DataDisplay";

const debuffsFrArts = {
  "Viridescent Venerer": [
    {
      desc: (
        <>
          Decreases opponent's <LGreen>Elemental RES</LGreen> to the element
          infused in the Swirl by <Green>40%</Green> for 10s.
        </>
      ),
      labels: ["Element swirled"],
      inputTypes: ["swirl"],
      addPntes: ({ rdMult, inputs }) => {
        rdMult[inputs[0] + " RES"] -= 40;
      }
    }
  ]
};

export default debuffsFrArts;
