import styled from "styled-components";
import { Text, Img, ColoredSpan } from "../../../styledCpns/DataDisplay";
import { Box } from "../../../styledCpns/Layout";
import { IconBtn } from "../../../styledCpns/Inputs";
import { TeammateIcon } from "../../Calculator/SetupManager/Teammates";
import { wikia, colorCodes } from "../../../data/misc";
import { getXtraTalentLv } from "../../../calculators/helpers";

const IconWrapper = styled.div`
  position: relative;
  width: 5rem;
  height: 5rem;
  border-radius: 50%;
  &.clickable {
    box-shadow: 0 0 3px 3px ${colorCodes.lightGold};
    cursor: pointer;
  }
  :not(:last-child) {
    margin-right: 1rem;
  }
  .calc-btn {
    position: absolute;
    bottom: -0.25rem;
    right: -0.25rem;
    display: none;
  }
  :hover .calc-btn {
    display: block;
  }
`;

export default function Members(props) {
  const { char, charData, party } = props;
  const talents = charData.actvTalents.map(
    (talent) => char[talent.type] + getXtraTalentLv(talent, char, party)
  );

  const Span = (props) => (
    <ColoredSpan fontWeight={700} color={charData.vision} {...props} />
  );

  return (
    <div>
      <div className="flex justify-center">
        <Img
          width="6rem"
          src={wikia + charData.icon + ".png"}
          alt={char.name}
        />
        <div className="ml-2 pt-2 flex-col justify-between">
          <Text variant="h5">
            Level <Span>{char.level}</Span>
          </Text>
          <Text variant="h6">
            Constellation <Span>{char.constellation}</Span>
          </Text>
          <Text variant="h6">
            Talents: <Span>{talents[0]}</Span> / <Span>{talents[1]}</Span> /{" "}
            <Span>{talents[2]}</Span>
          </Text>
        </div>
      </div>
      <Box className="mt-4 flex" width="17rem">
        {party.map((tm, tmI) => {
          const memberID = props.allIDs?.[tm.name];
          const isClickable = !props.isOg && memberID;
          return tm.name ? (
            <IconWrapper key={tmI} className={isClickable ? "clickable" : ""}>
              <TeammateIcon
                mutable={false}
                name={tm.name}
                handleClick={() => {
                  if (isClickable) props.switchMember(memberID);
                }}
              />
              {!isClickable && (
                <div className="calc-btn">
                  <IconBtn
                    icon="fas fa-calculator"
                    variant="positive"
                    onClick={() => props.buildTmSt(tmI)}
                  />
                </div>
              )}
            </IconWrapper>
          ) : null;
        })}
      </Box>
    </div>
  );
}
