import { useState, useContext, useMemo } from "react";
import { MyArtsCt, SetDBCt } from "../../App";
import { Box } from "../../styledCpns/Layout";
import { StyledInv } from "../../styledCpns/Database";
import PrePicker from "../Picker/PrePicker";
import ArtPicker from "../Picker/ArtPicker";
import BtnBar from "./BtnBar";
import LeftSection from "./LeftSection";
import RightSection from "./RightSection";
import { artMS } from "../../data/artifacts/config";
import { artifactIcons } from "../../data/misc";
import { deepCopy, initArtStatFilter } from "../../helpers/misc";
import useTypeFilter from "../../helpers/hooks/useTypeFilter";

export default function MyArtifacts() {
  const [prePickerOn, setPrePickerOn] = useState(false);
  const [artType, setArtType] = useState(null);
  const [chosenI, setChosenI] = useState(0);
  const myArts = useContext(MyArtsCt);
  const { setMyArts } = useContext(SetDBCt);

  const [types, typeFilter, setTypes] = useTypeFilter(false);
  const [sets, setSets] = useState([]);
  const [stats, setStats] = useState(initArtStatFilter());
  const arts = useMemo(() => getArts(myArts, types, sets, stats), [
    myArts,
    types,
    sets,
    stats
  ]);

  return (
    <Box className="pt-5 full-h flex-center" bgColor="cardBg2">
      <StyledInv>
        <BtnBar
          add={() => setPrePickerOn(true)}
          filter={{ types, setTypes, sets, setSets, stats, setStats }}
          typeFilter={typeFilter}
        />
        <div className="body">
          <LeftSection arts={arts} chosenI={chosenI} setChosenI={setChosenI} />
          <RightSection art={arts[chosenI]} />
        </div>
      </StyledInv>
      {prePickerOn && (
        <PrePicker
          close={() => setPrePickerOn(false)}
          choices={artifactIcons}
          pick={(artType) => {
            setArtType(artType);
            setPrePickerOn(false);
          }}
        />
      )}
      {artType && (
        <ArtPicker
          needMassAdd={true}
          artType={artType}
          close={() => setArtType(null)}
          pick={(newItem) => {
            setMyArts((prev) => {
              const newArts = deepCopy(prev);
              newArts.unshift({ ID: Date.now(), user: null, ...newItem });
              return newArts;
            });
          }}
        />
      )}
    </Box>
  );
}

// also used in ../Readers/ArtInventory
export function getArts(myArts, types, sets, stats) {
  let result = types.length
    ? myArts.filter((art) => types.includes(art.type))
    : myArts;
  if (sets.length) {
    result = result.filter((art) => sets.includes(art.set));
  }
  if (stats.main !== "All") {
    result = result.filter((art) => art.mainSType === stats.main);
    if (stats.subs[0] === "All") {
      result.sort((a, b) => compareMainStat(a, b));
    }
  }
  if (stats.subs[0] !== "All") {
    const requires = stats.subs.filter((s) => s !== "All");
    result = result.filter((art) =>
      requires.every((rq) => art.subS.map((ss) => ss.type).includes(rq))
    );
    result.sort((a, b) => {
      if (stats.main !== "All") {
        const msResult = compareMainStat(a, b);
        if (msResult) {
          return msResult;
        }
      }
      for (let rq of requires) {
        const ssResult = getVal(b, rq) - getVal(a, rq);
        if (ssResult) {
          return ssResult;
        }
      }
      return false;
    });
  }
  return result;
}

function compareMainStat(a, b) {
  return (
    artMS[b.type][b.mainSType][b.level] - artMS[a.type][a.mainSType][a.level]
  );
}

function getVal(art, type) {
  return art.subS.find((s) => s.type === type).val;
}
