import data from "./index";

export const buffs = [
  {
    src: "Constellation 2",
    desc: () => data.constellation[1].desc,
    isGranted: (char) => char.constellation >= 2,
    affect: "self",
    addBnes: ({ bonuses }) => {
      bonuses.NA.DMG += 15;
      bonuses.CA.DMG += 15;
    }
  }
];
