import { mediumPAs, TalentLvMults } from "../../config";
import { buffs } from "./modifiers";
import { getXtraTalentLv } from "../../../../calculators/helpers";

const actvTalents = [
  {
    type: "Normal Attack",
    name: "Guhua Style",
    image: "3/31/Talent_Guhua_Style",
    stats: [
      {
        name: "1-Hit",
        dmgTypes: ["NA", "Physical"],
        baseMult: 46.61,
        multType: 1
      },
      {
        name: "2-Hit",
        dmgTypes: ["NA", "Physical"],
        baseMult: 47.64,
        multType: 1
      },
      {
        name: "3-Hit (x2)",
        dmgTypes: ["NA", "Physical"],
        baseMult: 28.55,
        multType: 1
      },
      {
        name: "4-Hit",
        dmgTypes: ["NA", "Physical"],
        baseMult: 55.99,
        multType: 1
      },
      {
        name: "5-Hit (x2)",
        dmgTypes: ["NA", "Physical"],
        baseMult: 35.86,
        multType: 1
      },
      {
        name: "Charged Attack",
        dmgTypes: ["CA", "Physical"],
        baseMult: [47.3, 56.16],
        multType: 1
      },
      ...mediumPAs
    ]
  },
  {
    type: "Elemental Skill",
    name: "Guhua Sword: Fatal Rainscreen",
    image: "5/5d/Talent_Guhua_Sword_Fatal_Rainscreen",
    getXtraLv: (char) => (char.constellation >= 5 ? 3 : 0),
    stats: [
      {
        name: "Skill DMG",
        dmgTypes: ["ES", "Elemental"],
        baseMult: [168, 191.2],
        multType: 2,
        getTlBnes: ({ char, selfMCs, party }) => {
          const level =
            char["Elemental Skill"] +
            getXtraTalentLv(actvTalents[1], char, party);
          return {
            xtraMult:
              buffs[1].isGranted(char) && selfMCs.BCs[1].activated
                ? [84, 95.6].map((n) => n * TalentLvMults[2][level])
                : 0
          };
        }
      }
    ]
  },
  {
    type: "Elemental Burst",
    name: "Guhua Sword: Raincutter",
    image: "2/23/Talent_Guhua_Sword_Raincutter",
    getXtraLv: (char) => (char.constellation >= 3 ? 3 : 0),
    stats: [
      {
        name: "Sword Rain",
        dmgTypes: ["EB", "Elemental"],
        baseMult: 54.27,
        multType: 2
      }
    ],
    energyCost: 80
  }
];

export default actvTalents;
