import "./styles.css";
import "./custom.css";
import { useState, useEffect, createContext } from "react";
import { AbsoluteBox } from "./styledCpns/Layout";
import NavBar from "./components/NavBar";
import Intro from "./components/Intro";
import MyCharacters from "./components/MyCharacters";
import MyWeapons from "./components/MyWeapons";
import MyArtifacts from "./components/MyArtifacts";
import MySetups from "./components/MySetups";
import Calculator from "./components/Calculator";
import { adjustDB } from "./helpers/processors";

export const SetDBCt = createContext();
export const MyCharsCt = createContext();
export const MyWpsCt = createContext();
export const MyArtsCt = createContext();
export const MyStsCt = createContext();

export default function App() {
  const [tab, setTab] = useState("Calculator");
  const [introOn, setIntroOn] = useState(true);
  const [myChars, setMyChars] = useState([]);
  const [myWeapons, setMyWps] = useState([]);
  const [myArts, setMyArts] = useState([]);
  const [mySetups, setMySts] = useState([]);
  const [chosenStID, setChosenStID] = useState(0);
  const [importedSt, setImportedSt] = useState(null);
  const [caclChar, setCalcChar] = useState(null);

  const loadData = (data) => {
    const setups = data.Setups || [];
    // if (!data.version) adjustDB(setups);
    setMyChars(data.Characters);
    setMyWps(data.Weapons);
    setMyArts(data.Artifacts);
    setMySts(setups);
  };
  useEffect(() => {
    const data = localStorage.getItem("GDC_Data");
    if (data) loadData(JSON.parse(data));
  }, []);

  return (
    <div className="App flex-col">
      <SetDBCt.Provider
        value={{
          setMyChars,
          setMyWps,
          setMyArts,
          setMySts,
          setImportedSt,
          setTab, // for Caclculator/SetupManager/Settings/ExtraCtrls
          setChosenStID // "" & MySetups/...
        }}
      >
        <MyCharsCt.Provider value={myChars}>
          <MyWpsCt.Provider value={myWeapons}>
            <MyArtsCt.Provider value={myArts}>
              <MyStsCt.Provider value={mySetups}>
                <NavBar
                  current={tab}
                  setIntroOn={setIntroOn}
                  loadData={loadData}
                />
                <div className="grow-1 flex-center relative">
                  <Calculator
                    char={caclChar}
                    setChar={setCalcChar}
                    importedSt={importedSt}
                    toCalc={() => {
                      setTab("Calculator");
                      setImportedSt(null);
                    }}
                  />
                  {tab !== "Calculator" && (
                    <AbsoluteBox
                      top={0}
                      left={0}
                      zIndex={2}
                      className="full-w full-h"
                    >
                      {
                        {
                          Characters: myChars ? <MyCharacters /> : null,
                          Weapons: myWeapons ? <MyWeapons /> : null,
                          Artifacts: myArts ? <MyArtifacts /> : null,
                          Setups: mySetups ? (
                            <MySetups chosenStID={chosenStID} />
                          ) : null
                        }[tab]
                      }
                    </AbsoluteBox>
                  )}
                  {introOn && <Intro close={() => setIntroOn(false)} />}
                </div>
              </MyStsCt.Provider>
            </MyArtsCt.Provider>
          </MyWpsCt.Provider>
        </MyCharsCt.Provider>
      </SetDBCt.Provider>
    </div>
  );
}
