import { LGreen, Green, Red } from "../../../styledCpns/DataDisplay";
import { baseAtkType, subStatsType } from "../config";
import { LiyueSeries } from "../series";
import { incAllElmtDmgBnes } from "../../../helpers/calculators";

const GoldPolearms = {
  "Skyward Spine": {
    code: 77,
    icon: "6/69/Weapon_Skyward_Spine",
    rarity: "Gold",
    mainStat: {
      type: "Base ATK",
      value: baseAtkType[48]
    },
    subStats: [
      {
        type: "Energy Recharge",
        value: subStatsType["8%"]
      }
    ],
    addBnes: ({ ATTRs, wpRfm }) => {
      ATTRs["CRIT Rate"] += 6 + wpRfm * 2;
      ATTRs["Normal ATK SPD"] += 12;
    },
    pasvProcD: [
      {
        name: "Vacuum Blade",
        baseSType: "ATK",
        multiplier: [0, 0.4, 0.55, 0.7, 0.85, 1],
        dmgType: "Physical"
      }
    ],
    pasvName: "Blackwing",
    pasvDescs: ({ wpRfm }) => [
      {
        bonus: (
          <>
            Increases <LGreen>CRIT Rate</LGreen> by{" "}
            <Green>{6 + wpRfm * 2}%</Green> and increases{" "}
            <LGreen>Normal ATK SPD</LGreen> by <Green>12%</Green>. Additionally,
            Normal and Charged Attacks hits on opponents have a{" "}
            <LGreen>50% chance</LGreen> to trigger a vacuum blade that deals{" "}
            <Green>{25 + wpRfm * 15}%</Green> of <LGreen>ATK</LGreen> as DMG in
            a small AoE. This effect can occur no more than once every 2s.
          </>
        )
      }
    ]
  },
  "Primordial Jade Winged-Spear": {
    code: 78,
    icon: "8/80/Weapon_Primordial_Jade_Winged-Spear",
    rarity: "Gold",
    mainStat: {
      type: "Base ATK",
      value: baseAtkType[48]
    },
    subStats: [
      {
        type: "CRIT Rate",
        value: subStatsType["4.8%"]
      }
    ],
    buffs: [
      {
        affect: "self",
        labels: ["Stacks"],
        inputTypes: ["stacks"],
        values: [7],
        addBnes: ({ ATTRs, bonuses, wpRfm, inputs }) => {
          ATTRs["ATK%"] += (2.5 + wpRfm * 0.7) * inputs[0];
          if (inputs[0] === 7) bonuses.AllDmg += 9 + wpRfm * 3;
        },
        desc: ({ wpRfm }) =>
          GoldPolearms["Primordial Jade Winged-Spear"].pasvDescs({ wpRfm })[0]
            .buff
      }
    ],
    pasvName: "Eagle Spear of Justice",
    pasvDescs: ({ wpRfm }) => [
      {
        buff: (
          <>
            On hit, increases <LGreen>ATK</LGreen> by{" "}
            <Green>{2.5 + wpRfm * 0.7}%</Green> for 6s. Max <Green>7</Green>{" "}
            stacks. This effect can only occur once every 0.3s. While in
            possession of the maximum possible stacks, <LGreen>DMG</LGreen>{" "}
            dealt is increased by <Green>{9 + wpRfm * 3}%</Green>.
          </>
        )
      }
    ]
  },
  "Engulfing Lightning": {
    code: 79,
    icon: "2/21/Weapon_Engulfing_Lightning",
    rarity: "Gold",
    mainStat: {
      type: "Base ATK",
      value: baseAtkType[46]
    },
    subStats: [
      {
        type: "Energy Recharge",
        value: subStatsType["12%"]
      }
    ],
    addFinalBnes: ({ ATTRs, wpRfm }) => {
      let atkBn = (ATTRs["Energy Recharge"] / 100 - 1) * (21 + wpRfm * 7);
      atkBn = Math.min(Math.round(atkBn * 100) / 100, 70 + wpRfm * 10);
      ATTRs.ATK += Math.round((ATTRs["Base ATK"] * atkBn) / 100);
    },
    buffs: [
      {
        affect: "self",
        addBnes: ({ ATTRs, wpRfm }) => {
          ATTRs["Energy Recharge"] += 25 + wpRfm * 5;
        },
        desc: ({ wpRfm }) =>
          GoldPolearms["Engulfing Lightning"].pasvDescs({ wpRfm })[0].buff
      }
    ],
    pasvName: "Timeless Dream: Eternal Stove",
    pasvDescs: ({ wpRfm }) => [
      {
        bonus: (
          <>
            <LGreen>ATK</LGreen> increased by <Green>{21 + wpRfm * 7}%</Green>{" "}
            of <LGreen>Energy Recharge</LGreen> over the base 100%. You can gain
            a maximum bonus of {70 + wpRfm * 10}% ATK.
          </>
        ),
        buff: (
          <>
            Gain <Green>{25 + wpRfm * 5}%</Green>{" "}
            <LGreen>Energy Recharge</LGreen> for 12s after using an Elemental
            Burst.
          </>
        )
      }
    ]
  },
  "Staff of Homa": {
    code: 80,
    icon: "1/17/Weapon_Staff_of_Homa",
    rarity: "Gold",
    mainStat: {
      type: "Base ATK",
      value: baseAtkType[46]
    },
    subStats: [
      {
        type: "CRIT DMG",
        value: subStatsType["14.4%"]
      }
    ],
    addBnes: ({ ATTRs, wpRfm }) => {
      ATTRs["HP%"] += 15 + wpRfm * 5;
    },
    addFinalBnes: ({ ATTRs, wpRfm }) => {
      ATTRs.ATK += Math.round((ATTRs.HP * (6 + wpRfm * 2)) / 1000);
    },
    buffs: [
      {
        affect: "self",
        addFinalBnes: ({ ATTRs, wpRfm }) => {
          const { bnValue } = GoldPolearms["Staff of Homa"].buffs[0];
          ATTRs.ATK += bnValue(ATTRs, wpRfm);
        },
        bnValue: (ATTRs, wpRfm) =>
          Math.round((ATTRs.HP * (8 + wpRfm * 2)) / 1000),
        desc: ({ wpRfm, ATTRs }) => {
          const { buffs, pasvDescs } = GoldPolearms["Staff of Homa"];
          const value = buffs[0].bnValue(ATTRs, wpRfm);
          return (
            <>
              {pasvDescs({ wpRfm })[0].buff} <Red>ATK Bonus: {value}.</Red>
            </>
          );
        }
      }
    ],
    pasvName: "Reckless Cinnabar",
    pasvDescs: ({ wpRfm }) => [
      {
        bonus: (
          <>
            <LGreen>HP</LGreen> increased by <Green>{15 + wpRfm * 5}%</Green>.
            Additionally, provides an <LGreen>ATK Bonus</LGreen> based on{" "}
            <Green>{Math.round(6 + wpRfm * 2) / 10}%</Green> of the wielder's{" "}
            <LGreen>Max HP</LGreen>.
          </>
        ),
        buff: (
          <>
            When the wielder's HP is less than 50%, this{" "}
            <LGreen>ATK Bonus</LGreen> is increased by an additional{" "}
            <Green>{Math.round(8 + wpRfm * 2) / 10}%</Green> of{" "}
            <LGreen>Max HP</LGreen>.
          </>
        )
      }
    ]
  },
  "Vortex Vanquisher": {
    code: 81,
    icon: "d/d6/Weapon_Vortex_Vanquisher",
    rarity: "Gold",
    mainStat: {
      type: "Base ATK",
      value: baseAtkType[46]
    },
    subStats: [
      {
        type: "ATK%",
        value: subStatsType["10.8%"]
      }
    ],
    ...LiyueSeries
  },
  "Calamity Queller": {
    code: 82,
    icon: "8/8b/Weapon_Calamity_Queller",
    rarity: "Gold",
    mainStat: {
      type: "Base ATK",
      value: baseAtkType[49]
    },
    subStats: [
      {
        type: "ATK%",
        value: subStatsType["3.6%"]
      }
    ],
    addBnes: ({ ATTRs, wpRfm }) => {
      incAllElmtDmgBnes(ATTRs, 9 + wpRfm * 3);
    },
    buffs: [
      {
        affect: "self",
        labels: ["Stacks", "Not on the field"],
        inputTypes: ["stacks", "check"],
        values: [6],
        addBnes: ({ ATTRs, wpRfm, inputs }) => {
          ATTRs["ATK%"] +=
            (2.4 + wpRfm * 0.8) * inputs[0] * (inputs[1] ? 2 : 1);
        },
        desc: ({ wpRfm }) =>
          GoldPolearms["Calamity Queller"].pasvDescs({ wpRfm })[0].buff
      }
    ],
    pasvName: "Eagle Spear of Justice",
    pasvDescs: ({ wpRfm }) => [
      {
        bonus: (
          <>
            Gain <Green>{9 + wpRfm * 3}%</Green>{" "}
            <LGreen>All Elemental DMG Bonus</LGreen>.
          </>
        ),
        buff: (
          <>
            Obtain Consummation for 20s after using an Elemental Skill, causing{" "}
            <LGreen>ATK</LGreen> to increase by{" "}
            <Green>{(24 + wpRfm * 8) / 10}%</Green> per second. This ATK
            increase can stack <Green>6</Green> times. When the character
            equipped with this weapon is not on the field, Consummation's ATK
            increase is <LGreen>doubled</LGreen>.
          </>
        )
      }
    ]
  }
};

export default GoldPolearms;
