import { useState } from "react";
import { Img } from "../../styledCpns/DataDisplay";
import { Box } from "../../styledCpns/Layout";
import { ButtonBar } from "../../components/CommonCpns";
import { deepCopy } from "../misc";
import { wikia, colorCodes } from "../../data/misc";
import artifacts from "../../data/artifacts";

export default function useArtSetFilter(arts, sets, artType) {
  const [tempSets, setTempSets] = useState(getTempSets(arts, sets));
  const setsFilter = (
    <Box
      className="flex-col"
      borderRadius="0.5rem"
      bgColor="cardBg2"
      width="18rem"
      minWidth="18rem"
    >
      <ButtonBar
        className="mt-2"
        texts={["Turn All Off"]}
        availables={[tempSets.some((set) => set.chosen)]}
        handlers={[
          () => {
            if (tempSets.some((set) => set.chosen))
              setTempSets(tempSets.map((set) => ({ ...set, chosen: false })));
          }
        ]}
      />
      <Box className="mt-2 p-1 hide-sb" height="24rem">
        <div className="flex-wrap">
          {tempSets.map((set, i) => (
            <Box
              key={i}
              className="p-1"
              width="25%"
              onClick={() =>
                setTempSets((prev) => {
                  const result = deepCopy(prev);
                  result[i].chosen = !set.chosen;
                  return result;
                })
              }
            >
              <Box
                className="p-1"
                borderRadius="50%"
                boxShadow={
                  set.chosen ? "0 0 3px 2px " + colorCodes.green : "none"
                }
                bgColor={set.chosen ? "cardBg1" : "transparent"}
              >
                <Img
                  src={
                    wikia +
                    artifacts[set.name][artType || "flower"].icon +
                    ".png"
                  }
                  alt=""
                  width="100%"
                  draggable={false}
                />
              </Box>
            </Box>
          ))}
        </div>
      </Box>
    </Box>
  );
  return [
    tempSets.filter((set) => set.chosen).map((set) => set.name),
    setsFilter,
    setTempSets
  ];
}

function getTempSets(arts, sets) {
  const result = [];
  for (let { set } of arts) {
    if (!result.find(({ name }) => name === set)) {
      result.push({ name: set, chosen: sets.includes(set) });
    }
  }
  return result;
}
