import { Green, LGreen } from "../../styledCpns/DataDisplay";
import Mondstadt from "./Mondstadt";
import Liyue from "./Liyue";
import Inazuma from "./Inazuma";

const artifactBuffs = {
  "Noblesse Oblige": [
    {
      desc: Liyue["Noblesse Oblige"].setBnes[1].desc,
      affect: "party",
      addBnes: ({ ATTRs }) => {
        ATTRs["ATK%"] += 20;
      }
    }
  ],
  "Bloodstained Chivalry": [
    {
      desc: Liyue["Bloodstained Chivalry"].setBnes[1].desc,
      affect: "self",
      addBnes: ({ bonuses }) => {
        bonuses.CA.DMG += 50;
      }
    }
  ],
  Thundersoother: [
    {
      desc: Mondstadt.Thundersoother.setBnes[1].desc,
      affect: "self",
      addBnes: ({ bonuses }) => {
        bonuses.AllDmg += 35;
      }
    }
  ],
  "Crimson Witch of Flame": [
    {
      desc: Liyue["Crimson Witch of Flame"].setBnes[1].desc,
      affect: "self",
      labels: ["Stacks"],
      inputTypes: ["stacks"],
      maxs: [3],
      addBnes: ({ ATTRs, inputs }) => {
        ATTRs["Pyro DMG Bonus"] += 7.5 * inputs[0];
      }
    }
  ],
  Lavawalker: [
    {
      desc: Liyue.Lavawalker.setBnes[1].desc,
      affect: "self",
      addBnes: ({ bonuses }) => {
        bonuses.AllDmg += 35;
      }
    }
  ],
  "Archaic Petra": [
    {
      desc: Liyue["Archaic Petra"].setBnes[1].desc,
      affect: "party",
      labels: ["Element"],
      inputTypes: ["crystallize"],
      addBnes: ({ ATTRs, inputs }) => {
        ATTRs[inputs[0] + " DMG Bonus"] += 35;
      }
    }
  ],
  "Retracing Bolide": [
    {
      desc: Liyue["Retracing Bolide"].setBnes[1].desc,
      affect: "self",
      addBnes: ({ bonuses }) => {
        bonuses.NA.DMG += 40;
        bonuses.CA.DMG += 40;
      }
    }
  ],
  "Blizzard Strayer": [
    {
      desc: (
        <>
          When a character attacks an opponent affected by Cryo, their{" "}
          <LGreen>CRIT Rate</LGreen> is increased by <Green>20%</Green>.
        </>
      ),
      affect: "self",
      addBnes: ({ ATTRs }) => {
        ATTRs["CRIT Rate"] += 20;
      }
    },
    {
      desc: (
        <>
          If the opponent is Frozen, <LGreen>CRIT Rate</LGreen> is increased by
          an additional <Green>20%</Green>.
        </>
      ),
      affect: "self",
      addBnes: ({ ATTRs }) => {
        ATTRs["CRIT Rate"] += 20;
      }
    }
  ],
  "Heart of Depth": [
    {
      desc: Mondstadt["Heart of Depth"].setBnes[1].desc,
      affect: "self",
      addBnes: ({ bonuses }) => {
        bonuses.NA.DMG += 30;
        bonuses.CA.DMG += 30;
      }
    }
  ],
  "Pale Flame": [
    {
      desc: Liyue["Pale Flame"].setBnes[1].desc,
      affect: "self",
      labels: ["Stacks"],
      inputTypes: ["stacks"],
      maxs: [2],
      addBnes: ({ ATTRs, inputs }) => {
        ATTRs["ATK%"] += 9 * inputs[0];
        if (+inputs[0] === 2) ATTRs["Physical DMG Bonus"] += 25;
      }
    }
  ],
  "Tenacity of the Millelith": [
    {
      desc: Liyue["Tenacity of the Millelith"].setBnes[1].desc,
      affect: "party",
      addBnes: ({ ATTRs }) => {
        ATTRs["ATK%"] += 20;
        ATTRs["Shield Strength"] += 30;
      }
    }
  ],
  "Shimenawa's Reminiscence": [
    {
      desc: Inazuma["Shimenawa's Reminiscence"].setBnes[1].desc,
      affect: "self",
      addBnes: ({ bonuses }) => {
        bonuses.NA.DMG += 50;
        bonuses.CA.DMG += 50;
        bonuses.PA.DMG += 50;
      }
    }
  ],
  "Husk of Opulent Dreams": [
    {
      desc: (
        <>
          Curiosity can stack up to <Green>4</Green> <LGreen>times</LGreen>,
          each providing <Green>6%</Green> <LGreen>DEF</LGreen> and a{" "}
          <Green>6%</Green> <LGreen>Geo DMG Bonus</LGreen>.
        </>
      ),
      affect: "self",
      labels: ["Stacks"],
      inputTypes: ["stacks"],
      maxs: [4],
      addBnes: ({ ATTRs, inputs }) => {
        ATTRs["DEF%"] += 6 * inputs[0];
        ATTRs["Geo DMG Bonus"] += 6 * inputs[0];
      }
    }
  ]
};

export default artifactBuffs;
