import styled from "styled-components";
import { Text, Img, Green, LGreen } from "../../../styledCpns/DataDisplay";
import { Box } from "../../../styledCpns/Layout";
import { CloseBtn } from "../../CommonCpns";
import { wikia } from "../../../data/misc";
import characters from "../../../data/characters";

const Caret = ({ dir, onClick }) => (
  <p
    className={
      (dir === "right" ? "pl-3 pr-2 right" : "pl-2 pr-3 left") +
      " caret pointer"
    }
    onClick={onClick}
  >
    <i className={"fas fa-caret-" + dir} />
  </p>
);

export default function Details(props) {
  const { charName, ctrlLv, setCtrlLv } = props;
  if (!ctrlLv)
    return <div className={"details " + (ctrlLv ? "open" : "close")} />;
  const { name, desc } = characters[charName].constellation[ctrlLv - 1];
  const color = characters[charName].vision;

  return (
    <div className={"details " + (props.atDetails ? "open" : "close")}>
      <div className="full-h flex-col">
        <div className="pt-2 pb-3 flex-center">
          <div className="relative">
            <ConsIcon
              charName={charName}
              ctrlLv={ctrlLv}
              consLv={props.consLv}
            />
            {ctrlLv > 1 && (
              <Caret dir="left" onClick={() => setCtrlLv(ctrlLv - 1)} />
            )}
            {ctrlLv < 6 && (
              <Caret dir="right" onClick={() => setCtrlLv(ctrlLv + 1)} />
            )}
          </div>
        </div>
        <div className="grow-1 hide-sb">
          <div className="py-2 cons_details">
            <Text variant="h5" color={color} fontWeight={700}>
              {name}
            </Text>
            <Text variant="h6">
              Constellation Lv. <Green>{ctrlLv}</Green>
            </Text>
            <Text className="mt-3">
              {desc ? (
                ctrlLv === 3 || ctrlLv === 5 ? (
                  <>
                    Increases the Level of <LGreen>{desc}</LGreen> by{" "}
                    <Green>3</Green>.<br /> Maximum upgrade level is 15.
                  </>
                ) : (
                  desc
                )
              ) : (
                <>To be added...</>
              )}
            </Text>
          </div>
        </div>
        <div className="mt-3">
          <CloseBtn className="mx-auto glow-on-hover" onClick={props.close} />
        </div>
      </div>
    </div>
  );
}

const SlideShow = styled.div`
  position: absolute;
  top: 0;
  left: ${(props) => `-${props.leftDist * 3.25}rem`};
  display: flex;
  transition: left 100ms linear;
`;

function ConsIcon(props) {
  const { beta, icon, constellation } = characters[props.charName];
  return (
    <Box className="overflow-hidden relative" width="3.25rem" height="3.25rem">
      <SlideShow leftDist={props.ctrlLv - 1}>
        {constellation.map((cons, i) => (
          <Img
            key={i}
            className={props.consLv >= i + 1 ? "" : " unavailable"}
            width="3.25rem"
            src={wikia + (beta ? icon : cons.image) + ".png"}
            alt=""
            draggable={false}
          />
        ))}
      </SlideShow>
    </Box>
  );
}
