import { useContext } from "react";
import { SetDBCt } from "../../App";
import ArtifactCard from "./ArtifactCard";
import { deepCopy } from "../../helpers/misc";
import { processNumInp } from "../../helpers/processors";

export default function MyArtCard(props) {
  const { art } = props;
  const { setMyArts } = useContext(SetDBCt);
  return (
    <ArtifactCard
      {...props}
      enhance={(e) =>
        setMyArts((prev) => {
          const newArts = deepCopy(prev);
          newArts[
            newArts.findIndex((item) => item.ID === art.ID)
          ].level = +e.target.value.substr(1);
          return newArts;
        })
      }
      up4={() => {
        setMyArts((prev) => {
          const newArts = deepCopy(prev);
          for (let piece of newArts) {
            if (piece.ID === art.ID) {
              piece.level = Math.min(20, piece.level + 4);
              return newArts;
            }
          }
        });
      }}
      toMax={() => {
        setMyArts((prev) => {
          const newArts = deepCopy(prev);
          newArts[newArts.findIndex((item) => item.ID === art.ID)].level = 20;
          return newArts;
        });
      }}
      changeMSType={(e) =>
        setMyArts((prev) => {
          const newArts = deepCopy(prev);
          newArts[newArts.findIndex((item) => item.ID === art.ID)].mainSType =
            e.target.value;
          return newArts;
        })
      }
      changeSSType={(value, ssIndex) => {
        setMyArts((prev) => {
          const newArts = deepCopy(prev);
          for (let piece of newArts) {
            if (piece.ID === art.ID) {
              piece.subS[ssIndex].type = value;
              return newArts;
            }
          }
        });
      }}
      changeSSVal={(value, ssIndex, oldValue) => {
        setMyArts((prev) => {
          const newArts = deepCopy(prev);
          for (let piece of newArts) {
            if (piece.ID === art.ID) {
              piece.subS[ssIndex].val = processNumInp(value, oldValue, 9999);
              return newArts;
            }
          }
        });
      }}
    />
  );
}
