import characters from "../data/characters";
import weapons from "../data/weapons";
import artifacts from "../data/artifacts";
import { allStatsTypes } from "../data/artifacts/config";
import { elmts, ampRxnTrigElmts, rsnNames } from "../data/elements";
import { levels } from "../data/misc";

export const specialCodes = {
  Pyro: "PR",
  Hydro: "HD",
  Dendro: "DD",
  Electro: "ELT",
  Anemo: "ANM",
  Cryo: "CO",
  Geo: "GO",
  Recitative: "RCTT",
  Aria: "ARA",
  Interlude: "ITL"
};

export default function encodeExportedData(data) {
  let result = "";
  const { name, level, ascension, constellation, ...talents } = data.char;
  result +=
    [
      characters[name].code,
      levels.indexOf(level),
      constellation,
      ...["Normal Attack", "Elemental Skill", "Elemental Burst"].map(
        (type) => talents[type]
      )
    ].join("&") + "?";

  const selfMCsCodes = [];
  selfMCsCodes.push(getMCsCode(data.selfMCs.BCs));
  selfMCsCodes.push(getMCsCode(data.selfMCs.DCs));
  result += selfMCsCodes.join("&") + "?";

  result += getWpCode(data.weapon) + "?";
  result += getSubWpBCsCode(data.subWpMCs.BCs) + "?";
  result += getArtCode(data.art) + "?";
  result += getPartyCode(data.party) + "?";
  result += getElmtMCsCode(data.elmtMCs) + "?";
  result += getTgCode(data.target);
  return result;
}

function getWpCode(weapon) {
  const { type, name, level, refinement, BCs } = weapon;
  const result = [
    weapons[type][name].code,
    levels.indexOf(level),
    refinement,
    getMCsCode(BCs)
  ];
  return result.join("&");
}

function getSubWpBCsCode(BCs) {
  const result = [];
  for (let type in BCs) {
    for (let ctrl of BCs[type]) {
      if (ctrl.activated) {
        // currently ctrl.index is not needed, it's always 0
        result.push(
          weapons[type][ctrl.name].code +
            ":" +
            ctrl.refinement +
            getInputsCode(ctrl.inputs)
        );
      }
    }
  }
  return result.join("!");
}

function getArtCode(art) {
  const { pieces, BCs } = art;
  const result = [];

  const piecesCodes = [];
  for (let P of pieces) {
    if (P === null) {
      piecesCodes.push("X");
    } else {
      const infoCodes = [
        artifacts[P.set].code,
        P.level,
        allStatsTypes.indexOf(P.mainSType)
      ];
      const subSCodes = [];
      for (let ss of P.subS) {
        subSCodes.push(allStatsTypes.indexOf(ss.type) + ":" + ss.val);
      }
      infoCodes.push(subSCodes.join("/"));
      piecesCodes.push(infoCodes.join("#"));
    }
  }
  result.push(piecesCodes.join("!"));
  result.push(getMCsCode(BCs));

  for (let mcs of ["subBCs", "subDCs"]) {
    const code = [];
    for (let ctrl of art[mcs]) {
      if (ctrl.activated) {
        // currently ctrl.index is not needed, it's always 0
        code.push(artifacts[ctrl.set].code + getInputsCode(ctrl.inputs));
      }
    }
    result.push(code.join("!"));
  }

  return result.join("&");
}

function getPartyCode(party) {
  const result = [];
  for (let tm of party) {
    if (tm.name !== null) {
      result.push(
        characters[tm.name].code +
          "#" +
          getMCsCode(tm.BCs) +
          "#" +
          getMCsCode(tm.DCs)
      );
    }
  }
  return result.join("&");
}

function getElmtMCsCode(elmtMCs) {
  const { naAmpRxn, ampRxn } = elmtMCs;
  const result = [
    !naAmpRxn ? "" : Object.keys(ampRxnTrigElmts).indexOf(naAmpRxn),
    !ampRxn ? "" : Object.keys(ampRxnTrigElmts).indexOf(ampRxn),
    elmtMCs.superconduct ? "T" : "F",
    elmtMCs.resonance
      .filter((rsn) => rsn.activated)
      .map(({ name }) => Object.values(rsnNames).indexOf(name))
      .join("#")
  ];
  return result.join("&");
}

function getTgCode({ Level, ...Res }) {
  const result = [Level];
  for (let type of ["Physical", ...elmts]) {
    result.push(Res[type + " RES"]);
  }
  return result.join("&");
}

function getMCsCode(MCs) {
  return MCs.filter((ctrl) => ctrl.activated)
    .map((ctrl) => ctrl.index + getInputsCode(ctrl.inputs))
    .join("!");
}

function getInputsCode(inputs) {
  if (inputs) {
    const result = inputs
      .map((inp) => {
        if (inp === true) {
          return "T";
        } else if (inp === false) {
          return "F";
        } else if (specialCodes[inp]) {
          return specialCodes[inp];
        }
        return inp;
      })
      .join("/");
    return ":" + result;
  }
  return "";
}
