import { LGreen, Green } from "../../../../styledCpns/DataDisplay";
import data from "./index";

export const buffs = [
  {
    src: "Ascension 1 Passive Talent",
    desc: () => (
      <>
        After firing a Frostflake Arrow, the <LGreen>CRIT Rate</LGreen> of
        subsequent <LGreen>Frostflake Arrows</LGreen> and their resulting{" "}
        <LGreen>bloom effects</LGreen> is increased by <Green>20%</Green> for
        5s.
      </>
    ),
    isGranted: (char) => char.ascension >= 1,
    affect: "self"
  },
  {
    src: "Ascension 4 Passive Talent",
    desc: () => (
      <>
        Celestial Shower grants a <Green>20%</Green>{" "}
        <LGreen>Cryo DMG Bonus</LGreen> to active members in the AoE.
      </>
    ),
    isGranted: (char) => char.ascension >= 4,
    affect: "party",
    addBnes: ({ ATTRs }) => {
      ATTRs["Cryo DMG Bonus"] += 20;
    }
  },
  {
    src: "Constellation 4",
    desc: () => data.constellation[3].desc,
    isGranted: (char) => char.constellation >= 4,
    affect: "party",
    selfLabels: ["Stacks"],
    labels: ["Stacks"],
    inputs: [1],
    inputTypes: ["select"],
    maxs: [5],
    addBnes: ({ bonuses, inputs }) => {
      bonuses.AllDmg += 5 * inputs[0];
    }
  }
];

export const debuffs = [
  {
    src: "Constellation 1",
    desc: () => (
      <>
        Charge Level 2 Frostflake Arrow or Frostflake Arrow Bloom decreases
        opponents' <LGreen>Cryo RES</LGreen> by <Green>15%</Green> for 6s.
      </>
    ),
    isGranted: (char) => char.constellation >= 1,
    addPntes: ({ rdMult }) => {
      rdMult["Cryo RES"] -= 15;
    }
  }
];
