import { useContext } from "react";
import { PartiesCt, FixedCts, SetupCt } from "../../index";
import { Text } from "../../../../styledCpns/DataDisplay";
import { Section, NoContent } from "../CmnCpns";
import DebuffSetters from "./DebuffSetters";
import characters from "../../../../data/characters";
import { deepCopy } from "../../../../helpers/misc";
import { processNumInp } from "../../../../helpers/processors";

export default function PartyDebuffs({ char }) {
  const parties = useContext(PartiesCt);
  const curSetupI = useContext(SetupCt);
  const content = [];

  parties[curSetupI].forEach((teammate, tmI) => {
    if (teammate.DCs.length) {
      content.push(
        <TeammateDebuffs
          key={tmI}
          teammate={teammate}
          curSetupI={curSetupI}
          tmI={tmI}
          char={char}
        />
      );
    }
  });
  return content.length ? content : <NoContent type="debuffs" />;
}

function TeammateDebuffs(props) {
  const { setParties } = useContext(FixedCts);
  const { teammate, curSetupI, tmI } = props;
  const tmData = characters[teammate.name];
  const subContent = [];

  teammate.DCs.forEach((ctrl, dcIndex) => {
    const { activated, index, inputs } = ctrl;
    const debuff = tmData.debuffs[index];
    subContent.push(
      <Section
        key={dcIndex}
        checked={activated}
        handleCheck={() => {
          setParties((prev) => {
            const newPts = deepCopy(prev);
            newPts[curSetupI][tmI].DCs[dcIndex].activated = !activated;
            return newPts;
          });
        }}
        heading={debuff.src}
        desc={debuff.desc({
          fromSelf: false,
          char: props.char,
          inputs
        })}
        setters={
          <SetterSection
            debuff={debuff}
            inputs={inputs}
            setParties={setParties}
            curSetupI={curSetupI}
            tmI={tmI}
            dcIndex={dcIndex}
          />
        }
      />
    );
  });
  return (
    <>
      <Text
        className="pt-2 mb-m1"
        variant="h6"
        color={tmData.vision}
        align="center"
      >
        <strong>{teammate.name.toUpperCase()}</strong>
      </Text>
      {subContent}
    </>
  );
}

function SetterSection(props) {
  if (!props.debuff.labels) {
    return null;
  }
  const { debuff, inputs, setParties, curSetupI, tmI, dcIndex } = props;

  return (
    <DebuffSetters
      debuff={debuff}
      inputs={inputs}
      labels={debuff.labels}
      handleSelect={(value, i) =>
        setParties((prev) => {
          const newPts = deepCopy(prev);
          newPts[curSetupI][tmI].DCs[dcIndex].inputs[i] = value;
          return newPts;
        })
      }
      handleText={(value, i) =>
        setParties((prev) => {
          const newPts = deepCopy(prev);
          newPts[curSetupI][tmI].DCs[dcIndex].inputs[i] = processNumInp(
            value,
            inputs[i],
            13
          );
          return newPts;
        })
      }
    />
  );
}
