import { useState, useContext, useEffect } from "react";
import { SetDBCt } from "../../../App";
import { ModalCarpet, Box } from "../../../styledCpns/Layout";
import { TextArea, Button } from "../../../styledCpns/Inputs";
import { Text } from "../../../styledCpns/DataDisplay";
import { useCloseModal } from "../../../helpers/hooks";
import decodeImportedData from "../../../helpers/decode";

export default function ImportPreper(props) {
  const { close } = props;
  const { setImportedSt } = useContext(SetDBCt);
  const [code, setCode] = useState("");
  const [err, setErr] = useState(null);

  useEffect(() => setErr(null), [code]);

  const ref = useCloseModal(close);
  return (
    <ModalCarpet>
      <Box
        ref={ref}
        className="px-4 pt-3 pb-4 flex-col white-glow-shadow"
        borderRadius="0.5rem"
        width="21.75rem"
        bgColor="cardBg1"
      >
        <Text variant="h5" color="orange" fontWeight={700} align="center">
          Import Setup
        </Text>
        <Text
          className="mt-1 mb-2"
          variant="subtitle1"
          color="lightRed"
          align="center"
        >
          * Codes made before Jan 1st, 2022 are no longer functional.
        </Text>
        <TextArea
          className="p-2"
          rows="15"
          placeholder="We recommend you to save your work (download the database) before importing."
          value={code}
          onChange={(e) => setCode(e.target.value)}
        />
        {err !== null && (
          <Text className="mt-3" align="center" color="lightRed">
            {
              [
                "We're sorry. Your browser does not allow/support this function.",
                "This code is incomplete or has been wrongly modified.",
                "An unknown error has occurred. We're sorry that this setup cannot be imported."
              ][err]
            }
          </Text>
        )}
        <div className="mt-3 flex justify-between">
          <Button className="glow-on-hover" variant="negative" onClick={close}>
            Cancel
          </Button>
          <Button
            className="glow-on-hover"
            variant="neutral"
            onClick={() => {
              setErr(null);
              navigator.clipboard.readText().then(
                (txt) => setCode(txt),
                () => setErr(0)
              );
            }}
          >
            Paste
          </Button>
          <Button
            className="glow-on-hover"
            variant="positive"
            onClick={() => {
              if (code.length) {
                const respond = decodeImportedData(code);
                if (respond === "1") setErr("1");
                else if (respond === "2") setErr("2");
                else {
                  setImportedSt(respond);
                  close();
                }
              }
            }}
          >
            Confirm
          </Button>
        </div>
      </Box>
    </ModalCarpet>
  );
}
