export default function OneSetup({ stdValues, subNames }) {
  return (
    <tbody>
      <tr>
        <th className="limited-width" />
        <th>Non-crit</th>
        <th>Crit</th>
        <th className="lightGold-text">Avg.</th>
      </tr>
      {subNames.map((name, i) => {
        const { Crit, "Non-crit": NonCrit, Average } = stdValues[name];
        return (
          <tr key={i}>
            <td className="limited-width">{name}</td>
            <td>
              {Array.isArray(NonCrit)
                ? NonCrit.map((nc) => Math.round(nc)).join(" + ")
                : Math.round(NonCrit)}
            </td>
            <td>
              {Array.isArray(Crit)
                ? Crit.map((c) => Math.round(c)).join(" + ")
                : Math.round(Crit)}
            </td>
            <td className="lightGold-text">
              {Array.isArray(Average)
                ? Average.map((a) => Math.round(a)).join(" + ")
                : Math.round(Average)}
            </td>
          </tr>
        );
      })}
    </tbody>
  );
}
