import { LGreen, Green } from "../../../../styledCpns/DataDisplay";

export const buffs = [
  {
    src: "Constellation 1",
    desc: () => (
      <>
        Party members within the radius of Wake of Earth have their{" "}
        <LGreen>CRIT Rate</LGreen> increased by <Green>10%</Green>.
      </>
    ),
    isGranted: (char) => char.constellation >= 1,
    affect: "party",
    addBnes: ({ ATTRs }) => {
      ATTRs["CRIT Rate"] += 10;
    }
  }
];
