import {
  ColoredSpan,
  LGreen,
  Green,
  Red
} from "../../../../styledCpns/DataDisplay";
import { incAllNAs } from "../../../../helpers/calculators";
import data from "./index";

export const buffs = [
  {
    src: "Elemental Skill",
    desc: () => (
      <>
        Within 10s of remaining airborne after casting Chihayaburu, Kazuha's
        next <LGreen>Plunging Attack DMG</LGreen> is <LGreen>converted</LGreen>{" "}
        to <ColoredSpan color="Anemo">Anemo DMG</ColoredSpan>.
      </>
    ),
    isGranted: () => true,
    affect: "self",
    canInfuse: () => true,
    infuseRange: ["PA"],
    canBeOverrided: false
  },
  {
    src: "Ascension 4 Passive Talent",
    desc: ({ inputs }) => (
      <>
        Upon triggering a Swirl, Kazuha will grant all party members a{" "}
        <Green>0.04%</Green> <LGreen>Elemental DMG Bonus</LGreen> to the element
        absorbed by Swirl for every point of <LGreen>Elemental Mastery</LGreen>{" "}
        he has for 8s.{" "}
        <Red>
          {inputs[0]} DMG Bonus: {buffs[1].bnValue(inputs[1])}%.
        </Red>
      </>
    ),
    affect: "teammates",
    labels: ["Element Absorbed", "Elemental Mastery"],
    inputs: ["Pyro", 0],
    inputTypes: ["swirl", "text"],
    maxs: [null, 9999],
    addBnes: ({ ATTRs, inputs }) => {
      ATTRs[inputs[0] + " DMG Bonus"] += buffs[1].bnValue(inputs[1]);
    },
    bnValue: (EM) => Math.round(EM * 0.4) / 10
  },
  {
    src: "Constellation 2",
    desc: () => (
      <>
        The Autumn Whirlwind field created by Kazuha Slash increases the{" "}
        <LGreen>Elemental Mastery</LGreen> of him and characters within the
        field by <Green>200</Green>.
      </>
    ),
    isGranted: (char) => char.constellation >= 2,
    affect: "party",
    addBnes: ({ ATTRs }) => {
      ATTRs["Elemental Mastery"] += 200;
    }
  },
  {
    src: "Constellation 6",
    desc: ({ ATTRs }) => (
      <>
        {data.constellation[5].desc}{" "}
        <Red>DMG Bonus: {buffs[3].bnValue(ATTRs)}%.</Red>
      </>
    ),
    isGranted: (char) => char.constellation >= 6,
    affect: "self",
    addFinalBnes: ({ ATTRs, bonuses }) => {
      incAllNAs(bonuses, buffs[3].bnValue(ATTRs));
    },
    bnValue: (ATTRs) => Math.round(ATTRs["Elemental Mastery"] * 2) / 10,
    canInfuse: () => true,
    infuseRange: ["NA", "CA", "PA"],
    canBeOverrided: true
  }
];
