import { ColoredSpan, LGreen, Green } from "../../../../styledCpns/DataDisplay";
import actvTalents from "./talents";
import { TalentLvMults } from "../../config";
import { getXtraTalentLv } from "../../../../calculators/helpers";

export const buffs = [
  {
    src: "Elemental Burst",
    desc: () => (
      <>
        Converts Itto's <LGreen>Normal, Charged, and Plunging Attacks</LGreen>{" "}
        to <ColoredSpan color="Geo">Geo DMG</ColoredSpan>. This cannot be
        overridden. Increases Itto's <LGreen>Normal Attack SPD</LGreen>. Also
        increases his <LGreen>ATK</LGreen> based on his <LGreen>DEF</LGreen>.
      </>
    ),
    isGranted: () => true,
    affect: "self",
    addFinalBnes: ({ ATTRs, char, party }) => {
      const level =
        char["Elemental Burst"] + getXtraTalentLv(actvTalents[2], char, party);
      const mult = 57.6 * TalentLvMults[2][level];
      ATTRs.ATK += Math.round((ATTRs.DEF * mult) / 100);
      ATTRs["Normal ATK SPD"] += 10;
    },
    canInfuse: () => true,
    infuseRange: ["NA", "CA", "PA"],
    canBeOverrided: false
  },
  {
    src: "Ascension 4 Passive Talent",
    desc: () => (
      <>
        <LGreen>Arataki Kesagiri DMG</LGreen> is increased by <Green>35%</Green>{" "}
        of Itto's <LGreen>DEF</LGreen>.
      </>
    ),
    isGranted: (char) => char.ascension >= 4,
    affect: "self"
  },
  {
    src: "Constellation 4",
    desc: () => (
      <>
        When the Raging Oni King state caused by Royal Descent: Behold, Itto the
        Evil ends, all nearby party members gain <Green>20%</Green>{" "}
        <LGreen>DEF</LGreen> and <Green>20%</Green> <LGreen>ATK</LGreen> for
        10s.
      </>
    ),
    isGranted: (char) => char.constellation >= 4,
    affect: "party",
    addBnes: ({ ATTRs }) => {
      ATTRs["DEF%"] += 20;
      ATTRs["ATK%"] += 20;
    }
  },
  {
    src: "Constellation 6",
    desc: () => (
      <>
        Itto's <LGreen>Charged Attacks</LGreen> deal <Green>+70%</Green>{" "}
        <LGreen>CRIT DMG</LGreen>.
      </>
    ),
    isGranted: (char) => char.constellation >= 6,
    affect: "self",
    addBnes: ({ bonuses }) => {
      bonuses.CA.CD += 0.7;
    }
  }
];
