import { Setter, SetterInput, SetterSelect } from "../CmnCpns";
import { swirlables } from "../../../../data/elements";

export default function DebuffSetters(props) {
  const { debuff, inputs } = props;
  return props.labels.map((label, i) => {
    const type = debuff.inputTypes[i];
    let inputCpn;
    if (type === "absorption") {
      inputCpn = (
        <SetterSelect
          value={inputs[i]}
          onChange={(e) => props.handleSelect(e.target.value, i)}
        >
          {swirlables.map((opt) => (
            <option key={opt}>{opt}</option>
          ))}
        </SetterSelect>
      );
    } else if (type === "text") {
      inputCpn = (
        <SetterInput
          type="text"
          value={inputs[i]}
          onChange={(e) => props.handleText(e.target.value, i)}
        />
      );
    }
    return (
      <Setter key={i}>
        <p className="label">{label}</p>
        {inputCpn}
      </Setter>
    );
  });
}
