import styled from "styled-components";
import { useState } from "react";
import { Text } from "../../../styledCpns/DataDisplay";
import Overview from "./Overview";
import Details from "./Details";
import { colorCodes } from "../../../data/misc";
import characters from "../../../data/characters";

export default function PreCheck(props) {
  if (!characters[props.char.name].constellation.length) {
    return (
      <div className="pt-3 px-3">
        <Text variant="h5" align="center">
          The time has not yet come for this person's corner of the night sky to
          light up.
        </Text>
      </div>
    );
  }
  return <ConsList {...props} />;
}

function ConsList(props) {
  const [ctrlLv, setCtrlLv] = useState(0);
  const [atDetails, setAtDetails] = useState(false);
  const { constellation } = props.char;
  return (
    <StyledList className="full-w full-h overflow-hidden relative">
      <Overview
        charName={props.char.name}
        ctrlLv={ctrlLv}
        setCtrlLv={setCtrlLv}
        consLv={constellation}
        handleClick={props.handleClick}
        atDetails={atDetails}
        toDetails={(i) => {
          setAtDetails(true);
          setCtrlLv(i + 1);
        }}
      />
      <Details
        charName={props.char.name}
        ctrlLv={ctrlLv}
        consLv={constellation}
        atDetails={atDetails}
        setCtrlLv={setCtrlLv}
        close={() => {
          setAtDetails(false);
          setTimeout(() => setCtrlLv(0), 200);
        }}
      />
    </StyledList>
  );
}

const StyledList = styled.div`
  .details,
  .overview {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    transition: 200ms linear;
  }
  .overview.close,
  .caret.left {
    right: 100%;
  }
  .overview.open {
    right: 0;
  }
  .details.close,
  .caret.right {
    left: 100%;
  }
  .details.open {
    left: 0;
  }
  .caret {
    position: absolute;
    top: 0;
    font-size: 2.5rem;
    color: ${colorCodes.cardBg3};
  }
  .caret:hover {
    color: ${colorCodes.lightGold};
  }
  .cons_details {
    border-top: 3px solid ${colorCodes.cardBg2};
    border-bottom: 3px solid ${colorCodes.cardBg2};
  }
`;
