import { LGreen, Green, Red } from "../../../../styledCpns/DataDisplay";
import data from "./index";

export const buffs = [
  {
    src: "Ascension 4 Passive Talent",
    desc: ({ ATTRs }) => (
      <>
        Every point of <LGreen>Elemental Mastery</LGreen> will increase{" "}
        <LGreen>Sesshou Sakura DMG</LGreen> by <Green>0.15%</Green>.{" "}
        <Red>DMG Bonus: {buffs[0].bnValue(ATTRs)}%.</Red>
      </>
    ),
    isGranted: (char) => char.ascension >= 4,
    affect: "self",
    addBnes: ({ ATTRs, bonuses }) => {
      bonuses.ES.DMG += buffs[0].bnValue(ATTRs);
    },
    bnValue: (ATTRs) => ATTRs["Elemental Mastery"] * 0.15
  },
  {
    src: "Constellation 2",
    desc: () => data.constellation[1].desc,
    isGranted: (char) => char.constellation >= 2,
    affect: "self"
  },
  {
    src: "Constellation 4",
    desc: () => data.constellation[3].desc,
    isGranted: (char) => char.constellation >= 4,
    affect: "party",
    addBnes: ({ ATTRs }) => {
      ATTRs["Electro DMG Bonus"] += 20;
    }
  }
];

export const debuffs = [
  {
    src: "Constellation 6",
    desc: () => data.constellation[5].desc,
    isGranted: (char) => char.constellation >= 6,
    affect: "self",
    addPntes: ({ rdMult }) => {
      rdMult.DEFtoES += 60;
    }
  }
];
