import styled from "styled-components";
import { Text, List, Img } from "../../styledCpns/DataDisplay";
import { Select } from "../../styledCpns/Inputs";
import { Box, AbsoluteBox } from "../../styledCpns/Layout";
import { BetaMark } from "../CommonCpns";
import { pctOf } from "../../helpers/misc";
import { colorCodes, levels, wikia } from "../../data/misc";
import weapons from "../../data/weapons";

export default function WeaponCard(props) {
  const { weapon, mutable } = props;
  const wpData = weapons[weapon.type][weapon.name];
  const { rarity } = wpData;
  const isTrash = ["Green", "Gray"].includes(rarity);
  const selectLvs = [...levels];
  if (isTrash) selectLvs.splice(-4);

  const HiliText = (props) => <Text color={rarity} {...props} />;

  const LeftSec = (
    <div className="grow-1 flex-col justify-between">
      <Group className="pt-1 grow-1 flex align-center">
        <Text className="mr-2" variant="h6" fontWeight={700}>
          Level
        </Text>
        {mutable ? (
          <Select
            color={rarity}
            fontSize="1.125rem"
            fontWeight={700}
            align="right"
            value={weapon.level}
            onChange={(e) => props.upgrade(e.target.value)}
          >
            {selectLvs.map((level) => (
              <option key={level}>{level}</option>
            ))}
          </Select>
        ) : (
          <Text variant="h6" fontWeight={700} color={rarity}>
            {weapon.level}
          </Text>
        )}
      </Group>
      {wpData.subStats &&
        wpData.subStats.map((stat) => (
          <Group
            key={stat.type}
            className="grow-1 mt-1 pt-1 flex-col justify-center"
          >
            <Text fontWeight={700}>{stat.type}</Text>
            <HiliText variant="h3">
              {stat.value[weapon.level] + pctOf(stat.type)}
            </HiliText>
          </Group>
        ))}
      <Group className="grow-1 mt-1 pt-1 flex-col justify-center">
        <Text fontWeight={700}>Base ATK</Text>
        <HiliText variant="h2">{wpData.mainStat.value[weapon.level]}</HiliText>
      </Group>
    </div>
  );

  const RightSec = (
    <div className="ml-2">
      <Box className="relative" borderRadius="0.5rem" bgImg={rarity}>
        <Img
          width="8rem"
          src={wikia + wpData.icon + ".png"}
          alt=""
          draggable={false}
        />
        {wpData.beta && (
          <AbsoluteBox bottom={0} right={0}>
            <BetaMark />
          </AbsoluteBox>
        )}
      </Box>
      {!isTrash && (
        <Group className="mt-2 py-1 flex-col align-center">
          <Text fontWeight={700} align="center">
            Refinement
          </Text>
          {mutable ? (
            <Select
              color={rarity}
              fontSize="1.125rem"
              fontWeight={700}
              value={weapon.refinement}
              onChange={(e) => props.refine(e.target.value)}
            >
              {[1, 2, 3, 4, 5].map((level) => (
                <option key={level}>{level}</option>
              ))}
            </Select>
          ) : (
            <Text variant="h6" fontWeight={700} color={rarity}>
              {weapon.refinement}
            </Text>
          )}
        </Group>
      )}
    </div>
  );
  return (
    <div className="full-w">
      <Text variant="h2" color={rarity}>
        {weapon.name}
      </Text>
      <div className="mt-2 flex">
        {LeftSec}
        {RightSec}
      </div>
      <div className="mt-2">
        <Text variant="h6" fontWeight={700} color="orange">
          {wpData.pasvName}
        </Text>
        <List position="inside">
          {wpData.pasvDescs({ wpRfm: weapon.refinement }).map((desc, i) => (
            <li key={i}>
              {desc.bonus} {desc.buff} {desc.others}
            </li>
          ))}
        </List>
      </div>
    </div>
  );
}

let Group = styled.div`
  background-color: ${colorCodes.cardBg2};
  padding-right: 0.5rem;
  padding-left: 0.5rem;
`;
