import { useState } from "react";
import { Box, ModalCarpet, ModalInner } from "../../styledCpns/Layout";
import Heading from "./Heading";
import Body from "./Body";
import { useCloseModal } from "../../helpers/hooks";

export default function Picker(props) {
  const { data, dataType, close } = props;
  const [filter, setFilter] = useState({ type: "", value: "" });
  let filteredData = data;
  if (filter.type.length) {
    filteredData = data.filter((unit) => unit[filter.type] === filter.value);
  }

  const handleChoose = (chosen, newFilter) =>
    setFilter(chosen ? { type: "", value: "" } : newFilter);

  const ref = useCloseModal(close);
  return (
    <ModalCarpet>
      <ModalInner ref={ref}>
        {props.needMassAdd ? (
          <MassPicker
            dataType={dataType}
            data={filteredData}
            filter={filter}
            setFilter={handleChoose}
            pick={props.pick}
            close={close}
          />
        ) : (
          <SinglePicker
            dataType={dataType}
            data={filteredData}
            filter={filter}
            setFilter={handleChoose}
            pick={props.pick}
            close={close}
          />
        )}
      </ModalInner>
    </ModalCarpet>
  );
}

function SinglePicker(props) {
  const { dataType, close } = props;
  return (
    <>
      <Box height="10%" className="p-2">
        <Heading
          dataType={dataType}
          close={close}
          needMassAdd={false}
          filter={props.filter}
          setFilter={props.setFilter}
        />
      </Box>
      <Box height="90%" className="px-3 pt-2 pb-3">
        <Body
          data={props.data}
          forChar={dataType === "Character"}
          pick={(item) => {
            props.pick(item);
            close();
          }}
        />
      </Box>
    </>
  );
}

function MassPicker(props) {
  const { dataType, data, close } = props;
  const [massAdd, setMassAdd] = useState(false);
  const [total, setTotal] = useState(0);
  const [each, setEach] = useState(data.map(() => 0));
  return (
    <>
      <Box height="10%" className="p-2">
        <Heading
          dataType={dataType}
          close={close}
          needMassAdd={true}
          massAdd={massAdd}
          count={total}
          setMassAdd={setMassAdd}
          filter={props.filter}
          setFilter={props.setFilter}
        />
      </Box>
      <Box height="90%" className="px-3 pt-2 pb-3">
        <Body
          data={data}
          forChar={dataType === "Character"}
          each={each}
          pick={(item, i) => {
            props.pick(item);
            if (!massAdd) close();
            else {
              setTotal(total + 1);
              if (dataType !== "Character")
                setEach((prev) => {
                  const arr = [...prev];
                  arr[i]++;
                  return arr;
                });
            }
          }}
        />
      </Box>
    </>
  );
}
