import { Red } from "../../../../styledCpns/DataDisplay";
import data from "./index";

export const buffs = [
  {
    src: "Ascension 1 Passive Talent",
    desc: () => data.pasvTalents[0].desc,
    affect: "teammates",
    addBnes: ({ ATTRs }) => {
      ATTRs["Elemental Mastery"] += 50;
    }
  },
  {
    src: "Ascension 4 Passive Talent",
    desc: ({ inputs }) => (
      <>
        {data.pasvTalents[1].desc}.{" "}
        <Red>Elemental Mastery Bonus: {buffs[1].bnValue(inputs)}.</Red>
      </>
    ),
    affect: "teammates",
    labels: ["Elemental Mastery"],
    inputs: [0],
    inputTypes: ["text"],
    maxs: [9999],
    addBnes: ({ ATTRs, inputs }) => {
      ATTRs["Elemental Mastery"] += buffs[1].bnValue(inputs);
    },
    bnValue: ([EM]) => Math.round(EM * 0.2)
  },
  {
    src: "Constellation 6",
    desc: () => data.constellation[5].desc,
    affect: "teammates",
    labels: ["Element Absorbed"],
    inputs: ["Pyro"],
    inputTypes: ["swirl"],
    addBnes: ({ ATTRs, inputs }) => {
      ATTRs[inputs[0] + " DMG Bonus"] += 20;
    }
  }
];
