import styled from "styled-components";
import { Box } from "../../styledCpns/Layout";
import { wikia, visionIcons, weaponIcons } from "../../data/misc";

export default function CharFilter({ filter, setFilter, setFilterOn }) {
  const { type, value } = filter;
  return (
    <StyledDiv>
      <div className="py-3 flex-wrap justify-around">
        <Box className="flex overflow-auto hide-sb">
          {Object.entries(visionIcons).map((pair, i) => {
            const chosen = type === "vision" && value === pair[0];
            return (
              <img
                key={i}
                className={"filter_icon vision" + (chosen ? " chosen" : "")}
                src={wikia + pair[1] + ".png"}
                alt="vision"
                draggable={false}
                onClick={() => {
                  setFilter(chosen, { type: "vision", value: pair[0] });
                  setFilterOn(false);
                }}
              />
            );
          })}
        </Box>
        <Box className="flex overflow-auto hide-sb">
          {Object.entries(weaponIcons).map((pair, i) => {
            const chosen = type === "weapon" && value === pair[0];
            return (
              <img
                key={i}
                className={"filter_icon weapon" + (chosen ? " chosen" : "")}
                src={wikia + pair[1] + ".png"}
                alt="vision"
                draggable={false}
                onClick={() => {
                  setFilter(chosen, { type: "weapon", value: pair[0] });
                  setFilterOn(false);
                }}
              />
            );
          })}
        </Box>
      </div>
    </StyledDiv>
  );
}

const StyledDiv = styled.div`
  padding: 0 0.75rem;
  .filter_icon {
    cursor: pointer;
    border-radius: 50%;
  }
  .filter_icon:not(:last-child) {
    margin-right: 1rem;
  }
  .filter_icon.chosen {
    border: 3px solid white;
  }
  .vision {
    width: 2rem;
    height: 2rem;
  }
  .weapon {
    width: 2.25rem;
    height: 2.25rem;
    margin-top: 1rem;
  }
  @media (min-width: 769px) {
    .weapon {
      margin-top: 0;
    }
  }
  @media (min-width: 1025px) {
    .vision {
      width: 2.5rem;
      height: 2.5rem;
    }
    .weapon {
      width: 2.75rem;
      height: 2.75rem;
    }
  }
`;
