import { useContext } from "react";
import { ArtChestCt, FixedCts, SetupCt } from "../../index";
import { Section, Setter, SetterSelect } from "../CmnCpns";
import artifactDebuffs from "../../../../data/artifacts/artifactDebuffs";
import { swirlables } from "../../../../data/elements";
import { deepCopy } from "../../../../helpers/misc";

export default function ArtsDebuff(props) {
  const artChest = useContext(ArtChestCt);
  const curSetupI = useContext(SetupCt);
  const { setArtChest } = useContext(FixedCts);
  const { activated, set, inputs, index } = artChest[curSetupI].subDCs[0];
  const debuff = artifactDebuffs[set][index];

  return (
    <Section
      checked={activated}
      handleCheck={() =>
        setArtChest((prev) => {
          const newArts = deepCopy(prev);
          newArts[curSetupI].subDCs[0].activated = !activated;
          return newArts;
        })
      }
      heading={set}
      desc={debuff.desc}
      setters={
        <Setter>
          <p className="label">Element swirled</p>
          <SetterSelect
            value={inputs[0]}
            onChange={(e) =>
              setArtChest((prev) => {
                const newArts = deepCopy(prev);
                newArts[curSetupI].subDCs[0].inputs[0] = e.target.value;
                return newArts;
              })
            }
          >
            {swirlables.map((opt) => (
              <option key={opt}>{opt}</option>
            ))}
          </SetterSelect>
        </Setter>
      }
    />
  );
}
