import { useState, useRef, forwardRef } from "react";
import { ModalCarpet } from "../../styledCpns/Layout";
import { Button } from "../../styledCpns/Inputs";
import { Text } from "../../styledCpns/DataDisplay";
import { StyledInner, CloseBtn, Divider } from "./styles";
import { NavBtn } from "../NavBar";
import { useCloseModal } from "../../helpers/hooks";

export default function Upload(props) {
  const [optionsOn, setOptionsOn] = useState(false);

  return (
    <div>
      <NavBtn
        className={props.padding + " pointer"}
        onClick={() => setOptionsOn(true)}
      >
        <i className="fas fa-upload mr-2" />
        <Text fontWeight={700}>Upload</Text>
      </NavBtn>
      {optionsOn && (
        <ForwardedOptions
          loadData={props.loadData}
          close={() => setOptionsOn(false)}
          closeDD={props.closeDD}
        />
      )}
    </div>
  );
}

const ForwardedOptions = forwardRef(Options);

function Options(props) {
  const { loadData, close } = props;
  const [err, setErr] = useState(false);

  const tryToLoadFromLocalStorage = () => {
    let data = localStorage.getItem("GDC_Data");
    if (data) {
      data = JSON.parse(data);
      loadData(data);
      close();
    } else {
      setErr(true);
    }
  };

  const chooseRef = useRef();
  const upload = () => {
    const file = chooseRef.current.files[0];
    const reader = new FileReader();
    if (file?.type.match(/text.*/)) {
      reader.onload = function (event) {
        const data = JSON.parse(event.target.result);
        loadData(data);
        close();
        if (props.closeDD) props.closeDD();
      };
      reader.readAsText(file);
    }
  };

  const closeRef = useCloseModal(close);
  return (
    <ModalCarpet>
      <StyledInner ref={closeRef} className="pt-2 pb-3">
        <CloseBtn onClick={close} />
        <div className="option flex-col align-center">
          <Text variant="h6" fontWeight={700}>
            Load from Local Storage
          </Text>
          {err && (
            <Text color="red">! There's no data in your Local Storage !</Text>
          )}
          <Button
            className="my-1"
            variant="positive"
            onClick={tryToLoadFromLocalStorage}
          >
            Load
          </Button>
        </div>
        <Divider />
        <div className="option flex-col align-center">
          <Text variant="h6" fontWeight={700}>
            Upload a .TXT file
          </Text>
          <input
            ref={chooseRef}
            style={{ display: "none" }}
            type="file"
            accept="text/*"
            onChange={upload}
          />
          <Button
            className="my-1"
            variant="positive"
            onClick={() => {
              chooseRef.current.click();
            }}
          >
            Choose File
          </Button>
        </div>
      </StyledInner>
    </ModalCarpet>
  );
}
