// import styled from "styled-components";
import { useState, useContext } from "react";
// import { Collapse } from "@material-ui/core";
import { MyCharsCt } from "../../App";
import { Box } from "../../styledCpns/Layout";
// import { ActionBtn } from "../CommonCpns";
import { Select, Button } from "../../styledCpns/Inputs";
import CharSorting from "./CharSorting";

export default function TopBarSmall(props) {
  const myChars = useContext(MyCharsCt);
  return (
    <Box className="py-3 flex" bgColor="cardBg2">
      {myChars.length ? (
        <MoreChars {...props} />
      ) : (
        <NoChars setPickerOn={props.setPickerOn} />
      )}
    </Box>
  );
}

function NoChars(props) {
  return (
    <Button
      className="mx-auto"
      variant="positive"
      onClick={() => props.setPickerOn(true)}
    >
      Add New Characters
    </Button>
  );
}

// const StyledDD = styled.div`
//   position: absolute;
//   top: 100%;
//   z-index: 2;
//   box-shadow: 0 3px 2px -2px white;
//   .border-top {
//     border-top: 2px solid rgba(255, 255, 255, 0.3);
//   }
// `;

function MoreChars(props) {
  const { chosenI } = props;
  // const [dropped, setDropped] = useState(false);
  const [sorting, setSorting] = useState(false);
  const myChars = useContext(MyCharsCt);

  return (
    <div className="full-w flex-center relative">
      <Select
        className="px-2 py-1"
        color="black"
        borderRadius="0.25rem"
        bgColor="white"
        fontSize="1.5rem"
        fontWeight={700}
        align="center"
        value={myChars[chosenI].name}
        onChange={(e) =>
          props.setChosenI(
            myChars.findIndex(({ name }) => name === e.target.value)
          )
        }
      >
        {myChars.map(({ name }, i) => (
          <option key={i}>{name}</option>
        ))}
      </Select>
      <Button
        className="ml-4"
        variant="positive"
        onClick={() => props.setPickerOn(true)}
      >
        Add
      </Button>
      {/* <ActionBtn
        icon="fas fa-ellipsis-h"
        className="ml-4"
        bgColor={dropped ? "green" : "white"}
        onClick={() => setDropped(!dropped)}
      /> */}
      {/* <StyledDD className="full-w">
        <Collapse in={dropped}>
          <Box className="mt-3 px-3" bgColor="cardBg2">
            <Box className="p-3 flex justify-end border-top">
              <Button
                className="mr-4"
                variant="positive"
                onClick={() => props.setPickerOn(true)}
              >
                Add
              </Button>
              <Button variant="positive" onClick={() => setSorting(true)}>
                Sort
              </Button>
            </Box>
          </Box>
        </Collapse>
      </StyledDD> */}
      {sorting && (
        <CharSorting close={() => setSorting(false)} myChars={myChars} />
      )}
    </div>
  );
}
