import { Green, LGreen } from "../../../styledCpns/DataDisplay";
import { baseAtkType, subStatsType } from "../config";
import { BaneSeries1 } from "../series";

const BlueBows = {
  Messenger: {
    code: 1,
    icon: "3/38/Weapon_Messenger",
    rarity: "Blue",
    mainStat: {
      type: "Base ATK",
      value: baseAtkType[40]
    },
    subStats: [
      {
        type: "CRIT DMG",
        value: subStatsType["6.8%"]
      }
    ],
    pasvProcD: [
      {
        name: "Additional DMG",
        baseSType: "ATK",
        multiplier: [0, 1, 1.25, 1.5, 1.75, 2],
        dmgType: "Physical"
      }
    ],
    pasvName: "Archer's Message",
    pasvDescs: ({ wpRfm }) => [
      {
        bonus: (
          <>
            Charged Attack hits on weak spots deal an additional{" "}
            <Green>{75 + wpRfm * 25}%</Green> <LGreen>ATK</LGreen> DMG as CRIT
            DMG. Can only occur once every 10s.
          </>
        )
      }
    ]
  },
  "Raven Bow": {
    code: 2,
    icon: "d/d0/Weapon_Raven_Bow",
    rarity: "Blue",
    mainStat: {
      type: "Base ATK",
      value: baseAtkType[40]
    },
    subStats: [
      {
        type: "Elemental Mastery",
        value: subStatsType[20]
      }
    ],
    ...BaneSeries1("Flame and Water", "Hydro or Pyro")
  },
  "Sharpshooter's Oath": {
    code: 3,
    icon: "5/52/Weapon_Sharpshooter%27s_Oath",
    rarity: "Blue",
    mainStat: {
      type: "Base ATK",
      value: baseAtkType[39]
    },
    subStats: [
      {
        type: "CRIT DMG",
        value: subStatsType["10.2%"]
      }
    ],
    buffs: [
      {
        affect: "self",
        addBnes: ({ bonuses, wpRfm }) => {
          if (bonuses) bonuses.CA.DMG += 18 + wpRfm * 6;
        },
        desc: ({ wpRfm }) =>
          BlueBows["Sharpshooter's Oath"].pasvDescs({ wpRfm })[0].buff
      }
    ],
    pasvName: "Precise",
    pasvDescs: ({ wpRfm }) => [
      {
        buff: (
          <>
            Increases <LGreen>DMG</LGreen> against weak spots by{" "}
            <Green>{18 + wpRfm * 6}%</Green>.
          </>
        )
      }
    ]
  },
  Slingshot: {
    code: 4,
    icon: "c/ca/Weapon_Slingshot",
    rarity: "Blue",
    mainStat: {
      type: "Base ATK",
      value: baseAtkType[38]
    },
    subStats: [
      {
        type: "CRIT Rate",
        value: subStatsType["6.8%"]
      }
    ],
    buffs: [
      {
        affect: "self",
        addBnes: ({ bonuses, wpRfm }) => {
          if (bonuses) {
            bonuses.NA.DMG += 30 + wpRfm * 6;
            bonuses.CA.DMG += 30 + wpRfm * 6;
          }
        },
        desc: ({ wpRfm }) => BlueBows.Slingshot.pasvDescs({ wpRfm })[0].buff
      }
    ],
    pasvName: "Slingshot",
    pasvDescs: ({ wpRfm }) => [
      {
        buff: (
          <>
            If a <LGreen>Normal or Charged Attack</LGreen> hits a target within
            0.3s of being fired, increases <LGreen>DMG</LGreen> by{" "}
            <Green>{30 + wpRfm * 6}%</Green>. Otherwise, decreases DMG by 10%.
          </>
        )
      }
    ]
  }
};

export default BlueBows;
