import {
  ColoredSpan,
  LGreen,
  Green,
  Red
} from "../../../../styledCpns/DataDisplay";
import { TalentLvMults } from "../../config";
import actvTalents from "./talents";
import { getXtraTalentLv } from "../../../../calculators/helpers";

export const buffs = [
  {
    src: "Elemental Skill",
    desc: ({ char, charBCs, party }) => (
      <>
        When a Freeze Bomb or Chillwater Bomblet hits an opponent, Aloy receives
        1 Coil stack, increasing her <LGreen>Normal Attack DMG</LGreen>.{" "}
        <Red>Total DMG Bonus: {buffs[0].bnValue(char, charBCs, party)}%.</Red>
      </>
    ),
    isGranted: () => true,
    affect: "self",
    selfLabels: ["Stacks"],
    inputs: [1],
    inputTypes: ["select"],
    maxs: [3],
    addBnes: ({ bonuses, char, charBCs, party }) => {
      bonuses.NA.DMG += buffs[0].bnValue(char, charBCs, party);
    },
    bnValue: (char, charBCs, party) => {
      const level =
        char["Elemental Skill"] + getXtraTalentLv(actvTalents[1], char, party);
      const stacks = charBCs[1].activated ? 5 : charBCs[0].inputs[0];
      return Math.round(584.6 * TalentLvMults[5][level] * stacks) / 100;
    }
  },
  {
    src: "Elemental Skill",
    desc: () => (
      <>
        When Aloy has 4 Coil stacks, further increases the <LGreen>DMG</LGreen>{" "}
        dealt by her <LGreen>Normal Attacks</LGreen> and converts her{" "}
        <LGreen>Normal Attack DMG</LGreen> to{" "}
        <ColoredSpan color="Cryo">Cryo DMG</ColoredSpan>.
      </>
    ),
    isGranted: () => true,
    affect: "self",
    canInfuse: () => true,
    infuseRange: ["NA"],
    canBeOverrided: false
  },
  {
    src: "Ascension 1 Passive Talent",
    desc: () => (
      <>
        When Aloy receives the Coil effect from Frozen Wilds, her{" "}
        <LGreen>ATK</LGreen> is increased by <Green>16%</Green>, while nearby
        party members' <LGreen>ATK</LGreen> is increased by <Green>8%</Green>.
        This effect lasts 10s.
      </>
    ),
    isGranted: (char) => char.ascension >= 1,
    affect: "party",
    addBnes: ({ ATTRs, toSelf }) => {
      ATTRs["ATK%"] += toSelf ? 16 : 8;
    }
  },
  {
    src: "Ascension 4 Passive Talent",
    desc: () => (
      <>
        When Aloy is in the Rushing Ice state conferred by Frozen Wilds, her{" "}
        <LGreen>Cryo DMG Bonus</LGreen> increases by <Green>3.5%</Green> every
        1s, up to <Green>35%</Green>.
      </>
    ),
    isGranted: (char) => char.ascension >= 4,
    affect: "self",
    selfLabels: ["Stacks"],
    inputs: [1],
    inputTypes: ["select"],
    maxs: [10],
    addBnes: ({ ATTRs, inputs }) => {
      ATTRs["Cryo DMG Bonus"] += 3.5 * inputs[0];
    }
  }
];
