import { Box } from "../../../../styledCpns/Layout";
import Thumb from "../../../Readers/Thumb";
import weapons from "../../../../data/weapons";
import artifacts from "../../../../data/artifacts";
import { wikia, artifactIcons, artTypes } from "../../../../data/misc";

export function Weapon(props) {
  const { weapon } = props;
  const { icon, rarity } = weapons[weapon.type][weapon.name];
  return (
    <Box className="p-1" width="33.33%">
      <Thumb
        noDeco={true}
        item={weapon}
        icon={icon}
        rarity={rarity}
        chosen={props.chosen}
        choose={props.choose}
      />
    </Box>
  );
}

export function ArtPiece(props) {
  const { artP } = props;
  const { icon, rarity = "Gold" } = artifacts[artP.set][artP.type];
  return (
    <Box className="p-1" width="33.33%">
      <Thumb
        noDeco={true}
        item={artP}
        icon={icon}
        rarity={rarity}
        chosen={props.chosen}
        choose={props.choose}
      />
    </Box>
  );
}

export function ArtIcon(props) {
  const artT = artTypes[props.index];
  return (
    <Box className="p-1" width="33%" height="7.75rem">
      <Box
        className="p-3 full-h flex-center pointer glow-on-hover"
        borderRadius="0.25rem"
        bgColor="cardBg3"
        onClick={() => props.setArtInvType(artT)}
      >
        <img
          className="full-w"
          src={wikia + artifactIcons[artT] + ".png"}
          alt=""
        />
      </Box>
    </Box>
  );
}
