import { useContext, useCallback } from "react";
import { MyCharsCt, MyWpsCt, MyArtsCt } from "../../../App";
import { FixedCts } from "../index";
import Picker from "../../Picker";
import {
  initSelfMCs,
  initSubWpMCs,
  initParty,
  initElmtMCs,
  parseData
} from "./helpers";
import { initMonster, getSetupCtrl } from "../helpers";
import characters from "../../../data/characters";

export default function CharPicker(props) {
  const myChars = useContext(MyCharsCt);
  const myWeapons = useContext(MyWpsCt);
  const myArts = useContext(MyArtsCt);
  const {
    setSelfMCs,
    setWpRack,
    setSubWpMCs,
    setArtChest,
    setParties,
    setElmtMCs,
    setMonster,
    setSetups,
    setStdSetupI,
    setCurSetupI,
    setCmpSetupIs
  } = useContext(FixedCts);

  const pickChar = useCallback(
    (unit) => {
      const { char, wp, art } = parseData(unit, myWeapons, myArts);
      props.setChar(char);
      setSelfMCs([initSelfMCs(char.name)]);
      setWpRack([wp]);
      setSubWpMCs([initSubWpMCs()]);
      setArtChest([art]);
      setParties([initParty()]);
      setElmtMCs([initElmtMCs()]);
      setMonster(initMonster());
      setSetups([getSetupCtrl({})]);
      setCurSetupI(0);
      setStdSetupI(0);
      setCmpSetupIs([0]);
    },
    [myWeapons, myArts]
  );

  return (
    <Picker
      data={getCombinedList(myChars)}
      dataType="Character"
      close={props.close}
      pick={pickChar}
    />
  );
}

function getCombinedList(myChars) {
  const result = [];
  for (let name in characters) {
    const { beta, icon, rarity, vision, weapon } = characters[name];
    const char = { beta, icon, rarity, vision, weapon };
    const existedUnit = myChars.find((unit) => unit.name === name);
    if (existedUnit) {
      result.push({ ...existedUnit, ...char });
    } else {
      result.push({ name, ...char });
    }
  }
  return result;
}
