import { Text, Img } from "../../../styledCpns/DataDisplay";
import { Select, IconBtn, Button } from "../../../styledCpns/Inputs";
import { Box } from "../../../styledCpns/Layout";
import { MutableSSLine, ImmutableSSLine } from "./SSLine";
import { pctOf } from "../../../helpers/misc";
import artifacts from "../../../data/artifacts";
import { artMS } from "../../../data/artifacts/config";
import { wikia } from "../../../data/misc";

export default function ArtifactCard(props) {
  const { art, mutable } = props;
  const { name, icon } = artifacts[art.set][art.type];
  const { rarity = "Gold" } = art;
  const msType = art.mainSType;
  const availableMSTypes = artMS[art.type];
  const isFoP = ["flower", "plume"].includes(art.type);
  const msValues = availableMSTypes[msType];
  return (
    <div className="full-w">
      <Box className="px-3 pt-1" bgColor={rarity}>
        <Text className="ellipsis" variant="h5" fontWeight={700} color="black">
          {name}
        </Text>
      </Box>
      <div className="mt-3 mx-3 flex">
        <LevelSection
          mutable={mutable}
          rarity={rarity}
          level={art.level}
          enhance={props.enhance}
          up4={props.up4}
          toMax={props.toMax}
        />
        <Box borderRadius="0.5rem" bgImg={rarity}>
          <Img
            src={wikia + icon + ".png"}
            alt=""
            width="7rem"
            draggable={false}
          />
        </Box>
      </div>

      <div className="mt-2 ml-4">
        {isFoP || !mutable ? (
          <Text className={(!mutable ? "pl-2" : "pl-5") + " pt-1"} variant="h6">
            {msType}
          </Text>
        ) : (
          <div className="py-1">
            <Select
              className="pl-5 appearance-none bg-for-icon white-arrow-bg"
              fontSize="1.125rem"
              color="white"
              value={msType}
              onChange={props.changeMSType}
            >
              {Object.keys(availableMSTypes).map((type) => (
                <option key={type}>{type}</option>
              ))}
            </Select>
          </div>
        )}
        <Text
          className={!mutable ? "pl-2" : "pl-5"}
          variant="h3"
          color={rarity}
        >
          {msValues[art.level] + pctOf(msType)}
        </Text>
      </div>

      <div className={mutable ? "px-2" : ""}>
        {art.subS.map((subStat, ssIndex) =>
          mutable ? (
            <MutableSSLine
              key={ssIndex}
              stat={subStat}
              changeSSType={(e) => props.changeSSType(e.target.value, ssIndex)}
              changeSSVal={(e) =>
                props.changeSSVal(e.target.value, ssIndex, subStat.val)
              }
            />
          ) : (
            <ImmutableSSLine
              key={ssIndex}
              stat={subStat}
              ssSpace={props.ssSpace}
            />
          )
        )}
      </div>
    </div>
  );
}

function LevelSection(props) {
  const { rarity } = props;
  if (!props.mutable) {
    return (
      <Box width="9.75rem">
        <Box
          className="px-2 pt-2 pb-1 cursor-default"
          width="3.05rem"
          bgColor="cardBg3"
          borderRadius="50%"
        >
          <Text variant="h6" fontWeight={700} color={rarity}>
            {"+" + props.level}
          </Text>
        </Box>
      </Box>
    );
  }
  return (
    <div className="mr-4 pr-2 grow-1 flex justify-between">
      <div>
        <Box bgColor="cardBg3" borderRadius="50%">
          <Select
            color={rarity}
            fontSize="1.125rem"
            fontWeight={700}
            className="appearance-none px-2 pt-2 pb-1 pointer"
            value={"+" + props.level}
            onChange={props.enhance}
          >
            {[...Array(rarity === "Gold" ? 21 : 17).keys()].map((_, lv) => (
              <option key={lv}>+{lv}</option>
            ))}
          </Select>
        </Box>
      </div>
      <div className="mt-1 flex-col align-center">
        <IconBtn
          className="glow-on-hover"
          icon="fas fa-arrow-up"
          bgColor="orange"
          color="black"
          fontSize="1.25rem"
          size="1.75rem"
          onClick={props.up4}
        />
        <Button
          className="mt-4 glow-on-hover"
          padding="0.25rem 0.375rem"
          borderRadius="0.25rem"
          bgColor="orange"
          color="black"
          fontWeight={900}
          onClick={props.toMax}
        >
          MAX
        </Button>
      </div>
    </div>
  );
}
