import { LGreen, Green } from "../../../styledCpns/DataDisplay";
import { baseAtkType, subStatsType } from "../config";

const GoldBows = {
  "Skyward Harp": {
    code: 5,
    icon: "1/19/Weapon_Skyward_Harp",
    rarity: "Gold",
    mainStat: {
      type: "Base ATK",
      value: baseAtkType[48]
    },
    subStats: [
      {
        type: "CRIT Rate",
        value: subStatsType["4.8%"]
      }
    ],
    addBnes: ({ ATTRs, wpRfm }) => {
      ATTRs["CRIT DMG"] += 15 + wpRfm * 5;
    },
    pasvProcD: [
      {
        name: "AoE Attack",
        baseSType: "ATK",
        multiplier: [0, 1.25, 1.25, 1.25, 1.25, 1.25],
        dmgType: "Physical"
      }
    ],
    pasvName: "Echoing Ballad",
    pasvDescs: ({ wpRfm }) => [
      {
        bonus: (
          <>
            Increases <LGreen>CRIT DMG</LGreen> by{" "}
            <Green>{15 + wpRfm * 5}%</Green>. Hits have a{" "}
            <Green>{50 + wpRfm * 10}%</Green> <LGreen>chance</LGreen> to inflict
            a small AoE attack, dealing <LGreen>125% Physical ATK DMG</LGreen>.
            Can only occur once every <Green>{4.5 - wpRfm * 0.5}s</Green>.
          </>
        )
      }
    ]
  },
  "Polar Star": {
    code: 6,
    icon: "4/44/Weapon_Polar_Star",
    rarity: "Gold",
    mainStat: {
      type: "Base ATK",
      value: baseAtkType[46]
    },
    subStats: [
      {
        type: "CRIT Rate",
        value: subStatsType["7.2%"]
      }
    ],
    addBnes: ({ bonuses, wpRfm }) => {
      if (bonuses) {
        bonuses.ES.DMG += 9 + wpRfm * 3;
        bonuses.EB.DMG += 9 + wpRfm * 3;
      }
    },
    buffs: [
      {
        affect: "self",
        labels: ["Stacks"],
        inputTypes: ["stacks"],
        values: [4],
        addBnes: ({ ATTRs, wpRfm, inputs }) => {
          ATTRs["ATK%"] += GoldBows["Polar Star"].stackVal({ wpRfm })[
            inputs[0] - 1
          ];
        },
        desc: ({ wpRfm }) => GoldBows["Polar Star"].pasvDescs({ wpRfm })[0].buff
      }
    ],
    stackVal: ({ wpRfm }) => [
      7.5 + wpRfm * 2.5,
      15 + wpRfm * 5,
      22.5 + wpRfm * 7.5,
      36 + wpRfm * 12
    ],
    pasvName: "Daylight's Augury",
    pasvDescs: ({ wpRfm }) => [
      {
        bonus: (
          <>
            <LGreen>Elemental Skill</LGreen> and{" "}
            <LGreen>Elemental Burst DMG</LGreen> increased by{" "}
            <Green>{9 + wpRfm * 3}%</Green>. After a Normal Attack, Charged
            Attack, Elemental Skill or Elemental Burst hits an opponent, 1 stack
            of Ashen Nightstar will be gained for 12s.
          </>
        ),
        buff: (
          <>
            When 1/2/3/4 stacks of Ashen Nightstar are present,{" "}
            <LGreen>ATK</LGreen> is increased by{" "}
            <Green>
              {GoldBows["Polar Star"].stackVal({ wpRfm }).join("/")}%
            </Green>
            .
          </>
        ),
        others: (
          <>
            The stack of Ashen Nightstar created by the Normal Attack, Charged
            Attack, Elemental Skill or Elemental Burst will be counted
            independently of the others.
          </>
        )
      }
    ]
  },
  "Thundering Pulse": {
    code: 7,
    icon: "7/77/Weapon_Thundering_Pulse",
    rarity: "Gold",
    mainStat: {
      type: "Base ATK",
      value: baseAtkType[46]
    },
    subStats: [
      {
        type: "CRIT DMG",
        value: subStatsType["14.4%"]
      }
    ],
    addBnes: ({ ATTRs, wpRfm }) => {
      ATTRs["ATK%"] += 15 + wpRfm * 5;
    },
    buffs: [
      {
        affect: "self",
        labels: ["Stacks"],
        inputTypes: ["stacks"],
        values: [3],
        addBnes: ({ bonuses, wpRfm, inputs }) => {
          bonuses.NA.DMG += GoldBows["Thundering Pulse"].stackVal({ wpRfm })[
            inputs[0] - 1
          ];
        },
        desc: ({ wpRfm }) =>
          GoldBows["Thundering Pulse"].pasvDescs({ wpRfm })[0].buff
      }
    ],
    stackVal: ({ wpRfm }) => [9 + wpRfm * 3, 18 + wpRfm * 6, 30 + wpRfm * 10],
    pasvName: "Rule by Thunder",
    pasvDescs: ({ wpRfm }) => [
      {
        bonus: (
          <>
            Increases <LGreen>ATK</LGreen> by <Green>{15 + wpRfm * 5}%</Green>{" "}
            and grants the might of the Thunder Emblem.
          </>
        ),
        buff: (
          <>
            At stack levels 1/2/3, the Thunder Emblem increases{" "}
            <LGreen>Normal Attack DMG</LGreen> by{" "}
            <Green>
              {GoldBows["Thundering Pulse"].stackVal({ wpRfm }).join("/")}%
            </Green>
            .
          </>
        ),
        others: (
          <>
            The character will obtain 1 stack of Thunder Emblem in each of the
            following scenarios: Normal Attack deals DMG (stack lasts 5s),
            casting Elemental Skill (stack lasts 10s); Energy is less than 100%
            (stack disappears when Energy is full). Each stack's duration is
            calculated independently.
          </>
        )
      }
    ]
  },
  "Amos' Bow": {
    code: 8,
    icon: "d/de/Weapon_Amos%27_Bow",
    rarity: "Gold",
    mainStat: {
      type: "Base ATK",
      value: baseAtkType[46]
    },
    subStats: [
      {
        type: "ATK%",
        value: subStatsType["10.8%"]
      }
    ],
    addBnes: ({ bonuses, wpRfm }) => {
      if (bonuses) {
        bonuses.NA.DMG += 9 + wpRfm * 3;
        bonuses.CA.DMG += 9 + wpRfm * 3;
      }
    },
    buffs: [
      {
        affect: "self",
        labels: ["Stacks"],
        inputTypes: ["stacks"],
        values: [5],
        addBnes: ({ bonuses, wpRfm, inputs }) => {
          const value = (6 + wpRfm * 2) * inputs[0];
          bonuses.NA.DMG += value;
          bonuses.CA.DMG += value;
        },
        desc: ({ wpRfm }) => GoldBows["Amos' Bow"].pasvDescs({ wpRfm })[0].buff
      }
    ],
    pasvName: "Strong-Willed",
    pasvDescs: ({ wpRfm }) => [
      {
        bonus: (
          <>
            Increases <LGreen>Normal Attack</LGreen> and{" "}
            <LGreen>Charged Attack DMG</LGreen> by{" "}
            <Green>{9 + wpRfm * 3}%</Green>.
          </>
        ),
        buff: (
          <>
            After a <LGreen>Normal or Charged Attack</LGreen> is fired, DMG
            dealt increases by a further <Green>{6 + wpRfm * 2}%</Green> every
            0.1 seconds the arrow is in the air for up to <Green>5</Green>{" "}
            times.
          </>
        )
      }
    ]
  },
  "Elegy for the End": {
    code: 9,
    icon: "a/a5/Weapon_Elegy_for_the_End",
    rarity: "Gold",
    mainStat: {
      type: "Base ATK",
      value: baseAtkType[46]
    },
    subStats: [
      {
        type: "Energy Recharge",
        value: subStatsType["12%"]
      }
    ],
    addBnes: ({ ATTRs, wpRfm }) => {
      ATTRs["Elemental Mastery"] += 45 + wpRfm * 15;
    },
    buffs: [
      {
        affect: "party",
        addBnes: ({ ATTRs, wpRfm }) => {
          ATTRs["Elemental Mastery"] += 75 + wpRfm * 25;
          ATTRs["ATK%"] += 15 + wpRfm * 5;
        },
        desc: ({ wpRfm }) =>
          GoldBows["Elegy for the End"].pasvDescs({ wpRfm })[0].buff
      }
    ],
    pasvName: "The Parting Refrain",
    pasvDescs: ({ wpRfm }) => [
      {
        bonus: (
          <>
            A part of the "Millennial Movement" that wanders amidst the winds.
            Increases <LGreen>Elemental Mastery</LGreen> by{" "}
            <Green>{45 + wpRfm * 15}</Green>. When Elemental Skill or Elemental
            Burst hit opponents, the character gains a Sigil of Remembrance.
            This effect can be triggered once every 0.2s and can be triggered
            even if said character is not on the field. When you possess four
            Sigils of Remembrance, all of them will be consumed and all nearby
            party members will obtain the "Millennial Movement: Farewell Song"
            effect for 12s.
          </>
        ),
        buff: (
          <>
            "Millennial Movement: Farewell Song" increases{" "}
            <LGreen>Elemental Mastery</LGreen> by{" "}
            <Green>{75 + wpRfm * 25}</Green> and increases <LGreen>ATK</LGreen>{" "}
            by <Green>{15 + wpRfm * 5}%</Green>. Once this effect is triggered,
            you will not gain Sigils of Remembrance for 20s.
          </>
        ),
        others: (
          <>
            Of the many effects of the "Millennial Movement", buffs of the same
            type will not stack.
          </>
        )
      }
    ]
  }
};

export default GoldBows;
