import {
  useState,
  useEffect,
  useContext,
  useCallback,
  createContext
} from "react";
import { SetDBCt } from "../../App";
import { StyledCalculator } from "./styles";
import CalcChar from "./CalcChar";
import Modifiers from "./Modifiers";
import SetupManager from "./SetupManager";
import DmgResults from "./DmgResults";
import ImportCtrl from "./ImportCtrl";
import characters from "../../data/characters";
import monsters from "../../data/monsters";
import { elmts } from "../../data/elements";
import { initTarget, initMonster, doInit, doImport } from "./helpers";
import getBuffedStats from "../../calculators/buffedStats";
import getDmg from "../../calculators/damage";
import { deepCopy } from "../../helpers/misc";
import { getFinalInfusion } from "../../helpers/processors";

export const FixedCts = createContext();
export const SelfMCsCt = createContext();
export const WpRackCt = createContext();
export const SubWpMCsCt = createContext();
export const ArtChestCt = createContext();
export const PartiesCt = createContext();
export const ElmtMCsCt = createContext();
export const TargetCt = createContext();
export const ResultCt = createContext();
export const SetupCt = createContext();
export const ManagerCts = createContext();

export default function Calculators(props) {
  const { char, setChar, importedSt, toCalc } = props;
  const [selfMCs, setSelfMCs] = useState([]);
  const [wpRack, setWpRack] = useState([]);
  const [subWpMCs, setSubWpMCs] = useState([]);
  const [artChest, setArtChest] = useState([]);
  const [parties, setParties] = useState([]);
  const [elmtMCs, setElmtMCs] = useState([]);
  const [target, setTarget] = useState(initTarget());
  const [setups, setSetups] = useState([]);
  const [curSetupI, setCurSetupI] = useState(0);
  const [stdSetupI, setStdSetupI] = useState(0);
  const [cmpSetupIs, setCmpSetupIs] = useState([0]);

  const [monster, setMonster] = useState(initMonster());
  useEffect(() => {
    setTarget((prev) => {
      const newTg = deepCopy(prev);
      const { res, addResBn } = monsters[
        monsters.findIndex(({ name }) => name === monster.name)
      ];
      for (let type in res) {
        if (type === "Elemental RES") {
          for (let elmt of elmts) {
            newTg[elmt + " RES"] = res[type];
          }
        } else {
          newTg[type] = res[type];
        }
      }
      if (addResBn) {
        addResBn({ target: newTg, inputs: monster.inputs });
      }
      return newTg;
    });
  }, [monster]);

  const { setImportedSt } = useContext(SetDBCt);
  const [importType, setImportType] = useState(null);
  const initNewSession = useCallback(() => {
    if (importedSt) {
      doInit(importedSt, {
        setChar,
        setSelfMCs,
        setWpRack,
        setSubWpMCs,
        setArtChest,
        setParties,
        setElmtMCs,
        setSetups,
        setTarget,
        setStdSetupI,
        setCurSetupI,
        setCmpSetupIs,
        toCalc
      });
    }
  }, [importedSt]);
  const importSt = useCallback(
    (overrideChar, overrideTg) => {
      if (importedSt) {
        doImport(importedSt, overrideChar, overrideTg, setups.length, {
          setChar,
          setTarget,
          setSelfMCs,
          setWpRack,
          setSubWpMCs,
          setArtChest,
          setParties,
          setElmtMCs,
          setSetups,
          setCurSetupI,
          toCalc
        });
      }
    },
    [importedSt, setups.length]
  );

  useEffect(() => {
    if (importedSt) {
      if (char) {
        if (char.name === importedSt.char.name) {
          if (setups.length === 4) {
            setImportType(2);
          } else {
            let sameInfo = true;
            for (let k in char) {
              if (char[k] !== importedSt.char[k]) {
                sameInfo = false;
                break;
              }
            }
            let sameTg = true;
            for (let type in target) {
              if (target[type] !== importedSt.target[type]) {
                sameTg = false;
                break;
              }
            }
            if (sameInfo && sameTg) {
              importSt(false, false);
            } else {
              let code = 3;
              if (!sameInfo) code = code * 10;
              if (!sameTg) code = code * 10 + 1;
              setImportType(code);
            }
          }
        } else {
          setImportType(1);
        }
      } else {
        importSt(true, true);
      }
    }
  }, [importedSt]);

  let charData;
  const allATTRs = [];
  const allArtSBnes = [];
  const allRxnBnes = [];
  const allInfusion = [];
  const allDmg = [];
  if (char) {
    charData = characters[char.name];
    for (let i = 0; i < setups.length; i++) {
      const finalInfusion = getFinalInfusion(
        char,
        selfMCs[i].BCs,
        charData.vision,
        parties[i]
      );
      allInfusion.push(finalInfusion);
      const { ATTRs, artSBnes, rxnBnes, bonuses } = getBuffedStats(
        charData,
        char,
        selfMCs[i].BCs,
        wpRack[i],
        subWpMCs[i].BCs,
        artChest[i],
        elmtMCs[i].resonance,
        parties[i],
        finalInfusion
      );
      allATTRs.push(ATTRs);
      allArtSBnes.push(artSBnes);
      allRxnBnes.push(rxnBnes);
      allDmg.push(
        getDmg(
          deepCopy(char),
          selfMCs[i],
          parties[i],
          artChest[i].subDCs,
          ATTRs,
          bonuses,
          rxnBnes,
          finalInfusion,
          elmtMCs[i],
          target
        )
      );
    }
  }

  return (
    <FixedCts.Provider
      value={{
        setSelfMCs,
        setWpRack,
        setSubWpMCs,
        setArtChest,
        setParties,
        setElmtMCs,
        setTarget,
        setMonster,
        setSetups,
        setStdSetupI,
        setCurSetupI,
        setCmpSetupIs
      }}
    >
      <SelfMCsCt.Provider value={selfMCs}>
        <WpRackCt.Provider value={wpRack}>
          <SubWpMCsCt.Provider value={subWpMCs}>
            <ArtChestCt.Provider value={artChest}>
              <PartiesCt.Provider value={parties}>
                <ElmtMCsCt.Provider value={elmtMCs}>
                  <TargetCt.Provider value={{ target, monster }}>
                    <ResultCt.Provider
                      value={{
                        allInfusion,
                        allATTRs,
                        allArtSBnes,
                        allRxnBnes,
                        allDmg
                      }}
                    >
                      <SetupCt.Provider value={curSetupI}>
                        <StyledCalculator>
                          <div className="flex">
                            <CalcChar
                              char={char}
                              charData={charData}
                              setChar={setChar}
                            />
                            <Modifiers char={char} />
                            <ManagerCts.Provider
                              value={{ setups, stdSetupI, cmpSetupIs }}
                            >
                              <SetupManager char={char} charData={charData} />
                              <DmgResults char={char} />
                            </ManagerCts.Provider>
                            {importType ? (
                              <ImportCtrl
                                type={importType}
                                char={char}
                                importedSt={importedSt}
                                initNewSession={initNewSession}
                                importSt={importSt}
                                close={() => {
                                  setImportType(null);
                                  setImportedSt(null);
                                }}
                              />
                            ) : null}
                          </div>
                        </StyledCalculator>
                      </SetupCt.Provider>
                    </ResultCt.Provider>
                  </TargetCt.Provider>
                </ElmtMCsCt.Provider>
              </PartiesCt.Provider>
            </ArtChestCt.Provider>
          </SubWpMCsCt.Provider>
        </WpRackCt.Provider>
      </SelfMCsCt.Provider>
    </FixedCts.Provider>
  );
}
