import styled from "styled-components";
import { useState, useEffect } from "react";
import { Box } from "../../styledCpns/Layout";
import { Text } from "../../styledCpns/DataDisplay";
import OneSetup from "../Calculator/DmgResults/OneSetup";
import { DmgTable } from "../CommonCpns/DmgTable";
import { getNamesForTables } from "../../helpers/misc";
import { colorCodes } from "../../data/misc";

const StyledDiv = styled.div`
  min-width: 21.75rem;
  border-radius: 0.5rem;
  background-color: ${colorCodes.cardBg3};
`;

export default function DmgResults({ setup, dmg }) {
  const [closed, setClosed] = useState([]);
  const ID = setup?.ID ? setup.ID : "";
  useEffect(() => {
    setClosed([]);
  }, [ID]);

  if (!setup) {
    return <StyledDiv className="ml-3 full-h" />;
  }
  const namesForTables = getNamesForTables(setup.char);
  return (
    <StyledDiv className="ml-3 px-3 pt-2 pb-3 flex-col">
      <Text align="center">{setup.name}</Text>
      <Box className="mt-2 grow-1 hide-sb">
        {namesForTables.map((name, i) => {
          const stdValues = dmg[name.main];
          return (
            <DmgTable
              key={i}
              index={i}
              heading={name.main}
              closed={closed}
              setClosed={setClosed}
              mb={i === namesForTables.length - 1 ? "" : " mb-3"}
            >
              <OneSetup subNames={name.subs} stdValues={stdValues} />
            </DmgTable>
          );
        })}
      </Box>
    </StyledDiv>
  );
}
