import characters from "../../../data/characters";
import weapons from "../../../data/weapons";
import artifactBuffs from "../../../data/artifacts/artifactBuffs";
import { artTypes } from "../../../data/misc";
import { getArtSets } from "../../../helpers/processors";
import { initWeapon } from "../../Picker/WpPicker";

function getArtifact(artIDs, myArts) {
  const pieces = [];
  for (let type in artIDs) {
    const index = artTypes.indexOf(type);
    if (artIDs[type]) {
      const { ID, user, ...artP } = myArts.find(
        (piece) => piece.ID === artIDs[type]
      );
      pieces[index] = artP;
    } else {
      pieces[index] = null;
    }
  }
  return { pieces, sets: getArtSets(pieces) };
}

export function parseData(unit, myWeapons, myArts) {
  const {
    name,
    weaponID,
    artIDs = {
      flower: null,
      plume: null,
      sands: null,
      goblet: null,
      circlet: null
    }
  } = unit;
  const char = {
    name,
    level: unit.level || "1/20",
    ascension: unit.ascension || 0,
    "Normal Attack": unit["Normal Attack"] || 1,
    "Elemental Skill": unit["Elemental Skill"] || 1,
    "Elemental Burst": unit["Elemental Burst"] || 1,
    constellation: unit.constellation || 0
  };
  let wp;
  if (weaponID) {
    const existedWp = myWeapons.find((wp) => wp.ID === weaponID);
    if (existedWp) {
      const { ID, user, ...weapon } = existedWp;
      wp = {
        ...weapon,
        BCs: getMainWpBCs(existedWp.type, existedWp.name)
      };
    }
  } else {
    const newWp = initWeapon({ type: characters[name].weapon });
    wp = { ...newWp, BCs: getMainWpBCs(newWp.type, newWp.name) };
  }
  let art;
  const { pieces, sets } = getArtifact(artIDs, myArts);
  const bnLv = sets[0]?.bonusLv === 1;
  art = {
    pieces,
    sets,
    BCs: getMainArtBCs(bnLv ? sets[0].name : null),
    subBCs: getSubArtBCs(bnLv ? sets[0].name : null),
    subDCs: getSubArtDCs()
  };
  return { char, wp, art };
}

function getSelfBCs(charName) {
  const result = [];
  const { buffs } = characters[charName];
  if (buffs) {
    buffs.forEach((buff, index) => {
      if (buff.affect !== "teammates") {
        const node = { activated: false, index };
        if (buff.selfLabels) {
          node.inputs = [...buff.inputs];
        }
        result.push(node);
      }
    });
  }
  return result;
}

// v2.3 - debuffs need inputs:
// From self: AnemoMC C6 & Venti C6
// Not from self: Eula E & Shenhe Q
function getInputs(labels, inputTypes) {
  const result = [];
  labels.forEach((_, i) => {
    const type = inputTypes[i];
    if (type === "absorption") {
      result.push("Pyro");
    } else if (type === "text") {
      // level
      result.push(1);
    }
  });
  return result;
}

// also used in ./SetupManager/helpers
export function getCharDCs(charName, fromSelf) {
  const result = [];
  const { debuffs } = characters[charName];
  if (debuffs) {
    debuffs.forEach((debuff, index) => {
      if (!fromSelf && debuff.affect === "self") {
        return;
      }
      const node = { activated: false, index };
      const { selfLabels, labels, inputTypes } = debuff;
      if (fromSelf && selfLabels) {
        node.inputs = getInputs(selfLabels, inputTypes);
      } else if (!fromSelf && labels) {
        node.inputs = getInputs(labels, inputTypes);
      }
      result.push(node);
    });
  }
  return result;
}

// also used in decode & processors/adjustBD
export function initSelfMCs(charName) {
  return {
    BCs: getSelfBCs(charName),
    DCs: getCharDCs(charName, true)
  };
}

// also used in decode
export function getMainWpBCs(type, name) {
  let result = [];
  const { buffs } = weapons[type][name];
  if (buffs) {
    buffs.forEach((buff, index) => {
      if (buff.affect !== "teammates") {
        const node = { activated: false, index };
        if (buff.inputTypes) {
          node.inputs = [];
          buff.inputTypes.forEach((type, j) => {
            if (type === "stacks") {
              node.inputs.push(1);
            } else if (type === "check") {
              node.inputs.push(false);
            } else if (type === "choices") {
              node.inputs.push(buff.values[j][0]);
            }
          });
        }
        result.push(node);
      }
    });
  }
  return result;
}

export function initSubWpMCs() {
  return { BCs: {}, DCs: {} };
}

// also used in ./SetupManaget/SectionArts & decode
export function getMainArtBCs(setName) {
  const result = [];
  if (artifactBuffs[setName]) {
    artifactBuffs[setName].forEach((buff, index) => {
      if (buff.affect !== "teammates") {
        const node = { activated: false, index };
        if (buff.inputTypes) {
          node.inputs = [];
          buff.inputTypes.forEach((type, j) => {
            if (type === "stacks") {
              node.inputs.push(1);
            } else if (type === "crystallize") {
              node.inputs.push("Pyro");
            }
          });
        }
        result.push(node);
      }
    });
  }
  return result;
}

// also used in decode
// related to ./SetupManager/SectionArts => setArtPiece()
export function getSubArtBCs(mainSet) {
  const result = [];
  for (let set in artifactBuffs) {
    if (set !== mainSet) {
      artifactBuffs[set].forEach((buff, index) => {
        if (buff.affect !== "self") {
          const node = { set, activated: false, index };
          if (buff.inputTypes) {
            node.inputs = [];
            buff.inputTypes.forEach((type, j) => {
              if (type === "crystallize") {
                node.inputs.push("Pyro");
              }
            });
          }
          result.push(node);
        }
      });
    }
  }
  return result;
}

// also used in decode
export function getSubArtDCs() {
  return [
    { set: "Viridescent Venerer", activated: false, index: 0, inputs: ["Pyro"] }
  ];
}

// also used in decode
export function initParty() {
  return [
    { name: null, BCs: [], DCs: [] },
    { name: null, BCs: [], DCs: [] },
    { name: null, BCs: [], DCs: [] }
  ];
}

export function initElmtMCs() {
  return {
    naAmpRxn: null,
    ampRxn: null,
    superconduct: false,
    resonance: []
  };
}
