import styled from "styled-components";
import { useState } from "react";
import { Text } from "../../../styledCpns/DataDisplay";
import { Box } from "../../../styledCpns/Layout";
import { IconBtn } from "../../../styledCpns/Inputs";
import { Card } from "../../../styledCpns/Database";
import Members from "./Members";
import Gears from "./Gears";
import TopBar from "./TopBar";
import characters from "../../../data/characters";
import { colorCodes } from "../../../data/misc";

const StyledDiv = styled.div`
  border-left: 2px solid ${colorCodes.cardBg3};
`;

export default function Setup(props) {
  const { setup, remove, allIDs } = props;
  const { char } = props.setup;
  const isOg = props.setup.type === "original";
  const charData = characters[char.name];
  return (
    <Box
      className="mb-3 px-2 pt-3 pb-2"
      borderRadius="0.5rem"
      bgColor="cardBg3"
      boxShadow={props.chosen ? "0 0 5px 3px green" : "var(--cmn-shadow)"}
      onClick={props.choose}
    >
      <div className="px-2 flex justify-between">
        <Box className="flex align-center" width="24rem">
          {!isOg && <LinkButton uncombine={remove} />}
          <Text className="ellipsis" variant="h3" color="orange">
            {props.stName || setup.name}
          </Text>
        </Box>
        <div className="flex pb-3">
          <IconBtn
            icon="fas fa-wrench"
            className="mr-4 p-2 glow-on-hover"
            variant="positive"
            onClick={props.edit}
          />
          <IconBtn
            icon="fas fa-share-alt"
            className="mr-4 p-2 glow-on-hover"
            variant="neutral"
            onClick={props.share}
          />
          {isOg ? (
            <IconBtn
              icon="fas fa-trash-alt"
              className="glow-on-hover"
              variant="negative"
              onClick={remove}
            />
          ) : (
            <AddButton
              addable={Object.keys(allIDs).length < 4}
              add={props.add}
            />
          )}
        </div>
      </div>
      <div className="flex">
        <Card className="mr-1 flex">
          <Members
            char={char}
            charData={charData}
            isOg={isOg}
            party={setup.party}
            buildTmSt={props.buildTmSt}
            allIDs={allIDs}
            switchMember={props.switchMember}
          />
          <StyledDiv className="ml-3 pl-3">
            <TopBar result={props.result} setup={setup} />
            <Gears weapon={setup.weapon} artPieces={setup.art.pieces} />
          </StyledDiv>
        </Card>
      </div>
    </Box>
  );
}

function AddButton({ addable, add }) {
  return (
    <IconBtn
      icon="fas fa-plus"
      className={addable ? "glow-on-hover" : "unavailable"}
      variant="neutral"
      onClick={() => {
        if (addable) add();
      }}
    />
  );
}

const StyledBtn = styled.button`
  border-radius: 50%;
  width: 2rem;
  height: 2rem;
  color: white;
  font-size: 1rem;
  &.link {
    background-color: transparent;
  }
  &.unlink {
    background-color: ${colorCodes.red};
  }
`;

function LinkButton({ uncombine }) {
  if (window.innerWidth <= 1050) {
    return <MobileVer uncombine={uncombine} />;
  }
  return <PCVer uncombine={uncombine} />;
}

function MobileVer(props) {
  return (
    <IconBtn
      icon="fas fa-unlink"
      className="mr-2 glow-on-hover"
      variant="negative"
      onClick={props.uncombine}
    />
  );
}

function PCVer(props) {
  const [hovered, setHovered] = useState(false);
  return (
    <StyledBtn
      className={"mr-2 flex-center pointer " + (hovered ? "unlink" : "link")}
      onMouseEnter={() => setHovered(true)}
      onMouseLeave={() => setHovered(false)}
      onClick={props.uncombine}
    >
      <i className={"fas fa-" + (hovered ? "unlink" : "link")} />
    </StyledBtn>
  );
}
