import { LGreen, Green } from "../../../styledCpns/DataDisplay";
import { baseAtkType, subStatsType } from "../config";
import { LiyueSeries } from "../series";

const GoldClaymores = {
  "Song of Broken Pines": {
    code: 53,
    icon: "d/dd/Weapon_Song_of_Broken_Pines",
    rarity: "Gold",
    mainStat: {
      type: "Base ATK",
      value: baseAtkType[49]
    },
    subStats: [
      {
        type: "Physical DMG Bonus",
        value: subStatsType["4.5%"]
      }
    ],
    addBnes: ({ ATTRs, wpRfm }) => {
      ATTRs["ATK%"] += 12 + wpRfm * 4;
    },
    buffs: [
      {
        affect: "party",
        addBnes: ({ ATTRs, wpRfm }) => {
          ATTRs["Normal ATK SPD"] += 9 + wpRfm * 3;
          ATTRs["ATK%"] += 15 + wpRfm * 5;
        },
        desc: ({ wpRfm }) =>
          GoldClaymores["Song of Broken Pines"].pasvDescs({ wpRfm })[0].buff
      }
    ],
    pasvName: "Rebel's Banner Hymn",
    pasvDescs: ({ wpRfm }) => [
      {
        bonus: (
          <>
            A part of the "Millennial Movement" that wanders amidst the winds.
            Increases <LGreen>ATK</LGreen> by <Green>{12 + wpRfm * 4}%</Green>,
            and when Normal or Charged Attacks hit opponents, the character
            gains a Sigil of Whispers. This effect can be triggered once every
            0.3s. When you possess four Sigils of Whispers, all of them will be
            consumed and all nearby party members will obtain the "Millennial
            Movement: Banner-Hymn" effect for 12s.
          </>
        ),
        buff: (
          <>
            "Millennial Movement: Banner-Hymn" increases{" "}
            <LGreen>Normal ATK SPD</LGreen> by <Green>{9 + wpRfm * 3}%</Green>{" "}
            and increases <LGreen>ATK</LGreen> by{" "}
            <Green>{15 + wpRfm * 5}%</Green>. Once this effect is triggered, you
            will not gain Sigils of Whispers for 20s.
          </>
        ),
        others: (
          <>
            Of the many effects of the "Millennial Movement", buffs of the same
            type will not stack.
          </>
        )
      }
    ]
  },
  "Skyward Pride": {
    code: 54,
    icon: "0/0b/Weapon_Skyward_Pride",
    rarity: "Gold",
    mainStat: {
      type: "Base ATK",
      value: baseAtkType[48]
    },
    subStats: [
      {
        type: "Energy Recharge",
        value: subStatsType["8%"]
      }
    ],
    addBnes: ({ bonuses, wpRfm }) => {
      if (bonuses) bonuses.AllDmg += 6 + wpRfm * 2;
    },
    pasvProcD: [
      {
        name: "Vacuum Blade",
        baseSType: "ATK",
        multiplier: [0, 0.8, 1, 1.2, 1.4, 1.6],
        dmgType: "Physical"
      }
    ],
    pasvName: "Sky-ripping Dragon Spine",
    pasvDescs: ({ wpRfm }) => [
      {
        bonus: (
          <>
            Increases <LGreen>all DMG</LGreen> by{" "}
            <Green>{6 + wpRfm * 2}%</Green>. After using an Elemental Burst,
            Normal or Charged Attack, on hit, creates a vacuum blade that does{" "}
            <Green>{60 + wpRfm * 20}%</Green> of <LGreen>ATK</LGreen> as DMG to
            opponents along its path. Lasts for 20s or 8 vacuum blades.
          </>
        )
      }
    ]
  },
  "The Unforged": {
    code: 55,
    icon: "f/f7/Weapon_The_Unforged",
    rarity: "Gold",
    mainStat: {
      type: "Base ATK",
      value: baseAtkType[46]
    },
    subStats: [
      {
        type: "ATK%",
        value: subStatsType["10.8%"]
      }
    ],
    ...LiyueSeries
  },
  "Wolf's Gravestone": {
    code: 56,
    icon: "4/4f/Weapon_Wolf%27s_Gravestone",
    rarity: "Gold",
    mainStat: {
      type: "Base ATK",
      value: baseAtkType[46]
    },
    subStats: [
      {
        type: "ATK%",
        value: subStatsType["10.8%"]
      }
    ],
    addBnes: ({ ATTRs, wpRfm }) => {
      ATTRs["ATK%"] += 15 + wpRfm * 5;
    },
    buffs: [
      {
        affect: "party",
        addBnes: ({ ATTRs, wpRfm }) => {
          ATTRs["ATK%"] += 30 + wpRfm * 10;
        },
        desc: ({ wpRfm }) =>
          GoldClaymores["Wolf's Gravestone"].pasvDescs({ wpRfm })[0].buff
      }
    ],
    pasvName: "Wolfish Tracker",
    pasvDescs: ({ wpRfm }) => [
      {
        bonus: (
          <>
            Increases <LGreen>ATK</LGreen> by <Green>{15 + wpRfm * 5}%</Green>.
          </>
        ),
        buff: (
          <>
            On hit, attacks against opponents with less than 30% HP increase all
            party members' <LGreen>ATK</LGreen> by{" "}
            <Green>{30 + wpRfm * 10}%</Green> for 12s. Can only occur once every
            30s.
          </>
        )
      }
    ]
  },
  "Redhorn Stonethresher": {
    code: 57,
    icon: "d/d4/Weapon_Redhorn_Stonethresher",
    rarity: "Gold",
    mainStat: {
      type: "Base ATK",
      value: baseAtkType["44b"]
    },
    subStats: [
      {
        type: "CRIT DMG",
        value: subStatsType["19.2%"]
      }
    ],
    addBnes: ({ ATTRs, wpRfm }) => {
      ATTRs["DEF%"] += 21 + wpRfm * 7;
    },
    addFinalBnes: ({ bonuses, wpRfm, ATTRs }) => {
      if (bonuses) {
        const value = Math.round((ATTRs.DEF * (30 + wpRfm * 10)) / 100);
        bonuses.NA.Flat += value;
        bonuses.CA.Flat += value;
      }
    },
    pasvName: "Gokadaiou Otogibanashi",
    pasvDescs: ({ wpRfm }) => [
      {
        bonus: (
          <>
            <LGreen>DEF</LGreen> is increased by{" "}
            <Green>{21 + wpRfm * 7}%</Green>.{" "}
            <LGreen>Normal and Charged Attack DMG</LGreen> is increased by{" "}
            <Green>{30 + wpRfm * 10}%</Green> of <LGreen>DEF</LGreen>.
          </>
        )
      }
    ]
  }
};

export default GoldClaymores;
