import { useContext } from "react";
import { FixedCts, SetupCt } from "../../index";
import { Section, Setter, SetterSelect } from "../CmnCpns";
import weapons from "../../../../data/weapons";
import { deepCopy } from "../../../../helpers/misc";

export default function WpBuffsSub({ subBCs }) {
  const curSetupI = useContext(SetupCt);
  const { setSubWpMCs } = useContext(FixedCts);

  const content = [];
  for (let type in subBCs) {
    subBCs[type].forEach((ctrl, bcIndex) => {
      const { activated, name } = ctrl;
      const wpData = weapons[type][name];
      content.push(
        <Section
          key={type + bcIndex}
          checked={activated}
          handleCheck={() =>
            setSubWpMCs((prev) => {
              const MCs = deepCopy(prev);
              MCs[curSetupI].BCs[type][bcIndex].activated = !activated;
              return MCs;
            })
          }
          heading={name}
          desc={wpData.pasvDescs({ wpRfm: ctrl.refinement })[0].buff}
          setters={
            <SetterSection
              buff={wpData.buffs[ctrl.index]}
              ctrl={ctrl}
              wpType={type}
              bcIndex={bcIndex}
              curSetupI={curSetupI}
              setSubWpMCs={setSubWpMCs}
            />
          }
        />
      );
    });
  }
  return content;
}

function SetterSection(props) {
  const { buff, ctrl, wpType, bcIndex, curSetupI, setSubWpMCs } = props;
  const xtraSetters = [];

  if (buff.labels) {
    buff.labels.forEach((label, i) => {
      const type = buff.inputTypes[i];
      let input;
      if (type === "choices") {
        input = (
          <SetterSelect
            value={ctrl.inputs[i]}
            onChange={(e) =>
              setSubWpMCs((prev) => {
                const MCs = deepCopy(prev);
                MCs[curSetupI].BCs[wpType][bcIndex].inputs[i] = e.target.value;
                return MCs;
              })
            }
          >
            {buff.values[i].map((opt) => (
              <option key={opt}>{opt}</option>
            ))}
          </SetterSelect>
        );
      }
      xtraSetters.push(
        <Setter key={i}>
          <p className="label">{label}</p>
          {input}
        </Setter>
      );
    });
  }
  return (
    <>
      <Setter>
        <p className="label">Refinement</p>
        <SetterSelect
          value={ctrl.refinement}
          onChange={(e) =>
            setSubWpMCs((prev) => {
              const MCs = deepCopy(prev);
              MCs[curSetupI].BCs[wpType][bcIndex].refinement = +e.target.value;
              return MCs;
            })
          }
        >
          {[1, 2, 3, 4, 5].map((opt) => (
            <option key={opt}>{opt}</option>
          ))}
        </SetterSelect>
      </Setter>
      {xtraSetters}
    </>
  );
}
