import { Text } from "../../../../styledCpns/DataDisplay";
import { NoContent } from "../../../Calculator/Modifiers/CmnCpns";
import { ExpandableList } from "../../../CommonCpns";
import ModReader from "./ModReader";
import characters from "../../../../data/characters";
import artifactDebuffs from "../../../../data/artifacts/artifactDebuffs";
import { elmtDebuffDesc, rsnNames } from "../../../../data/elements";

export default function Debuffs(props) {
  const headingList = ["Resonance & Reactions", "Self", "Party", "Artifacts"];
  return (
    <ExpandableList
      headingList={headingList}
      contentList={headingList.map((cate, i) => (
        <Inner key={i} cate={cate} {...props} />
      ))}
    />
  );
}

function Inner(props) {
  const { cate, char, party } = props;
  const content = [];
  if (cate === "Resonance & Reactions") {
    const { superconduct, resonance } = props.elmtMCs;
    if (superconduct) {
      content.push(
        <ModReader
          key="sc"
          heading="Superconduct"
          desc={elmtDebuffDesc.Superconduct}
        />
      );
    }
    const geoName = rsnNames.Geo;
    const geoRsn = resonance.find((rsn) => rsn.name === geoName);
    if (geoRsn && geoRsn.activated) {
      content.push(
        <ModReader key="geo" heading={geoName} desc={elmtDebuffDesc[geoName]} />
      );
    }
  } else if (cate === "Self") {
    props.selfDCs.forEach((ctrl, i) => {
      const debuff = characters[char.name].debuffs[ctrl.index];
      if (ctrl.activated && debuff.isGranted(char)) {
        content.push(
          <ModReader
            key={"self" + i}
            heading={debuff.src}
            desc={debuff.desc({
              fromSelf: true,
              char,
              party
            })}
          />
        );
      }
    });
  } else if (cate === "Party") {
    for (let { name, DCs } of party) {
      const tmData = characters[name];
      if (name && DCs.some((ctrl) => ctrl.activated)) {
        content.push(
          <Text
            key={name}
            className="pt-2 mb-m1"
            variant="h6"
            fontWeight={700}
            color={tmData.vision}
            align="center"
          >
            {name.toUpperCase()}
          </Text>
        );
      }
      DCs.forEach((ctrl, i) => {
        if (ctrl.activated) {
          const debuff = tmData.debuffs[ctrl.index];
          const { inputs } = ctrl;
          content.push(
            <ModReader
              key={name + i}
              heading={debuff.src}
              desc={debuff.desc({
                fromSelf: false,
                char: props.char,
                inputs
              })}
              labels={debuff.labels}
              inputTypes={debuff.inputTypes}
              inputs={inputs}
            />
          );
        }
      });
    }
  } else if (cate === "Artifacts") {
    props.artSubDCs.forEach((ctrl, i) => {
      if (ctrl.activated) {
        const { set } = ctrl;
        const debuff = artifactDebuffs[set][ctrl.index];
        content.push(
          <ModReader
            key={set + i}
            heading={set}
            desc={debuff.desc}
            labels={debuff.labels}
            inputTypes={debuff.inputTypes}
            inputs={ctrl.inputs}
          />
        );
      }
    });
  }
  return content.length ? content : <NoContent type="buffs" />;
}
