import styled from "styled-components";
import { IconBtn } from "../../styledCpns/Inputs";
import { colorCodes } from "../../data/misc";

export const StyledInner = styled.div`
  width: 90%;
  max-width: 25rem;
  border-radius: 0.5rem;
  background-color: ${colorCodes.defaultBg};
  color: black;
  .option {
    padding: 0.5rem 1rem;
  }
  .option:first-child {
    border-bottom: 1px solid black;
  }
  .option:hover {
    background-color: ${colorCodes.green};
    cursor: default;
  }
`;

export const CloseBtn = (props) => (
  <IconBtn
    className="ml-auto mr-2 mb-2"
    icon="fas fa-times"
    size="1.75rem"
    variant="negative"
    {...props}
  />
);

export const Divider = styled.div`
  width: 100%;
  border-bottom: 2px solid black;
`;
