import {
  ColoredSpan,
  LGreen,
  Green,
  Red
} from "../../../../styledCpns/DataDisplay";
import { TalentLvMults } from "../../config";
import actvTalents from "./talents";
import { getEBcost } from "../../../../helpers/calculators";
import { getXtraTalentLv } from "../../../../calculators/helpers";

const MusouIsshinBnMult = [
  0,
  0.73,
  0.78,
  0.84,
  0.91,
  0.96,
  1.02,
  1.09,
  1.16,
  1.23,
  1.31,
  1.38,
  1.45,
  1.54
];

function countResolve(energyCost, level) {
  return (
    Math.round(energyCost * Math.min(Math.ceil(14.5 + level * 0.5), 20)) / 100
  );
}

export const buffs = [
  {
    src: "Elemental Skill",
    desc: ({ toSelf, char, inputs, party }) => (
      <>
        Eye of Stormy Judgment increases <LGreen>Elemental Burst DMG</LGreen>{" "}
        based on the Energy Cost of the Elemental Burst during the Eye's
        duration.{" "}
        <Red>
          Elemental Burst DMG: {buffs[0].bnValue(toSelf, char, inputs, party)}
          %.
        </Red>
      </>
    ),
    isGranted: () => true,
    affect: "party",
    labels: ["Elemental Skill Level"],
    inputs: [1],
    inputTypes: ["text"],
    maxs: [13],
    addBnes: ({ bonuses, inputs, toSelf, char, party }) => {
      bonuses.EB.DMG += buffs[0].bnValue(toSelf, char, inputs, party);
    },
    bnValue: (toSelf, char, inputs, party) => {
      const level = toSelf
        ? char["Elemental Skill"] + getXtraTalentLv(actvTalents[1], char, party)
        : inputs[0];
      const bonus = getEBcost(char.name) * Math.min(0.21 + level / 100, 0.3);
      return Math.round(bonus * 10) / 10;
    }
  },
  {
    src: "Elemental Burst",
    desc: ({ char, ATTRs, charBCs, party }) => {
      const [resolve, initBn, atkBn] = buffs[1].bnValue(char, charBCs, party);
      return (
        <>
          • When nearby party members (excluding the Raiden Shogun) use their
          Elemental Bursts, she will build up Resolve stacks based on the total
          Energy Cost.
          <br />• While in Musou Isshin state, Normal, Charged, and Plunging
          Attacks deal <ColoredSpan color="Electro">Electro DMG</ColoredSpan> in
          form of <Red>Elemental Burst DMG</Red>
          <br />• The <LGreen>DMG</LGreen> dealt by{" "}
          <LGreen>Musou no Hitotachi</LGreen> and{" "}
          <LGreen>Musou Isshin's attacks</LGreen> will be increased based on the{" "}
          <LGreen>number of Chakra Desiderata's Resolve stacks</LGreen> (up to
          60) consumed when this skill is used.{" "}
          <Red>
            Total Resolve: {resolve}. Musou no Hitotachi DMG Bonus:{" "}
            {Math.round((ATTRs.ATK * initBn) / 100)} (base). Musou Isshin's
            attacks DMG Bonus: {Math.round((ATTRs.ATK * atkBn) / 100)} (base).
          </Red>
        </>
      );
    },
    isGranted: () => true,
    affect: "self",
    selfLabels: ["Total Energy Spent"],
    inputs: [0],
    inputTypes: ["text"],
    maxs: [999],
    bnValue: (char, charBCs, party) => {
      const lv =
        char["Elemental Burst"] + getXtraTalentLv(actvTalents[2], char, party);
      let stacks = countResolve(charBCs[1].inputs[0], lv);
      if (buffs[3].isGranted(char) && charBCs[3].activated) {
        stacks += buffs[3].bnValue(char, charBCs);
      }
      stacks = Math.min(Math.round(stacks * 100) / 100, 60);
      return [
        stacks,
        3.89 * TalentLvMults[2][lv] * stacks,
        MusouIsshinBnMult[lv] * stacks
      ];
    },
    canInfuse: () => true,
    infuseRange: ["NA", "CA", "PA"],
    canBeOverrided: false
  },
  {
    src: "Ascension 4 Passive Talent",
    desc: ({ ATTRs }) => (
      <>
        Each <Green>1%</Green> above 100% <LGreen>Energy Recharge</LGreen> that
        the Raiden Shogun possesses grants her <Green>0.4%</Green>{" "}
        <LGreen>Electro DMG Bonus</LGreen>.{" "}
        <Red>Electro DMG Bonus: {buffs[2].bnValue(ATTRs)}%.</Red>
      </>
    ),
    isGranted: (char) => char.ascension >= 4,
    affect: "self",
    addBnes: ({ ATTRs }) => {
      ATTRs["Electro DMG Bonus"] += buffs[2].bnValue(ATTRs);
    },
    bnValue: (ATTRs) => Math.round((ATTRs["Energy Recharge"] - 100) * 4) / 10
  },
  {
    src: "Constellation 1",
    desc: ({ char, charBCs }) => (
      <>
        When <ColoredSpan color="Electro">Electro</ColoredSpan> characters use
        their Elemental Bursts, the <LGreen>Resolve</LGreen> gained is increased
        by <Green>80%</Green>. When characters of other Elemental Types use
        their Elemental Bursts, the <LGreen>Resolve</LGreen> gained is increased
        by <Green>20%</Green>.{" "}
        <Red>Extra Resolve: {buffs[3].bnValue(char, charBCs)}.</Red>
      </>
    ),
    isGranted: (char) => char.constellation >= 1,
    affect: "self",
    selfLabels: ["Energy Spent by Electro Characters (part of total)"],
    inputs: [0],
    inputTypes: ["text"],
    maxs: [999],
    bnValue: (char, [_, EBctrl, __, C1ctrl]) => {
      const otherEC = EBctrl.inputs[0] - C1ctrl.inputs[0];
      if (otherEC < 0) return 0;
      const lv = char["Elemental Burst"] + (char.constellation >= 3 ? 3 : 0);
      return (
        Math.round(
          countResolve(C1ctrl.inputs[0], lv) * 8 + countResolve(otherEC, lv) * 2
        ) / 10
      );
    }
  },
  {
    src: "Constellation 4",
    desc: () => (
      <>
        When the Musou Isshin state expires, all nearby party members gain{" "}
        <Green>30%</Green> <LGreen>bonus ATK</LGreen> for 10s.
      </>
    ),
    isGranted: (char) => char.constellation >= 4,
    affect: "party",
    addBnes: ({ ATTRs }) => {
      ATTRs["ATK%"] += 30;
    }
  }
];

export const debuffs = [
  {
    src: "Constellation 2",
    desc: () => (
      <>
        Musou no Hitotachi and all Raiden Shogun's attacks under Musou Isshin
        state ignore <Green>60%</Green> <LGreen>DEF</LGreen> of the opponents.
      </>
    ),
    isGranted: (char) => char.constellation >= 2,
    affect: "self",
    addPntes: ({ rdMult }) => {
      rdMult.DEF += 60;
    }
  }
];
