import { ColoredSpan, LGreen, Green } from "../../../../styledCpns/DataDisplay";

export const buffs = [
  {
    src: "Ascension 1 Passive Talent",
    desc: () => (
      <>
        After recasting Stellar Restoration while a Lightning Stiletto is
        present, Keqing's weapon gains an{" "}
        <ColoredSpan color="Electro">Electro Infusion</ColoredSpan> for 5s.
      </>
    ),
    isGranted: (char) => char.ascension >= 1,
    affect: "self",
    canInfuse: () => true,
    infuseRange: ["NA", "CA", "PA"],
    canBeOverrided: true
  },
  {
    src: "Ascension 4 Passive Talent",
    desc: () => (
      <>
        When casting Starward Sword, Keqing's <LGreen>CRIT Rate</LGreen> and{" "}
        <LGreen>Energy Recharge</LGreen> is increased by <Green>15%</Green>.
        This effect lasts for 8s.
      </>
    ),
    isGranted: (char) => char.ascension >= 4,
    affect: "self",
    addBnes: ({ ATTRs }) => {
      ATTRs["CRIT Rate"] += 15;
      ATTRs["Energy Recharge"] += 15;
    }
  },
  {
    src: "Constellation 4",
    desc: () => (
      <>
        For 10s after Keqing triggers an Electro-related Elemental Reaction, her{" "}
        <LGreen>ATK</LGreen> is increased by <Green>25%</Green>.
      </>
    ),
    isGranted: (char) => char.constellation >= 4,
    affect: "self",
    addBnes: ({ ATTRs }) => {
      ATTRs["ATK%"] += 25;
    }
  },
  {
    src: "Constellation 6",
    desc: () => (
      <>
        When initiating a Normal Attack, a Charged Attack, Elemental Skill or
        Elemental Burst, Keqing gains a <Green>6%</Green>{" "}
        <LGreen>Electro DMG Bonus</LGreen> for 8s. Effects triggered by Normal
        Attacks, Charged Attacks, Elemental Skills and Elemental Bursts are
        considered independent entities.
      </>
    ),
    isGranted: (char) => char.constellation >= 6,
    affect: "self",
    selfLabels: ["Stacks"],
    inputs: [1],
    inputTypes: ["select"],
    maxs: [4],
    addBnes: ({ ATTRs, inputs }) => {
      ATTRs["Electro DMG Bonus"] += 6 * inputs[0];
    }
  }
];
