import { useState, useContext, useRef } from "react";
import { MyCharsCt, SetDBCt } from "../../App";
import { Box } from "../../styledCpns/Layout";
import Picker from "../Picker";
import TopBar from "./TopBar";
import TopBarSmall from "./TopBarSmall";
import Info from "./Info";
import { initWeapon } from "../Picker/WpPicker";
import { deepCopy } from "../../helpers/misc";
import characters from "../../data/characters";

export default function MyCharacters() {
  const [chosenI, setChosenI] = useState(0);
  const [pickerOn, setPickerOn] = useState(false);
  const myChars = useContext(MyCharsCt);
  const charListRef = useRef();

  return (
    <Box className="full-h flex-col" bgColor="cardBg3">
      {window.innerWidth <= 700 ? (
        <TopBarSmall
          chosenI={chosenI}
          setChosenI={setChosenI}
          setPickerOn={setPickerOn}
        />
      ) : (
        <TopBar
          chosenI={chosenI}
          setChosenI={setChosenI}
          setPickerOn={setPickerOn}
          charListRef={charListRef}
        />
      )}
      <div className="pt-4 grow-1 flex">
        <div className="full-w flex justify-center align-start">
          {Boolean(myChars.length) && <Info charIndex={chosenI} />}
        </div>
      </div>
      {pickerOn && (
        <CharPicker
          close={() => setPickerOn(false)}
          setChosenI={setChosenI}
          charListRef={charListRef}
        />
      )}
    </Box>
  );
}

function CharPicker(props) {
  const { charListRef } = props;
  const { setMyChars, setMyWps } = useContext(SetDBCt);
  const myChars = useContext(MyCharsCt);
  const wishlist = getNewChars(myChars);
  return (
    <Picker
      needMassAdd={true}
      data={wishlist}
      dataType="Character"
      close={props.close}
      pick={({ name, weapon }) => {
        const weaponID = Date.now();
        setMyWps((prev) => {
          const newWps = deepCopy(prev);
          newWps.push({
            ID: weaponID,
            user: name,
            ...initWeapon({ type: weapon })
          });
          return newWps;
        });
        setMyChars((prev) => {
          const newChars = deepCopy(prev);
          newChars.unshift({
            name,
            level: "1/20",
            ascension: 0,
            "Normal Attack": 1,
            "Elemental Skill": 1,
            "Elemental Burst": 1,
            constellation: 0,
            weaponID,
            artIDs: {
              flower: null,
              plume: null,
              sands: null,
              goblet: null,
              circlet: null
            }
          });
          return newChars;
        });
        props.setChosenI(0);
        if (charListRef.current) charListRef.current.scrollLeft = 0;
      }}
    />
  );
}

function getNewChars(myChars) {
  const result = [];
  for (let name in characters) {
    if (characters[name].beta) continue;
    let existed = false;
    for (let unit of myChars) {
      if (name === unit.name) {
        existed = true;
        break;
      }
    }
    if (!existed) {
      const { beta, icon, rarity, vision, weapon } = characters[name];
      result.push({ beta, name, icon, rarity, vision, weapon });
    }
  }
  return result;
}
