function mapToCharLv(arr) {
  const result = {};
  [1, 20, 40, 50, 60, 70, 80, 90].forEach((lv, i) => {
    result[lv] = arr[i];
  });
  return result;
}

export const baseRxnDmg = mapToCharLv([9, 40, 104, 162, 245, 383, 540, 725]);

