import { Text } from "../../../styledCpns/DataDisplay";
import { Select, Button } from "../../../styledCpns/Inputs";
import { Box } from "../../../styledCpns/Layout";
import { ButtonBar } from "../../CommonCpns";
import { MutableSSLine } from "../../Readers/ArtifactCard/SSLine";
import { artMS } from "../../../data/artifacts/config";
import { pctOf } from "../../../helpers/misc";

export function FilledCard(props) {
  const { artP } = props;
  const { rarity = "Gold" } = artP;
  const msType = artP.mainSType;
  const availableMSTypes = artMS[artP.type];
  const isFoP = ["flower", "plume"].includes(artP.type);
  const msValues = availableMSTypes[msType];

  return (
    <div className="mt-3">
      <div className="flex justify-evenly align-center">
        <Button
          className="glow-on-hover"
          variant="negative"
          onClick={props.remove}
        >
          Remove
        </Button>
        <Box bgColor="cardBg3" borderRadius="50%">
          <Select
            color={rarity}
            fontSize="1.125rem"
            fontWeight={700}
            className="appearance-none px-2 pt-2 pb-1 pointer"
            value={"+" + artP.level}
            onChange={props.enhance}
          >
            {[...Array(rarity === "Gold" ? 21 : 17).keys()].map((_, lv) => (
              <option key={lv}>+{lv}</option>
            ))}
          </Select>
        </Box>
        <Button
          className="glow-on-hover"
          variant="positive"
          onClick={props.switch}
        >
          Switch
        </Button>
      </div>
      <div className="mt-3 ml-4">
        {isFoP ? (
          <Text className="pl-5 pt-1" variant="h6">
            {msType}
          </Text>
        ) : (
          <div className="py-1">
            <Select
              fontSize="1.125rem"
              color="white"
              className="pl-5 appearance-none bg-for-icon white-arrow-bg"
              value={msType}
              onChange={props.changeMSType}
            >
              {Object.keys(availableMSTypes).map((type) => (
                <option key={type}>{type}</option>
              ))}
            </Select>
          </div>
        )}
        <Text className="pl-5" variant="h3" color={rarity}>
          {msValues[artP.level] + pctOf(msType)}
        </Text>
      </div>
      <div className="px-2">
        {artP.subS.map((subStat, ssIndex) => (
          <MutableSSLine
            key={ssIndex}
            stat={subStat}
            changeSSType={(e) => props.changeSSType(e.target.value, ssIndex)}
            changeSSVal={(e) =>
              props.changeSSVal(e.target.value, ssIndex, subStat.val)
            }
          />
        ))}
      </div>
    </div>
  );
}

export function EmptyCard(props) {
  return (
    <Box height="17.6rem">
      <div className="py-4 flex-center">
        <Text variant="h6">Not equipping any artifact</Text>
      </div>
      <ButtonBar
        texts={["Switch"]}
        variants={["positive"]}
        handlers={[props.switch]}
      />
    </Box>
  );
}
