import { useState, useContext, useCallback } from "react";
import { PartiesCt, SetupCt, WpRackCt, FixedCts, ManagerCts } from "../index";
import { Box } from "../../../styledCpns/Layout";
import { Pedestal, CopySection } from "./styles";
import Picker from "../../Picker";
import { TeammateIcon, TeammateSlot } from "./Teammates";
import characters from "../../../data/characters";
import { rsnNames } from "../../../data/elements";
import { initTeammate, visionCount, getWpRange, getSubWpBCs } from "./helpers";
import { deepCopy } from "../../../helpers/misc";

export default function SectionParty({ char }) {
  const parties = useContext(PartiesCt);
  const curSetupI = useContext(SetupCt);
  const mainWpName = useContext(WpRackCt)[curSetupI].name;
  const party = parties[curSetupI];
  const [teammateI, setTeammateI] = useState(null);
  const { setElmtMCs, setParties, setSubWpMCs } = useContext(FixedCts);
  const { setups } = useContext(ManagerCts);

  const addTeammate = useCallback(
    ({ name, vision, weapon }) => {
      const oldCount = visionCount(char, party);
      const oldRange = getWpRange(party);
      const { name: oldName } = party[teammateI];
      const newParty = deepCopy(party);
      newParty[teammateI] = initTeammate(name);
      const newCount = visionCount(char, newParty);
      const newRange = getWpRange(newParty);
      setParties((prev) => {
        const newPts = deepCopy(prev);
        newPts[curSetupI][teammateI] = initTeammate(name);
        return newPts;
      });
      setElmtMCs((prev) => {
        const MCs = deepCopy(prev);
        if (oldName) {
          const oldVision = characters[oldName].vision;
          if (
            rsnNames[oldVision] &&
            oldCount[oldVision] === 2 &&
            newCount[oldVision] === 1
          ) {
            const index = MCs[curSetupI].resonance.findIndex(
              (rsn) => rsn.name === rsnNames[oldVision]
            );
            MCs[curSetupI].resonance.splice(index, 1);
          }
        }
        if (
          rsnNames[vision] &&
          oldCount[vision] === 1 &&
          newCount[vision] === 2
        ) {
          MCs[curSetupI].resonance.push({
            name: rsnNames[vision],
            activated: vision === "Pyro"
          });
        }
        return MCs;
      });
      setSubWpMCs((prev) => {
        const MCs = deepCopy(prev);
        if (oldName) {
          const oldWeapon = characters[oldName].weapon;
          if (!newRange[oldWeapon]) {
            delete MCs[curSetupI].BCs[oldWeapon];
          }
        }
        if (!oldRange[weapon]) {
          MCs[curSetupI].BCs[weapon] = getSubWpBCs(weapon, mainWpName);
        }
        return MCs;
      });
    },
    [char, party, curSetupI, teammateI]
  );

  const removeTeammate = useCallback(
    (tmI) => {
      const newParty = deepCopy(party);
      newParty[tmI] = { name: null, BCs: [], DCs: [] };
      const newCount = visionCount(char, newParty);
      const newRange = getWpRange(newParty);
      setParties((prev) => {
        const newPts = deepCopy(prev);
        newPts[curSetupI] = newParty;
        return newPts;
      });
      const { weapon, vision } = characters[party[tmI].name];
      if (newCount[vision] === 1) {
        setElmtMCs((prev) => {
          const MCs = deepCopy(prev);
          const index = MCs[curSetupI].resonance.findIndex(
            (rsn) => rsn.name === rsnNames[vision]
          );
          MCs[curSetupI].resonance.splice(index, 1);
          return MCs;
        });
      }
      if (!newRange[weapon]) {
        setSubWpMCs((prev) => {
          const MCs = deepCopy(prev);
          delete MCs[curSetupI].BCs[weapon];
          return MCs;
        });
      }
    },
    [char, party, curSetupI]
  );

  const copyParty = (name) => {
    const index = setups.findIndex((st) => st.name === name);
    const newPt = deepCopy(parties[index]);
    setParties((prev) => {
      const newPtes = deepCopy(prev);
      newPtes[curSetupI] = newPt;
      return newPtes;
    });
    setElmtMCs((prev) => {
      const MCs = deepCopy(prev);
      MCs[curSetupI] = deepCopy(prev[index]);
      return MCs;
    });
    setSubWpMCs((prev) => {
      const MCs = deepCopy(prev);
      for (let type in getWpRange(newPt)) {
        MCs[curSetupI].BCs[type] = getSubWpBCs(type, mainWpName);
      }
      return MCs;
    });
  };
  const copyOpts = [];
  if (party.every((tm) => !tm.name)) {
    for (let i in parties) {
      if (parties[i].some(({ name }) => name)) {
        copyOpts.push(setups[i].name);
      }
    }
  }
  const isOg = setups[curSetupI].type === "original";
  return (
    <Pedestal>
      {copyOpts.length ? (
        <CopySection options={copyOpts} copy={copyParty} />
      ) : null}
      <Box className="px-2 flex justify-around" width="100%">
        {party.map((teammate, tmI) => (
          <Box key={tmI} className="relative" width="4.5rem" height="4.5rem">
            {teammate.name ? (
              <TeammateIcon
                mutable={isOg}
                name={teammate.name}
                handleClick={() => {
                  if (isOg) setTeammateI(tmI);
                }}
                removeTeammate={() => removeTeammate(tmI)}
              />
            ) : (
              <TeammateSlot handleClick={() => setTeammateI(tmI)} />
            )}
          </Box>
        ))}
      </Box>
      {teammateI !== null && (
        <CharPicker
          char={char}
          party={party}
          addTeammate={addTeammate}
          close={() => setTeammateI(null)}
        />
      )}
    </Pedestal>
  );
}

function CharPicker(props) {
  const remains = [];
  for (let name in characters) {
    if (
      name !== props.char.name &&
      props.party.findIndex((teammate) => teammate.name === name) === -1
    ) {
      const { beta, icon, rarity, vision, weapon } = characters[name];
      remains.push({ beta, name, icon, rarity, vision, weapon });
    }
  }
  return (
    <Picker
      data={remains}
      dataType="Character"
      close={props.close}
      pick={props.addTeammate}
    />
  );
}
