import {
  ColoredSpan,
  LGreen,
  Green,
  Red
} from "../../../../styledCpns/DataDisplay";
import { TalentLvMults } from "../../config";
import actvTalents from "./talents";
import { incElementalBnes } from "../../../../helpers/calculators";
import { getXtraTalentLv } from "../../../../calculators/helpers";

export const buffs = [
  {
    src: "Elemental Skill",
    desc: ({ toSelf, inputs }) => (
      <>
        Grants the active character within its AoE an <LGreen>ATK Bonus</LGreen>{" "}
        based on Kujou Sara's <LGreen>Base ATK</LGreen>.{" "}
        {!toSelf && <Red>ATK Bonus: {buffs[0].bnValue(...inputs)}.</Red>}
      </>
    ),
    isGranted: () => true,
    affect: "party",
    labels: ["Base ATK", "Elemental Skill Level"],
    inputs: [0, 1],
    inputTypes: ["text", "text"],
    maxs: [9999, 13],
    addBnes: ({ ATTRs, inputs, toSelf, char, party }) => {
      const { bnValue } = buffs[0];
      ATTRs.ATK += toSelf
        ? bnValue(
            ATTRs["Base ATK"],
            char["Elemental Skill"] +
              getXtraTalentLv(actvTalents[1], char, party)
          )
        : bnValue(...inputs);
    },
    bnValue: (baseATK, level) =>
      Math.round(baseATK * ((42.96 * TalentLvMults[2][level]) / 100))
  },
  {
    src: "Constellation 6",
    desc: () => (
      <>
        The <ColoredSpan color="Electro"> Electro DMG</ColoredSpan> of
        characters who have had their ATK increased by Tengu Juurai has its{" "}
        <LGreen>Crit DMG</LGreen> increased by <Green>60%</Green>.
      </>
    ),
    isGranted: (char) => char.constellation >= 6,
    affect: "party",
    addBnes: ({ bonuses, char, infusion }) => {
      incElementalBnes(char.name, infusion, "Electro", bonuses, "CD", 0.6);
    }
  }
];
