import { LGreen, Green } from "../../../styledCpns/DataDisplay";
import { baseAtkType, subStatsType } from "../config";
import { LiyueSeries } from "../series";
import { incAllNAs, incAllElmtDmgBnes } from "../../../helpers/calculators";

const GoldSwords = {
  "Mistsplitter Reforged": {
    code: 101,
    icon: "0/09/Weapon_Mistsplitter_Reforged",
    rarity: "Gold",
    mainStat: {
      type: "Base ATK",
      value: baseAtkType[48]
    },
    subStats: [
      {
        type: "CRIT DMG",
        value: subStatsType["9.6%"]
      }
    ],
    addBnes: ({ ATTRs, wpRfm }) => {
      incAllElmtDmgBnes(ATTRs, 9 + wpRfm * 3);
    },
    buffs: [
      {
        affect: "self",
        labels: ["Stacks"],
        inputTypes: ["stacks"],
        values: [3],
        addBnes: ({ ATTRs, wpRfm, inputs, charData }) => {
          ATTRs[charData.vision + " DMG Bonus"] += GoldSwords[
            "Mistsplitter Reforged"
          ].stackVal({ wpRfm })[inputs[0] - 1];
        },
        desc: ({ wpRfm }) =>
          GoldSwords["Mistsplitter Reforged"].pasvDescs({ wpRfm })[0].buff
      }
    ],
    stackVal: ({ wpRfm }) => [6 + wpRfm * 2, 12 + wpRfm * 4, 21 + wpRfm * 7],
    pasvName: "Mistsplitter's Edge",
    pasvDescs: ({ wpRfm }) => [
      {
        bonus: (
          <>
            Gain a <Green>{9 + wpRfm * 3}%</Green>{" "}
            <LGreen>Elemental DMG Bonus</LGreen> for every element and receive
            the might of Mistsplitter's Emblem.
          </>
        ),
        buff: (
          <>
            At stack levels 1/2/3, Mistsplitter's Emblem provides a{" "}
            <Green>
              {GoldSwords["Mistsplitter Reforged"]
                .stackVal({ wpRfm })
                .join("/")}
              %
            </Green>{" "}
            Elemental DMG Bonus for the{" "}
            <LGreen>character's Elemental Type</LGreen>.
          </>
        ),
        others: (
          <>
            The character will obtain 1 stack of Mistsplitter's Emblem in each
            of the following scenarios: Normal Attack deals Elemental DMG (stack
            lasts 5s), casting Elemental Burst (stack lasts 10s); Energy is less
            than 100% (stack disappears when Energy is full). Each stack's
            duration is calculated independently.
          </>
        )
      }
    ]
  },
  "Aquila Favonia": {
    code: 102,
    icon: "6/6a/Weapon_Aquila_Favonia",
    rarity: "Gold",
    mainStat: {
      type: "Base ATK",
      value: baseAtkType[48]
    },
    subStats: [
      {
        type: "Physical DMG Bonus",
        value: subStatsType["9%"]
      }
    ],
    addBnes: ({ ATTRs, wpRfm }) => {
      ATTRs["ATK%"] += 15 + wpRfm * 5;
    },
    pasvProcD: [
      {
        name: "AoE DMG",
        baseSType: "ATK",
        multiplier: [0, 1.6, 2, 2.4, 2.8, 3.2],
        dmgType: "Physical"
      }
    ],
    pasvName: "Falcon's Defiance",
    pasvDescs: ({ wpRfm }) => [
      {
        bonus: (
          <>
            <LGreen>ATK</LGreen> is increased by{" "}
            <Green>{15 + wpRfm * 5}%</Green>. Triggers on taking DMG: the soul
            of the Falcon of the West awakens, holding the banner of the
            resistance aloft, regenerating HP equal to {85 + wpRfm * 15}% of ATK
            and dealing <Green>{160 + wpRfm * 40}%</Green>
            of <LGreen>ATK</LGreen> as DMG to surrounding opponents. This effect
            can only occur once every 15s.
          </>
        )
      }
    ]
  },
  "Skyward Blade": {
    code: 103,
    icon: "0/03/Weapon_Skyward_Blade",
    rarity: "Gold",
    mainStat: {
      type: "Base ATK",
      value: baseAtkType[46]
    },
    subStats: [
      {
        type: "Energy Recharge",
        value: subStatsType["12%"]
      }
    ],
    addBnes: ({ ATTRs, wpRfm }) => {
      ATTRs["CRIT Rate"] += 3 + wpRfm;
    },
    buffs: [
      {
        affect: "self",
        addBnes: ({ ATTRs }) => {
          ATTRs["Normal ATK SPD"] += 10;
          ATTRs["Charged ATK SPD"] += 10;
        },
        desc: ({ wpRfm }) =>
          GoldSwords["Skyward Blade"].pasvDescs({ wpRfm })[0].buff
      }
    ],
    pasvProcD: [
      {
        name: "Additional DMG",
        baseSType: "ATK",
        multiplier: [0, 0.2, 0.25, 0.3, 0.35, 0.4],
        dmgType: "Physical"
      }
    ],
    pasvName: "Sky-Piercing Fang",
    pasvDescs: ({ wpRfm }) => [
      {
        bonus: (
          <>
            <LGreen>Crit Rate</LGreen> increased by <Green>{3 + wpRfm}%</Green>.
            And Normal and Charged hits deal additional DMG equal to{" "}
            <Green>{15 + wpRfm * 5}%</Green> of ATK. Skypiercing Might lasts for
            12s.
          </>
        ),
        buff: (
          <>
            Gains Skypiercing Might upon using Elemental Burst: Increases
            Movement SPD and <LGreen>ATK SPD</LGreen> by <Green>10%</Green>.
          </>
        ),
        others: <></>
      }
    ]
  },
  "Freedom-Sworn": {
    code: 104,
    icon: "3/39/Weapon_Freedom-Sworn",
    rarity: "Gold",
    mainStat: {
      type: "Base ATK",
      value: baseAtkType[46]
    },
    subStats: [
      {
        type: "Elemental Mastery",
        value: subStatsType[43]
      }
    ],
    addBnes: ({ bonuses, wpRfm }) => {
      if (bonuses) bonuses.AllDmg += 7.5 + wpRfm * 2.5;
    },
    buffs: [
      {
        affect: "party",
        addBnes: ({ ATTRs, bonuses, wpRfm }) => {
          incAllNAs(bonuses, 12 + wpRfm * 4);
          ATTRs["ATK%"] += 15 + wpRfm * 5;
        },
        desc: ({ wpRfm }) =>
          GoldSwords["Freedom-Sworn"].pasvDescs({ wpRfm })[0].buff
      }
    ],
    pasvName: "Revolutionary Chorale",
    pasvDescs: ({ wpRfm }) => [
      {
        bonus: (
          <>
            A part of the "Millennial Movement" that wanders amidst the winds.
            Increases <LGreen>DMG</LGreen> by{" "}
            <Green>{7.5 + wpRfm * 2.5}%</Green>. When triggering Elemental
            Reactions, the character gains a Sigil of Rebellion. This effect can
            be triggered once every 0.5s. When you possess 2 Sigils of
            Rebellion, all of them will be consumed and all nearby party members
            will obtain the "Millennial Movement: Song of Resistance" effect for
            12s.
          </>
        ),
        buff: (
          <>
            "Millennial Movement: Song of Resistance" increases{" "}
            <LGreen>Normal, Charged, and Plunging Attack DMG</LGreen> by{" "}
            <Green>{12 + wpRfm * 4}%</Green> and increases <LGreen>ATK</LGreen>{" "}
            by <Green>{15 + wpRfm * 5}%</Green>. Once this effect is triggered,
            you will not gain Sigils of Rebellion for 20s.
          </>
        ),
        others: (
          <>
            Of the many effects of the "Millennial Movement", buffs of the same
            type will not stack.
          </>
        )
      }
    ]
  },
  "Summit Shaper": {
    code: 105,
    icon: "c/ca/Weapon_Summit_Shaper",
    rarity: "Gold",
    mainStat: {
      type: "Base ATK",
      value: baseAtkType[46]
    },
    subStats: [
      {
        type: "ATK%",
        value: subStatsType["10.8%"]
      }
    ],
    ...LiyueSeries
  },
  "Primordial Jade Cutter": {
    code: 106,
    icon: "2/2a/Weapon_Primordial_Jade_Cutter",
    rarity: "Gold",
    mainStat: {
      type: "Base ATK",
      value: baseAtkType["44b"]
    },
    subStats: [
      {
        type: "CRIT Rate",
        value: subStatsType["9.6%"]
      }
    ],
    addBnes: ({ ATTRs, wpRfm }) => {
      ATTRs["HP%"] += 15 + wpRfm * 5;
    },
    addFinalBnes: ({ ATTRs, wpRfm }) => {
      ATTRs.ATK += Math.round((ATTRs.HP * (0.9 + wpRfm * 0.3)) / 100);
    },
    pasvName: "Protector's Virtue",
    pasvDescs: ({ wpRfm }) => [
      {
        bonus: (
          <>
            <LGreen>HP</LGreen> increased by <Green>{15 + wpRfm * 5}%</Green>.
            Additionally, provides an <LGreen>ATK Bonus</LGreen> based on{" "}
            <Green>{(9 + wpRfm * 3) / 10}%</Green> of the wielder's{" "}
            <LGreen>Max HP</LGreen>.
          </>
        )
      }
    ]
  }
};

export default GoldSwords;
