import { useContext } from "react";
import { SetDBCt } from "../../../../App";
import { Box } from "../../../../styledCpns/Layout";
import { SetBonus, ButtonBar } from "../../../CommonCpns";
import MyWpCard from "../../../Readers/MyWpCard";
import MyArtCard from "../../../Readers/MyArtCard";
import StatsTable from "../../../Readers/StatsTable";
import { HiddenCard } from "./styles";
import { artTypes } from "../../../../data/misc";
import { deepCopy } from "../../../../helpers/misc";

export default function HiddenGear(props) {
  const { opened, setOpened } = props;
  if (opened === "weapon") {
    return <Weapon weapon={props.weapon} setWpInvOn={props.setWpInvOn} />;
  } else {
    const { pieces, sets } = props.art;
    if (opened === "set bonus") {
      return (
        <HiddenCard>
          <Box className="px-1 hide-sb" width="18.75rem" height="27.35rem">
            <SetBonus sets={sets} />
          </Box>
        </HiddenCard>
      );
    } else if (opened === "stats bonus") {
      return (
        <ArtAllStats artSBnes={props.artSBnes} close={() => setOpened(null)} />
      );
    }
    const index = artTypes.indexOf(opened);
    if (index === -1 || !pieces[index]) return null;
    return (
      <Artifact
        artP={pieces[index]}
        ownerIndex={props.ownerIndex}
        setShowing={props.setShowing}
        setOpened={setOpened}
        setArtInvType={props.setArtInvType}
      />
    );
  }
}

function Weapon(props) {
  return (
    <HiddenCard>
      <Box className="px-1 hide-sb" width="18.75rem" height="24.5rem">
        <MyWpCard weapon={props.weapon} mutable={true} />
      </Box>
      <ButtonBar
        className="mt-3"
        texts={["Switch"]}
        variants={["positive"]}
        handlers={[() => props.setWpInvOn(true)]}
      />
    </HiddenCard>
  );
}

function Artifact(props) {
  const { artP, ownerIndex } = props;
  const { setMyChars, setMyArts } = useContext(SetDBCt);
  return (
    <HiddenCard>
      <Box className="hide-sb" width="18.75rem" height="24.5rem">
        <MyArtCard art={artP} mutable={true} />
      </Box>
      <ButtonBar
        className="mt-3"
        texts={["Remove", "Switch"]}
        handlers={[
          () => {
            props.setShowing(false);
            setTimeout(() => {
              props.setOpened(null);
              setMyChars((prev) => {
                const newChars = deepCopy(prev);
                newChars[ownerIndex].artIDs[artP.type] = null;
                return newChars;
              });
              setMyArts((prev) => {
                const newArts = deepCopy(prev);
                newArts[
                  newArts.findIndex((art) => art.ID === artP.ID)
                ].user = null;
                return newArts;
              });
            }, 200);
          },
          () => props.setArtInvType(artP.type)
        ]}
      />
    </HiddenCard>
  );
}

function ArtAllStats(props) {
  return (
    <HiddenCard>
      <Box className="custom-sb" width="18.75rem" height="27.35rem">
        <StatsTable stats={props.artSBnes} />
      </Box>
    </HiddenCard>
  );
}
