import {
  initSelfMCs,
  getMainWpBCs,
  initSubWpMCs,
  getSubArtBCs,
  getSubArtDCs,
  initParty,
  initElmtMCs
} from "../../CalcChar/helpers";
import { initWeapon } from "../../../Picker/WpPicker";

function getResult(indexes, prev, newElmt) {
  const result = [];
  for (let index of indexes) {
    if (index === null) result.push(newElmt);
    else result.push(prev[index]);
  }
  return result;
}

export function getNewSetupName(setups) {
  const existedIs = [1, 2, 3, 4];
  for (let st of setups) {
    const parts = st.name.split(" ");
    if (parts.length === 2 && parts[0] === "Setup" && !isNaN(parts[1])) {
      const i = existedIs.indexOf(+parts[1]);
      if (i !== -1) existedIs.splice(i, 1);
    }
  }
  return "Setup " + existedIs[0];
}

export function applySettings(tempSetups, charName, wpType, CBs) {
  const setups = [];
  const cmpSetupIs = [];
  const indexes = [];
  tempSetups.forEach(({ name, ID, type, checked, index }, i) => {
    setups.push({ name, ID, type });
    if (checked) {
      cmpSetupIs.push(i);
    }
    indexes.push(index);
  });
  CBs.setSetups(setups);
  CBs.setCmpSetupIs(cmpSetupIs);
  CBs.setSelfMCs((prev) => getResult(indexes, prev, initSelfMCs(charName)));
  CBs.setWpRack((prev) => {
    const result = [];
    let ID = Date.now();
    for (let index of indexes) {
      if (index === null) {
        const newWp = initWeapon({ ID: ID++, type: wpType });
        result.push({ ...newWp, BCs: getMainWpBCs(wpType, newWp.name) });
      } else {
        result.push(prev[index]);
      }
    }
    return result;
  });
  CBs.setSubWpMCs((prev) => getResult(indexes, prev, initSubWpMCs()));
  CBs.setArtChest((prev) =>
    getResult(indexes, prev, {
      pieces: [null, null, null, null, null],
      sets: [],
      BCs: [],
      subBCs: getSubArtBCs(null),
      subDCs: getSubArtDCs()
    })
  );
  CBs.setParties((prev) => getResult(indexes, prev, initParty()));
  CBs.setElmtMCs((prev) => getResult(indexes, prev, initElmtMCs()));
}
