import { useState, useContext, useEffect, useRef, useCallback } from "react";
import { MyStsCt, SetDBCt, MyCharsCt, MyWpsCt, MyArtsCt } from "../../App";
import { Box } from "../../styledCpns/Layout";
import { StyledInv } from "../../styledCpns/Database";
import { Button, IconBtn } from "../../styledCpns/Inputs";
import { Confirmation, ShareLayout, NoItems } from "../CommonCpns";
import Setup from "./Setup";
import ComplexSetup from "./ComplexSetup";
import DmgResults from "./DmgResults";
import CombineCtrl from "./CombineCtrl";
import AddCtrl from "./AddCtrl";
import Info from "./Info";
import characters from "../../data/characters";
import { getFinalInfusion } from "../../helpers/processors";
import { deepCopy } from "../../helpers/misc";
import getBuffedStats from "../../calculators/buffedStats";
import getDmg from "../../calculators/damage";
import { initTmSetup, scroll } from "./helpers";

export default function MySetups(props) {
  const { chosenStID } = props;
  const { setMySts, setChosenStID, setImportedSt } = useContext(SetDBCt);
  const mySetups = useContext(MyStsCt);
  const myChars = useContext(MyCharsCt);
  const myWeapons = useContext(MyWpsCt);
  const myArts = useContext(MyArtsCt);
  const [infoOn, setInfoOn] = useState(false);
  const [removedID, setRemovedID] = useState(null);
  const [sharedID, setSharedID] = useState(null);
  const [combining, setCombining] = useState(false);
  const [addingID, setAddingID] = useState(null);
  const ref = useRef();

  let chosenSt = mySetups.find((st) => st.ID === chosenStID);
  if (chosenSt && chosenSt.type === "complex") {
    chosenSt = mySetups.find((st) => st.ID === chosenSt.shownID);
  }
  useEffect(() => {
    if (chosenStID) scroll(ref, mySetups, chosenStID);
  }, [chosenStID]);

  const buildTmSt = useCallback(
    (setup, tmI) => {
      setImportedSt(initTmSetup(setup, tmI, myChars, myWeapons, myArts));
    },
    [myChars, myWeapons, myArts]
  );

  let infusion;
  let ATTRs;
  let artSBnes;
  let rxnBnes;
  let dmg;

  if (chosenSt) {
    const {
      char,
      selfMCs,
      weapon,
      subWpMCs,
      art,
      elmtMCs,
      party,
      target
    } = chosenSt;
    const charData = characters[char.name];
    infusion = getFinalInfusion(char, selfMCs.BCs, charData.vision, party);
    let bonuses;
    const stats = getBuffedStats(
      charData,
      char,
      selfMCs.BCs,
      weapon,
      subWpMCs.BCs,
      art,
      elmtMCs.resonance,
      party,
      infusion
    );
    ATTRs = stats.ATTRs;
    artSBnes = stats.artSBnes;
    rxnBnes = stats.rxnBnes;
    bonuses = stats.bonuses;
    dmg = getDmg(
      deepCopy(char),
      selfMCs,
      party,
      art.subDCs,
      ATTRs,
      bonuses,
      rxnBnes,
      infusion,
      elmtMCs,
      target
    );
  }
  return (
    <Box
      className="full-h flex-center"
      bgColor="cardBg2"
      style={{ paddingTop: "2.5rem" }}
    >
      <StyledInv>
        <Box className="btn_bar" height="2.5rem">
          <IconBtn
            className="mr-3 glow-on-hover"
            icon="fas fa-info"
            variant="positive"
            size="1.625rem"
            fontSize="0.875rem"
            onClick={() => setInfoOn(true)}
          />
          <Button
            className="glow-on-hover"
            variant="positive"
            onClick={() => setCombining(true)}
          >
            Combine
          </Button>
        </Box>
        <Box className="body" height="35rem">
          <Box
            ref={ref}
            className="pl-2 pt-2 flex-col align-start overflow-auto"
            width="41rem"
            minWidth="41rem"
            style={{ scrollBehavior: "smooth" }}
          >
            {mySetups.length ? (
              mySetups.map((setup, i) => {
                const { ID } = setup;
                if (setup.type === "original") {
                  return (
                    <Setup
                      key={i}
                      setup={setup}
                      chosen={ID === chosenStID}
                      choose={() => setChosenStID(ID)}
                      edit={() => setImportedSt(setup)}
                      share={() => setSharedID(ID)}
                      remove={() => setRemovedID(ID)}
                      buildTmSt={(tmI) => buildTmSt(setup, tmI)}
                      result={{ infusion, ATTRs, artSBnes, rxnBnes }}
                    />
                  );
                } else if (setup.type === "complex") {
                  return (
                    <ComplexSetup
                      key={i}
                      setup={setup}
                      choose={() => setChosenStID(ID)}
                      chosen={ID === chosenStID}
                      setImportedSt={setImportedSt}
                      setSharedID={setSharedID}
                      add={() => setAddingID(ID)}
                      buildTmSt={buildTmSt}
                      result={{ infusion, ATTRs, artSBnes, rxnBnes }}
                    />
                  );
                }
                return null;
              })
            ) : (
              <div className="full-w">
                <NoItems type="Setups" />
              </div>
            )}
          </Box>
          <DmgResults setup={chosenSt} dmg={dmg} />
        </Box>
      </StyledInv>
      {removedID && (
        <RemoveCtrl
          index={mySetups.findIndex((st) => st.ID === removedID)}
          setups={mySetups}
          setMySts={setMySts}
          close={() => setRemovedID(null)}
        />
      )}
      {sharedID && (
        <ShareCtrl
          setup={mySetups[mySetups.findIndex((st) => st.ID === sharedID)]}
          close={() => setSharedID(null)}
        />
      )}
      {combining && (
        <CombineCtrl setups={mySetups} close={() => setCombining(false)} />
      )}
      {addingID && (
        <AddCtrl
          setups={mySetups}
          addingID={addingID}
          close={() => setAddingID(false)}
        />
      )}
      {infoOn && <Info close={() => setInfoOn(false)} />}
    </Box>
  );
}

function RemoveCtrl(props) {
  const { index } = props;
  return (
    <Confirmation
      message={
        <>
          Do you wish to remove "<b>{props.setups[index].name}</b>"?
        </>
      }
      confirm={() =>
        props.setMySts((prev) => {
          const newSts = [...prev];
          newSts.splice(index, 1);
          return newSts;
        })
      }
      close={props.close}
    />
  );
}

function ShareCtrl(props) {
  const { name, ID, type, ...data } = props.setup;
  return <ShareLayout setupName={name} data={data} close={props.close} />;
}
