import characters from "../data/characters";
import artifactDebuffs from "../data/artifacts/artifactDebuffs";
import { ampRxnTrigElmts, tfmRxns, baseRxnDmg } from "../data/elements";
import {
  getDefMult,
  getResMult,
  getXtraTalentLv,
  calcBase,
  isInfused
} from "./helpers";

function getRdMult(char, charDCs, party, artDCs, elmtMCs, target) {
  const { Level, ...RES } = target;
  const rdMult = { ...RES, DEF: 0, DEFtoES: 0 };
  charDCs.forEach(({ activated, inputs }, i) => {
    const debuff = characters[char.name].debuffs[i];
    if (debuff.isGranted(char) && activated) {
      debuff.addPntes({ rdMult, fromSelf: true, char, inputs, party });
    }
  });
  party.forEach((teammate) => {
    if (teammate.name) {
      teammate.DCs.forEach(({ activated, inputs }, i) => {
        if (activated) {
          const { addPntes } = characters[teammate.name].debuffs[i];
          addPntes({ rdMult, fromSelf: false, inputs });
        }
      });
    }
  });
  artDCs.forEach((ctrl) => {
    if (ctrl.activated) {
      const debuff = artifactDebuffs[ctrl.set][ctrl.index];
      debuff.addPntes({ rdMult, inputs: ctrl.inputs });
    }
  });
  const geoRsnI = elmtMCs.resonance.findIndex(
    (rsn) => rsn.name === "Enduring Rock" && rsn.activated
  );
  if (geoRsnI >= 0) {
    rdMult["Geo RES"] -= 20;
  }
  if (elmtMCs.superconduct) rdMult["Physical RES"] -= 40;

  const { DEF, DEFtoES, ...others } = rdMult;
  for (let RES in others) {
    rdMult[RES] = getResMult(rdMult[RES] / 100);
  }
  return rdMult;
}

function getBaseDmg(ATTRs, dmgPiece, level, tlBnes) {
  const { xtraLv = 0, xtraMult = 0 } = tlBnes;
  const lv = level + xtraLv;
  if (Array.isArray(dmgPiece.baseMult)) {
    return dmgPiece.baseMult.map((mult, i) =>
      calcBase(
        ATTRs,
        { ...dmgPiece, baseMult: mult },
        lv,
        Array.isArray(xtraMult) ? xtraMult[i] : xtraMult
      )
    );
  }
  return calcBase(ATTRs, dmgPiece, lv, xtraMult);
}

function getTotalFlat(tlBnes, [atkType, elmtType], bonuses) {
  return (
    (tlBnes.xtraFlat || 0) +
    (bonuses[atkType]?.Flat || 0) +
    (bonuses[elmtType]?.Flat || 0)
  );
}

function getDmgBnMult(tlBnes, [atkType, elmtType], bonuses, inf, ATTRs) {
  let result = (tlBnes.xtraDmgBn || 0) + (bonuses[atkType]?.DMG || 0);
  if (isInfused(atkType, elmtType, inf)) {
    result += ATTRs[inf + " DMG Bonus"];
  } else {
    result += bonuses[elmtType]?.DMG || 0;
  }
  return 1 + (bonuses.AllDmg + result) / 100;
}

function getRxnMult(elmtMCs, elmtType, inf, rxnBnes, vision) {
  const { ampRxn, naAmpRxn } = elmtMCs;
  if (
    ampRxn &&
    (elmtType === "Elemental" ||
      (inf === vision && ampRxnTrigElmts[ampRxn].includes(inf)))
  ) {
    return rxnBnes[ampRxn];
  } else if (
    naAmpRxn &&
    inf !== vision &&
    ampRxnTrigElmts[naAmpRxn].includes(inf)
  ) {
    return rxnBnes["na" + naAmpRxn];
  }
  return 1;
}

function getReduction(
  { level },
  { Level },
  rdMult,
  [atkType, elmtType],
  vision,
  inf
) {
  let DEF = rdMult.DEF;
  if (atkType === "ES") DEF += rdMult.DEFtoES;
  DEF = getDefMult(+level.split("/")[0], Level, DEF);
  if (elmtType === "Elemental") {
    return DEF * rdMult[vision + " RES"];
  } else if (elmtType === "Physical" && !["NA", "CA", "PA"].includes(atkType)) {
    return DEF * rdMult["Physical RES"];
  } else if (elmtType === "Various") {
    return DEF;
  }
  return DEF * rdMult[inf + " RES"];
}

function getCrit(CR, CD, tlBnes, dmgTypes, bonuses) {
  const { xtraCR = 0, xtraCD = 0 } = tlBnes;
  const result = { CR: CR + xtraCR, CD: CD + xtraCD };
  const [atkType, elmtType] = dmgTypes;
  result.CR += (bonuses[atkType]?.CR || 0) + (bonuses[elmtType]?.CR || 0);
  result.CD += (bonuses[atkType]?.CD || 0) + (bonuses[elmtType]?.CD || 0);
  result.CR = Math.min(Math.max(result.CR, 0), 1);
  return result;
}

export default function getDmg(
  char,
  selfMCs,
  party,
  artDCs,
  ATTRs,
  bonuses,
  rxnBnes,
  infusion,
  elmtMCs,
  target
) {
  const CR = ATTRs["CRIT Rate"] / 100;
  const CD = ATTRs["CRIT DMG"] / 100;
  const rdMult = getRdMult(char, selfMCs.DCs, party, artDCs, elmtMCs, target);
  const { actvTalents, vision } = characters[char.name];

  const finalResult = {};
  actvTalents.forEach((talent) => {
    const { type } = talent;
    const level = char[type] + getXtraTalentLv(talent, char, party);
    finalResult[type] = {};

    talent.stats.forEach((piece) => {
      const { dmgTypes, getTlBnes } = piece;
      const tlBnes = getTlBnes
        ? piece.getTlBnes({ char, selfMCs, ATTRs, party })
        : {};
      const base = getBaseDmg(ATTRs, piece, level, tlBnes);
      let [totalFlat, dmgBnMult, rxnMult, reduction, crit] = [0, 1, 1, 1, null];
      if (dmgTypes) {
        const inf = infusion[dmgTypes[0]];
        totalFlat = getTotalFlat(tlBnes, dmgTypes, bonuses);
        dmgBnMult = getDmgBnMult(tlBnes, dmgTypes, bonuses, inf, ATTRs);
        rxnMult = getRxnMult(elmtMCs, dmgTypes[1], inf, rxnBnes, vision);
        reduction = getReduction(char, target, rdMult, dmgTypes, vision, inf);
        crit = getCrit(CR, CD, tlBnes, dmgTypes, bonuses);
      }
      const isArr = Array.isArray(base);

      finalResult[type][piece.name] = {
        "Non-crit": isArr
          ? base.map((b) => (b + totalFlat) * dmgBnMult * rxnMult * reduction)
          : (base + totalFlat) * dmgBnMult * rxnMult * reduction,
        get Crit() {
          return dmgTypes
            ? isArr
              ? this["Non-crit"].map((nc) => nc * (1 + crit.CD))
              : this["Non-crit"] * (1 + crit.CD)
            : 0;
        },
        get Average() {
          return dmgTypes
            ? isArr
              ? this["Non-crit"].map((nc) => nc * (1 + crit.CR * crit.CD))
              : this["Non-crit"] * (1 + crit.CR * crit.CD)
            : this["Non-crit"];
        }
      };
    });
  });

  finalResult["Reactions DMG"] = {};
  for (let rxn in tfmRxns) {
    const charLv = char.level.split("/")[0];
    let base =
      baseRxnDmg[charLv] * tfmRxns[rxn].mult * (1 + rxnBnes[rxn] / 100);
    const { dmgType } = tfmRxns[rxn];
    if (dmgType !== "Various") base *= rdMult[dmgType + " RES"];
    finalResult["Reactions DMG"][rxn] = {
      "Non-crit": base,
      Crit: 0,
      get Average() {
        return this["Non-crit"];
      }
    };
  }
  return finalResult;
}
