import { useState } from "react";
import { Box, AbsoluteBox, ModalCarpet } from "../../../styledCpns/Layout";
import { IconBtn } from "../../../styledCpns/Inputs";
import { Card } from "../styles";
import Inner from "./Inner";
import { getNamesForTables } from "../../../helpers/misc";
import { useCloseModal } from "../../../helpers/hooks";

export default function DmgResults({ char }) {
  const [enlarged, setEnlarged] = useState(false);
  if (!char) {
    return (
      <Card className="px-3 pt-2 pb-4" bgColor="cardBg3">
        <Box height="38.55rem" />
      </Card>
    );
  }
  const namesForTables = getNamesForTables(char);
  return (
    <Card className="px-3 pt-2 pb-4 relative" bgColor="cardBg3">
      {window.innerWidth >= 610 && (
        <AbsoluteBox top="0.75rem" left="0.75rem">
          <IconBtn
            icon="fas fa-expand-arrows-alt"
            className="glow-on-hover"
            variant="positive"
            size="1.75rem"
            onClick={() => setEnlarged(true)}
          />
        </AbsoluteBox>
      )}
      <Inner char={char} namesForTables={namesForTables} />
      {enlarged && (
        <EnlargedInner
          char={char}
          namesForTables={namesForTables}
          close={() => setEnlarged(false)}
        />
      )}
    </Card>
  );
}

function EnlargedInner(props) {
  const ref = useCloseModal(props.close);
  return (
    <ModalCarpet>
      <Box
        ref={ref}
        className="px-4 py-3"
        borderRadius="0.5rem"
        bgColor="cardBg3"
      >
        <Inner char={props.char} namesForTables={props.namesForTables} />
      </Box>
    </ModalCarpet>
  );
}
