import styled from "styled-components";
import { useContext, useState } from "react";
import { ManagerCts, FixedCts, SetupCt } from "../index";
import { Img } from "../../../styledCpns/DataDisplay";
import { Box } from "../../../styledCpns/Layout";
import { Button, IconBtn } from "../../../styledCpns/Inputs";
import { Card, MainSelect } from "../styles";
import WpInventory from "../../Readers/WpInventory";
import ArtInventory from "../../Readers/ArtInventory";
import PrePicker from "../../Picker/PrePicker";
import SectionParty from "./SectionParty";
import SectionWp from "./SectionWp";
import SectionArts, { setArtPiece } from "./SectionArts";
import Settings from "./Settings";
import ImportPreper from "./ImportPreper";
import { colorCodes, wikia, artifactIcons, artTypes } from "../../../data/misc";
import { getMainWpBCs } from "../CalcChar/helpers";
import { deepCopy } from "../../../helpers/misc";

const Icon = styled(Img)`
  border-radius: 50%;
  &:hover {
    background-color: ${colorCodes.lightGold};
  }
`;

export default function SetupManager(props) {
  const { char } = props;
  const { setups, stdSetupI, cmpSetupIs } = useContext(ManagerCts);
  const {
    setCurSetupI,
    setWpRack,
    setArtChest,
    setStdSetupI,
    setCmpSetupIs
  } = useContext(FixedCts);
  const curSetupI = useContext(SetupCt);
  const [inventory, setInv] = useState(null);
  const [prePickerOn, setPrePickerOn] = useState(false);
  const [settingsOn, setSettingsOn] = useState(false);
  const [impPreping, setImpPreping] = useState(false);
  const chosen = stdSetupI === curSetupI;

  const importBtn = (
    <Box height="2rem">
      <Button
        className="full-w full-h glow-on-hover"
        bgColor="#2269d6"
        color="white"
        onClick={() => setImpPreping(true)}
      >
        Import
      </Button>
      {impPreping && <ImportPreper close={() => setImpPreping(false)} />}
    </Box>
  );

  if (!char) {
    return (
      <Card className="p-3" bgColor="cardBg3">
        <Box height="38.5rem">{importBtn}</Box>
      </Card>
    );
  }
  const wpType = props.charData.weapon;
  return (
    <Card className="p-3 relative overflow-hidden" bgColor="cardBg3">
      <MainSelect
        tab={setups[curSetupI].name}
        setTab={(name) =>
          setCurSetupI(setups.findIndex((st) => st.name === name))
        }
        options={setups.map((st) => st.name)}
      />
      <Box className="mt-3 hide-sb" height="31.85rem">
        <SectionParty char={char} />
        <SectionWp />
        <SectionArts />
      </Box>
      <div className="mt-3 flex justify-between align-center">
        <Button
          className={chosen ? "unavailable" : "glow-on-hover"}
          variant="positive"
          onClick={() => {
            if (!chosen) {
              setStdSetupI(curSetupI);
              const i = cmpSetupIs.indexOf(curSetupI);
              setCmpSetupIs((prev) => {
                if (prev.length === 1) {
                  return [curSetupI];
                }
                const newIs = [...prev];
                if (i === -1) {
                  newIs.unshift(curSetupI);
                } else if (i > 0) {
                  [newIs[0], newIs[i]] = [newIs[i], newIs[0]];
                }
                return newIs;
              });
            }
          }}
        >
          Choose
        </Button>
        <IconBtn
          className="glow-on-hover"
          icon="fas fa-cog"
          fontSize="1.25rem"
          variant="positive"
          onClick={() => setSettingsOn(true)}
        />
        <div className="flex">
          <Icon
            width="2.5rem"
            className="mr-1 pointer"
            src={wikia + "7/7b/Icon_Inventory_Weapons.png"}
            alt="weapons"
            onClick={() => setInv("weapons")}
          />
          <Icon
            width="2.5rem"
            className="pointer"
            src={wikia + "6/6a/Icon_Inventory_Artifacts.png"}
            alt="artifacts"
            onClick={() => setPrePickerOn(true)}
          />
        </div>
      </div>
      <Settings
        settingsOn={settingsOn}
        char={char}
        wpType={wpType}
        close={() => setSettingsOn(false)}
        importBtn={importBtn}
      />
      {inventory === "weapons" && (
        <WpInventory
          wpType={wpType}
          close={() => setInv(null)}
          btnText="Pick"
          btnClick={(targetWp) =>
            setWpRack((prev) => {
              const newRack = deepCopy(prev);
              newRack[curSetupI] = {
                ...targetWp,
                BCs: getMainWpBCs(targetWp.type, targetWp.name)
              };
              return newRack;
            })
          }
        />
      )}
      {prePickerOn && (
        <PrePicker
          choices={artifactIcons}
          pick={(artType) => {
            setInv(artType);
            setPrePickerOn(false);
          }}
          close={() => setPrePickerOn(false)}
        />
      )}
      {![null, "weapons"].includes(inventory) && (
        <ArtInventory
          artType={inventory}
          close={() => setInv(null)}
          btnText="Pick"
          btnClick={(targetArt) => {
            const { ID, user, ...artP } = targetArt;
            setArtPiece(
              curSetupI,
              artTypes.indexOf(inventory),
              artP,
              setArtChest
            );
          }}
        />
      )}
    </Card>
  );
}
