import { useContext } from "react";
import { ResultCt, ManagerCts } from "../index";

export default function CmpTable(props) {
  const { stdValues, focus } = props;
  const { allDmg } = useContext(ResultCt);
  const { setups, stdSetupI, cmpSetupIs } = useContext(ManagerCts);
  const { main, subs } = props.name;
  const otherSetupIs = [...cmpSetupIs];
  otherSetupIs.splice(otherSetupIs.indexOf(stdSetupI), 1);
  return (
    <tbody>
      <tr>
        <th className="sticky-col" />
        <th color="lightGold-text">{setups[stdSetupI].name}</th>
        {otherSetupIs.map((index, i) => (
          <th key={i}>{setups[index].name}</th>
        ))}
      </tr>
      {subs.map((name, i) => {
        const stdVal = stdValues[name][focus];
        const isArr = Array.isArray(stdVal);
        return (
          <tr key={i}>
            <td className="sticky-col">{name}</td>
            <td color="lightGold-text">
              {isArr
                ? stdVal.map((sV) => Math.round(sV)).join(" + ")
                : Math.round(stdVal)}
            </td>
            {otherSetupIs.map((index, j) => {
              const thisVal = allDmg[index][main][name][focus];
              const diff = isArr ? thisVal[0] - stdVal[0] : thisVal - stdVal;
              const pctDiff =
                Math.round(
                  (Math.abs(diff) * 1000) / (isArr ? stdVal[0] : stdVal)
                ) / 10;
              return (
                <OtherVals
                  key={j}
                  value={
                    isArr
                      ? thisVal.map((v) => Math.round(v)).join(" + ")
                      : Math.round(thisVal)
                  }
                  diff={diff}
                  pctDiff={pctDiff}
                />
              );
            })}
          </tr>
        );
      })}
    </tbody>
  );
}

function OtherVals({ value, diff, pctDiff }) {
  return (
    <td className={"relative" + (pctDiff ? " tooltip_wrapper" : "")}>
      {value}
      {pctDiff ? (
        <>
          <span>
            <i
              className={"fas fa-long-arrow-alt-" + (diff > 0 ? "up" : "down")}
            />
          </span>
          <span
            className={
              "white-glow-shadow tooltip " + (diff > 0 ? "higher" : "lower")
            }
          >
            <b>{(diff > 0 ? "+" : "-") + pctDiff + "%"}</b>
          </span>
        </>
      ) : null}
    </td>
  );
}
