import { useState, useContext } from "react";
import { WpRackCt, SetupCt, FixedCts } from "../index";
import { Img, Text } from "../../../styledCpns/DataDisplay";
import { Select } from "../../../styledCpns/Inputs";
import { Box, AbsoluteBox } from "../../../styledCpns/Layout";
import { BetaMark } from "../../CommonCpns";
import { Pedestal } from "./styles";
import WpPicker from "../../Picker/WpPicker";
import weapons from "../../../data/weapons";
import { wikia, levels } from "../../../data/misc";
import { getBCsOfWp } from "./helpers";
import { getMainWpBCs } from "../CalcChar/helpers";
import { deepCopy } from "../../../helpers/misc";

export default function SectionWp() {
  const wpRack = useContext(WpRackCt);
  const curSetupI = useContext(SetupCt);
  const { setWpRack, setSubWpMCs } = useContext(FixedCts);
  const [pickerOn, setPickerOn] = useState(false);
  const weapon = wpRack[curSetupI];
  const wpData = weapons[weapon.type][weapon.name];
  const { rarity } = wpData;
  const isTrash = ["Green", "Gray"].includes(rarity);

  return (
    <Pedestal>
      <div className="px-2 flex-center">
        <Box
          className="relative pointer"
          bgImg={rarity}
          borderRadius="0.375rem"
          onClick={() => setPickerOn(true)}
        >
          <Img
            src={wikia + wpData.icon + ".png"}
            alt=""
            width="5.625rem"
            draggable={false}
          />
          {wpData.beta && (
            <AbsoluteBox top="-0.25rem" left="-0.25rem">
              <BetaMark />
            </AbsoluteBox>
          )}
        </Box>
        <div className="ml-2 overflow-hidden">
          <Text
            className="ellipsis"
            variant="h4"
            color={rarity}
            fontWeight={700}
          >
            {weapon.name}
          </Text>
          <div className="mt-1 flex-wrap">
            <Text className="mr-1">Level</Text>
            <Select
              color={rarity}
              fontSize="1rem"
              align="right"
              value={weapon.level}
              onChange={(e) =>
                setWpRack((prev) => {
                  const rack = deepCopy(prev);
                  rack[curSetupI].level = e.target.value;
                  return rack;
                })
              }
            >
              {(isTrash ? levels.slice(0, -4) : levels).map((level) => (
                <option key={level}>{level}</option>
              ))}
            </Select>
          </div>
          {!isTrash && (
            <div className="mt-1 flex-wrap">
              <Text className="mr-2">Refinement Rank</Text>
              <Select
                color={rarity}
                fontSize="1rem"
                value={weapon.refinement}
                onChange={(e) =>
                  setWpRack((prev) => {
                    const rack = deepCopy(prev);
                    rack[curSetupI].refinement = +e.target.value;
                    return rack;
                  })
                }
              >
                {[1, 2, 3, 4, 5].map((level) => (
                  <option key={level}>{level}</option>
                ))}
              </Select>
            </div>
          )}
        </div>
      </div>
      {pickerOn && (
        <WpPicker
          wpType={weapon.type}
          close={() => setPickerOn(false)}
          pick={(item) => {
            const { type, name } = item;
            setWpRack((prev) => {
              const rack = deepCopy(prev);
              rack[curSetupI] = {
                ...item,
                BCs: getMainWpBCs(type, name)
              };
              return rack;
            });
            setSubWpMCs((prev) => {
              const MCs = deepCopy(prev);
              const typeBCs = MCs[curSetupI].BCs[type];
              if (typeBCs) {
                MCs[curSetupI].BCs[type] = typeBCs.filter(
                  (ctrl) => ctrl.name !== name
                );
                MCs[curSetupI].BCs[type].push(
                  ...getBCsOfWp(weapon.type, weapon.name)
                );
              }
              return MCs;
            });
          }}
        />
      )}
    </Pedestal>
  );
}
