import styled from "styled-components";
import { colorCodes } from "../../../../data/misc";

export const HiddenSpace = styled.div`
  display: flex;
  align-items: center;
  width: 0;
  transition: width 200ms ease-in-out;
  &.show {
    width: 20.375rem;
  }
`;

export const HiddenCard = styled.div`
  border-left: 2px solid ${colorCodes.cardBg2};
  border-radius: 0 0.5rem 0.5rem 0;
  padding: 1rem 0.75rem;
  background-color: ${colorCodes.cardBg1};
`;
