import { ColoredSpan, LGreen, Green } from "../../../../styledCpns/DataDisplay";
import actvTalents from "./talents";
import { TalentLvMults } from "../../config";
import { getXtraTalentLv } from "../../../../calculators/helpers";

export const buffs = [
  {
    src: "Elemental Burst",
    desc: () => (
      <>
        Converts <LGreen>attack DMG</LGreen> to{" "}
        <ColoredSpan color="Geo">Geo DMG</ColoredSpan> that cannot be overridden
        by any other elemental infusion. Increased <LGreen>ATK</LGreen> that
        scales based on her <LGreen>DEF</LGreen>.
      </>
    ),
    isGranted: () => true,
    affect: "self",
    addFinalBnes: ({ ATTRs, char, charBCs, party }) => {
      const level =
        char["Elemental Burst"] + getXtraTalentLv(actvTalents[2], char, party);
      let mult = 40 * TalentLvMults[2][level];
      if (buffs[2].isGranted(char) && charBCs[2].activated) {
        mult += 50;
      }
      ATTRs.ATK += Math.round((ATTRs.DEF * mult) / 100);
    },
    canInfuse: () => true,
    infuseRange: ["NA", "CA", "PA"],
    canBeOverrided: false
  },
  {
    src: "Constellation 2",
    desc: () => (
      <>
        Increases her <LGreen>Charged Attack DMG</LGreen> by <Green>15%</Green>.
      </>
    ),
    isGranted: (char) => char.constellation >= 2,
    affect: "self",
    addBnes: ({ bonuses }) => {
      bonuses.CA.DMG += 15;
    }
  },
  {
    src: "Constellation 6",
    desc: () => (
      <>
        <LGreen>Sweeping Time</LGreen> increases Noelle's <LGreen>ATK</LGreen>{" "}
        by an additional <Green>50%</Green> of her <LGreen>DEF</LGreen>.
      </>
    ),
    isGranted: (char) => char.constellation >= 6,
    affect: "self"
  }
];
