import { LGreen, Green, Red } from "../../../../styledCpns/DataDisplay";

export const buffs = [
  {
    src: "Ascension 1 Passive Talent",
    desc: () => (
      <>
        Characters shielded by Sweeping Fervor deal <Green>15%</Green>{" "}
        <LGreen>increased Physical DMG</LGreen>.
      </>
    ),
    isGranted: (char) => char.ascension >= 1,
    affect: "party",
    addBnes: ({ ATTRs }) => {
      ATTRs["Physical DMG Bonus"] += 15;
    }
  },
  {
    src: "Constellation 1",
    desc: () => (
      <>
        Upon scoring a CRIT Hit, increases <LGreen>ATK SPD</LGreen> of Xinyan's{" "}
        <LGreen>Normal and Charged Attacks</LGreen> by <Green>12%</Green> for
        5s.
      </>
    ),
    isGranted: (char) => char.constellation >= 1,
    affect: "self",
    addBnes: ({ ATTRs }) => {
      ATTRs["Normal ATK SPD"] += 12;
      ATTRs["Charged ATK SPD"] += 12;
    }
  },
  {
    src: "Constellation 2",
    desc: () => (
      <>
        <LGreen>Riff Revolution's Physical DMG</LGreen> has its{" "}
        <LGreen>CRIT Rate</LGreen> increased by <Green>100%</Green>.
      </>
    ),
    isGranted: (char) => char.constellation >= 2,
    affect: "self"
  },
  {
    src: "Constellation 6",
    desc: ({ ATTRs }) => (
      <>
        Xinyan's <LGreen>Charged Attacks</LGreen> gain an{" "}
        <LGreen>ATK Bonus</LGreen> equal to <Green>50%</Green> of her{" "}
        <LGreen>DEF</LGreen>.{" "}
        <Red>Flat DMG Bonus: {buffs[3].bnValue(ATTRs)}.</Red>
      </>
    ),
    isGranted: (char) => char.constellation >= 6,
    affect: "self",
    addFinalBnes: ({ ATTRs, bonuses }) => {
      bonuses.CA.Flat += buffs[3].bnValue(ATTRs);
    },
    bnValue: (ATTRs) => Math.round(ATTRs.DEF / 2)
  }
];

export const debuffs = [
  {
    src: "Constellation 4",
    desc: () => (
      <>
        Sweeping Fervor's swing DMG decreases opponent's{" "}
        <LGreen>Physical RES</LGreen> by <Green>15%</Green> for 12s.
      </>
    ),
    isGranted: (char) => char.constellation >= 4,
    addPntes: ({ rdMult }) => {
      rdMult["Physical RES"] -= 15;
    }
  }
];
