import { LGreen, Green, Red } from "../../../../styledCpns/DataDisplay";
import characters from "../../index";
import actvTalents from "./talents";
import { TalentLvMults } from "../../config";
import { getXtraTalentLv } from "../../../../calculators/helpers";

function countElmt(char, party) {
  const result = [characters[char.name].vision];
  for (let { name } of party) {
    if (name && !result.includes(characters[name].vision)) {
      result.push(characters[name].vision);
    }
  }
  return result.length;
}

export const buffs = [
  {
    src: "Elemental Burst",
    desc: ({ toSelf, inputs, char, charBCs, party }) => (
      <>
        When <LGreen>Normal Attack DMG</LGreen> is dealt to opponents, Bonus DMG
        will be dealt based on Yun Jin's <LGreen>current DEF</LGreen>.{" "}
        {!toSelf && (
          <Red>
            DMG Bonus: {buffs[0].bnValue(...inputs, char, charBCs, party)}{" "}
            (base).
          </Red>
        )}
      </>
    ),
    isGranted: () => true,
    affect: "party",
    labels: ["Current DEF", "Elemental Burst Level"],
    inputs: [0, 1],
    inputTypes: ["text", "text"],
    maxs: [9999, 13],
    addFinalBnes: ({
      toSelf,
      char,
      charBCs,
      ATTRs,
      bonuses,
      inputs,
      party
    }) => {
      const { bnValue } = buffs[0];
      bonuses.NA.Flat += toSelf
        ? bnValue(
            ATTRs.DEF,
            char["Elemental Burst"] +
              getXtraTalentLv(actvTalents[2], char, party),
            char,
            charBCs,
            party
          )
        : bnValue(...inputs, char, charBCs, party);
    },
    bnValue: (DEF, level, char, BCs, party) => {
      const mult =
        32.16 * TalentLvMults[2][level] +
        buffs[0].getXtraMult(char, BCs, party);
      return Math.round((DEF * mult) / 100);
    },
    getXtraMult: (char, BCs, party) => {
      let result = 0;
      if (buffs[1].isGranted(char) && BCs[1].activated) {
        const numOfElmt = countElmt(char, party);
        result += numOfElmt * 2.5;
        if (numOfElmt === 4) result += 1.5;
      }
      return result;
    }
  },
  {
    src: "Ascension 4 Passive Talent",
    desc: ({ char, party }) => {
      const numOfElmt = countElmt(char, party);
      return (
        <>
          The <LGreen>Normal Attack DMG Bonus</LGreen> granted by Flying Cloud
          Flag Formation is further increased by{" "}
          <Green>
            <span className={numOfElmt === 1 ? "" : "unavailable"}>2.5</span>
            <span className="unavailable">/</span>
            <span className={numOfElmt === 2 ? "" : "unavailable"}>5</span>
            <span className="unavailable">/</span>
            <span className={numOfElmt === 3 ? "" : "unavailable"}>7.5</span>
            <span className="unavailable">/</span>
            <span className={numOfElmt === 4 ? "" : "unavailable"}>11.5</span>%
          </Green>{" "}
          of Yun Jin's <LGreen>DEF</LGreen> when the party contains characters
          of 1/2/3/4 Elemental Types.
        </>
      );
    },
    isGranted: (char) => char.ascension >= 4,
    affect: "party"
  },
  {
    src: "Constellation 2",
    desc: () => (
      <>
        After Cliffbreaker's Banner is unleashed, all nearby party members's{" "}
        <LGreen>Normal Attack DMG</LGreen> is increased by <Green>15%</Green>{" "}
        for 10s.
      </>
    ),
    isGranted: (char) => char.constellation >= 2,
    affect: "party",
    addBnes: ({ bonuses }) => {
      bonuses.NA.DMG += 15;
    }
  },
  {
    src: "Constellation 4",
    desc: () => (
      <>
        When Yun Jin trigger the Crystallize Reaction, her <LGreen>DEF</LGreen>{" "}
        is increased by <Green>20%</Green> for 12s.
      </>
    ),
    isGranted: (char) => char.constellation >= 4,
    affect: "self",
    addBnes: ({ ATTRs }) => {
      ATTRs["DEF%"] += 20;
    }
  },
  {
    src: "Constellation 6",
    desc: () => (
      <>
        Characters under the effects of the Flying Cloud Flag Formation have
        their <LGreen>Normal ATK SPD</LGreen> increased by <Green>12%</Green>.
      </>
    ),
    isGranted: (char) => char.constellation >= 6,
    affect: "party",
    addBnes: ({ ATTRs }) => {
      ATTRs["Normal ATK SPD"] += 12;
    }
  }
];
