import { LGreen, Green } from "../../../../styledCpns/DataDisplay";
import actvTalents from "./talents";
import { getXtraTalentLv } from "../../../../calculators/helpers";
import data from "./index";

export const buffs = [
  {
    src: "Constellation 1",
    desc: () => data.constellation[0].desc,
    isGranted: (char) => char.constellation >= 1,
    affect: "self",
    addBnes: ({ ATTRs }) => {
      ATTRs["Physical DMG Bonus"] += 30;
    }
  },
  {
    src: "Constellation 4",
    desc: () => data.constellation[3].desc,
    isGranted: (char) => char.constellation >= 4,
    affect: "self"
  }
];

export const debuffs = [
  {
    src: "Elemental Skill",
    desc: ({ fromSelf, char, inputs, party }) => (
      <>
        If Grimheart stacks are consumed, surrounding opponents will have their{" "}
        <LGreen>Physical RES</LGreen> and <LGreen>Cryo RES</LGreen> decreased by{" "}
        <Green>{debuffs[0].pntValue(fromSelf, char, inputs, party)}%</Green>.
      </>
    ),
    isGranted: () => true,
    labels: ["Elemental Skill Level"],
    inputTypes: ["text"],
    addPntes: ({ rdMult, fromSelf, char, inputs, party }) => {
      const value = debuffs[0].pntValue(fromSelf, char, inputs, party);
      rdMult["Physical RES"] -= value;
      rdMult["Cryo RES"] -= value;
    },
    pntValue: (fromSelf, char, inputs, party) => {
      let value = 0;
      if (fromSelf) {
        value =
          15 +
          char["Elemental Skill"] +
          getXtraTalentLv(actvTalents[1], char, party);
      } else if (inputs[0]) {
        value = 15 + inputs[0];
      }
      return Math.min(value, 25);
    }
  }
];
