import styled from "styled-components";
import { colorCodes } from "../data/misc";

export const Box = styled.div`
  width: ${(props) => props.width};
  min-width: ${(props) => props.minWidth};
  height: ${(props) => props.height};
  border: ${(props) => props.border};
  border-radius: ${(props) => props.borderRadius};
  box-shadow: ${(props) => props.boxShadow};
  background-color: ${(props) => colorCodes[props.bgColor] || props.bgColor};
  background-image: ${(props) =>
    ({
      Gold: "linear-gradient(to bottom, #a68746, #c5a03c, #e3bb2c)",
      Purple: "linear-gradient(to bottom, #7e4da6, #9b4cc6, #bd44e4)",
      Blue: "linear-gradient(to bottom, #5d8fa8, #72aae3, #8bb6ff)",
      Green: "linear-gradient(to bottom, #5ca862, #61c370, #63dd7e)",
      Gray: "linear-gradient(to bottom, #6c6969, #8c8989, #9c9a9a)"
    }[props.bgImg])};
  background-size: ${(props) => props.bgSize};
  background-position: ${(props) => props.bgPosition};
`;

export const AbsoluteBox = styled.div`
  position: absolute;
  top: ${(props) => props.top};
  bottom: ${(props) => props.bottom};
  left: ${(props) => props.left};
  right: ${(props) => props.right};
  z-index: ${(props) => props.zIndex};
  transform: ${(props) => props.transform};
`;

export const ModalCarpet = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
`;

export const ModalInner = styled.div`
  width: 90vw;
  height: 90vh;
  border-radius: 0.625rem;
  background-color: ${colorCodes.cardBg2};
  box-shadow: 0 0 3px white;
  @media (min-width: 440px) {
    width: 398.45px;
  }
  @media (min-width: 610px) {
    width: 586px;
  }
  @media (min-width: 769px) {
    width: 722px;
  }
  @media (min-width: 1025px) {
    height: 85vh;
    width: 944px;
  }
`;
