import { useState, useContext } from "react";
import { SetDBCt } from "../../App";
import { Text } from "../../styledCpns/DataDisplay";
import { Box } from "../../styledCpns/Layout";
import { ButtonBar } from "../CommonCpns";
import Filter from "./Filter";
import { deepCopy, initArtStatFilter } from "../../helpers/misc";

export default function BtnBar(props) {
  const { filter } = props;
  const { setMyArts } = useContext(SetDBCt);
  const [filterOn, setFilterOn] = useState(false);
  const isFiltered =
    filter.types.length ||
    filter.sets.length ||
    filter.stats.main !== "All" ||
    filter.stats.subs.some((s) => s !== "All");

  return (
    <div className="btn_bar full-w">
      <ButtonBar
        className="mr-3"
        distance="ml-3"
        texts={["Add", "Sort"]}
        variants={["positive", "positive"]}
        handlers={[
          props.add,
          () =>
            setMyArts((prev) => {
              const newArts = deepCopy(prev);
              newArts.sort((a, b) => sortArtifacts(a, b));
              return newArts;
            })
        ]}
      />
      {window.innerWidth >= 600 && props.typeFilter}
      <Box className="flex pointer">
        <Box
          className={
            (isFiltered ? "pr-2" : "pr-3") + " pl-3 py-1 glow-on-hover"
          }
          bgColor="lightGold"
          borderRadius={isFiltered ? "1rem 0 0 1rem" : "1rem"}
          onClick={() => setFilterOn(true)}
        >
          <Text color="black" fontWeight={700}>
            Filter
          </Text>
        </Box>
        {isFiltered && (
          <Box
            className="pl-2 flex-center glow-on-hover"
            bgColor="red"
            borderRadius="0 1rem 1rem 0"
            style={{ paddingRight: "0.75rem" }}
            onClick={() => {
              filter.setTypes([]);
              filter.setSets([]);
              filter.setStats(initArtStatFilter());
            }}
          >
            <i className="fas fa-times" />
          </Box>
        )}
      </Box>
      {filterOn && <Filter filter={filter} close={() => setFilterOn(false)} />}
    </div>
  );
}

function sortArtifacts(a, b) {
  if (a.level !== b.level) {
    return b.level - a.level;
  }
  if (a.type !== b.type) {
    const type = {
      flower: 5,
      plume: 4,
      sands: 3,
      goblet: 2,
      circlet: 1
    };
    return type[b.type] - type[a.type];
  }
  return b.set.localeCompare(a.set);
}
