import { getNewSetupName } from "./SetupManager/Settings/helpers";
import { elmts } from "../../data/elements";
import monsters from "../../data/monsters";

export function initTarget() {
  const result = { Level: 1, "Physical RES": 10 };
  for (let elmt of elmts) {
    result[elmt + " RES"] = 10;
  }
  return result;
}

export function initMonster() {
  return {
    name: monsters[0].name,
    inputs: []
  };
}

// also used in ./CalcChar/CharPicker
// & ./SetupManager/Settings/index, SetupCtrl
export function getSetupCtrl({
  name = "Setup 1",
  ID = Date.now(),
  type = "original"
}) {
  return { name, ID, type };
}

export function doInit(importedSt, CBs) {
  CBs.setChar(importedSt.char);
  CBs.setSelfMCs([importedSt.selfMCs]);
  CBs.setWpRack([importedSt.weapon]);
  CBs.setSubWpMCs([importedSt.subWpMCs]);
  CBs.setArtChest([importedSt.art]);
  CBs.setParties([importedSt.party]);
  CBs.setElmtMCs([importedSt.elmtMCs]);
  const { ID, type } = importedSt;
  CBs.setSetups([getSetupCtrl({ ID, type })]);
  CBs.setTarget(importedSt.target);
  CBs.setStdSetupI(0);
  CBs.setCurSetupI(0);
  CBs.setCmpSetupIs([0]);
  CBs.toCalc();
}

export function doImport(importedSt, overrideChar, overrideTg, length, CBs) {
  if (overrideChar) CBs.setChar(importedSt.char);
  if (overrideTg) CBs.setTarget(importedSt.target);
  CBs.setSelfMCs((prev) => [...prev, importedSt.selfMCs]);
  CBs.setWpRack((prev) => [...prev, importedSt.weapon]);
  CBs.setSubWpMCs((prev) => [...prev, importedSt.subWpMCs]);
  CBs.setArtChest((prev) => [...prev, importedSt.art]);
  CBs.setParties((prev) => [...prev, importedSt.party]);
  CBs.setElmtMCs((prev) => [...prev, importedSt.elmtMCs]);
  const { ID, type } = importedSt;
  CBs.setSetups((prev) => [
    ...prev,
    getSetupCtrl({ name: getNewSetupName(prev), ID, type })
  ]);
  CBs.setCurSetupI(length);
  CBs.toCalc();
}
