import styled from "styled-components";
import { Box } from "../../styledCpns/Layout";
import { Text } from "../../styledCpns/DataDisplay";
import { TeammateIcon } from "../Calculator/SetupManager/Teammates";
import { colorCodes } from "../../data/misc";

export const Layout = styled.div`
  max-width: 90%;
  height: 90%;
  border-radius: 0.5rem;
  background-color: ${colorCodes.cardBg2};
  overflow-wrap: break-word;
  cursor: default;
  .setup {
    display: flex;
    flex-direction: column;
  }
  .picked {
    box-shadow: 0 0 10px 3px green inset;
  }
  .members {
    margin-top: 0.5rem;
  }
  .icon_wrapper {
    width: 4rem;
    border-radius: 50%;
  }
  .icon_wrapper:first-child {
    box-shadow: 0 0 3px 2px ${colorCodes.orange};
  }
  .icon_wrapper:not(:last-child) {
    margin-right: 1rem;
  }
  @media (min-width: 39.5rem) {
    .setup {
      flex-direction: row;
      align-items: center;
    }
    .setup_name {
      width: 10rem;
      margin-right: 1rem;
    }
    .members {
      margin-top: 0;
    }
  }
`;

export function Menu(props) {
  const { options, setPickedIDs, notFull } = props;
  return (
    <div className="mt-2 pr-3 grow-1 custom-sb">
      <div>
        {!options.length && (
          <Box className="full-h flex-center" height="10rem">
            <Text variant="h6" fontWeight={700}>
              No Setups available for choosing...
            </Text>
          </Box>
        )}
        {options.map((st, i) => {
          const isPicked = props.pickedIDs.indexOf(st.ID) !== -1;
          return (
            <Option
              key={i}
              st={st}
              isPicked={isPicked}
              notFull={notFull}
              handleClick={() => {
                if (isPicked) {
                  setPickedIDs((prev) => {
                    const IDs = [...prev];
                    IDs.splice(IDs.indexOf(st.ID), 1);
                    return IDs;
                  });
                } else if (notFull) {
                  setPickedIDs((prev) => [...prev, st.ID]);
                }
              }}
            />
          );
        })}
      </div>
    </div>
  );
}

function Option(props) {
  const { st, isPicked, notFull } = props;
  const tmNs = st.party.map((tm) => tm.name);
  return (
    <Box
      className={
        "mb-2 p-3 setup" +
        (isPicked ? " picked" : notFull ? "" : " unavailable")
      }
      borderRadius="0.5rem"
      bgColor="cardBg1"
      onClick={props.handleClick}
    >
      <div className="setup_name">
        <Text variant="h6" fontWeight={700} color="orange">
          {st.name}
        </Text>
      </div>
      <div className="members flex">
        {[st.char.name, ...tmNs].map((name, j) =>
          name ? (
            <div key={j} className="icon_wrapper">
              <TeammateIcon
                mutable={false}
                name={name}
                handleClick={() => {}}
              />
            </div>
          ) : null
        )}
      </div>
    </Box>
  );
}
