import { useState } from "react";
import { Box, ModalCarpet } from "../../../styledCpns/Layout";
import { Img } from "../../../styledCpns/DataDisplay";
import WeaponCard from "../../Readers/WeaponCard";
import ArtifactCard from "../../Readers/ArtifactCard";
import { wikia, artifactIcons, artTypes } from "../../../data/misc";
import weapons from "../../../data/weapons";
import artifacts from "../../../data/artifacts";
import { useCloseModal } from "../../../helpers/hooks";

const Case = (props) => (
  <div className="p-1 glow-on-hover">
    <Box
      className={"p-1" + (props.bgImg ? " pointer" : "")}
      bgImg={props.bgImg}
      borderRadius="0.25rem"
      onClick={props.popModal}
    >
      <Img width="4.25rem" src={wikia + props.icon + ".png"} alt="" />
    </Box>
  </div>
);

export default function Gears(props) {
  const { weapon, artPieces } = props;
  const wpData = weapons[weapon.type][weapon.name];
  const [type, setType] = useState(null);
  return (
    <Box className="flex-wrap" width="15.75rem">
      <Case
        bgImg={wpData.rarity}
        icon={wpData.icon}
        popModal={() => setType("weapon")}
      />
      {artPieces.map((artP, i) => {
        if (artP) {
          const { icon, rarity = "Gold" } = artifacts[artP.set][artP.type];
          return (
            <Case
              key={i}
              bgImg={rarity}
              icon={icon}
              popModal={() => setType("artifacts")}
            />
          );
        }
        return <Case key={i} icon={artifactIcons[artTypes[i]]} />;
      })}
      {type ? (
        <GearReader
          type={type}
          weapon={weapon}
          artPieces={artPieces}
          close={() => setType(null)}
        />
      ) : null}
    </Box>
  );
}

function GearReader(props) {
  const ref = useCloseModal(props.close);
  return (
    <ModalCarpet>
      <Box
        ref={ref}
        className="px-3 py-4 flex overflow-auto white-glow-shadow"
        borderRadius="0.5rem"
        bgColor="cardBg1"
        style={{ maxWidth: "90%" }}
      >
        {props.type === "weapon" ? (
          <Box className="hide-sb" width="18.75rem" height="30rem">
            <WeaponCard mutable={false} weapon={props.weapon} />
          </Box>
        ) : (
          props.artPieces.map((artP, i) =>
            artP ? (
              <Box key={i} className="px-1" width="14.5rem">
                <ArtifactCard mutable={false} art={artP} ssSpace={2} />
              </Box>
            ) : null
          )
        )}
      </Box>
    </ModalCarpet>
  );
}
