import styled from "styled-components";
import { colorCodes } from "../../data/misc";

export const Cell = styled.div`
  flex-grow: 0;
  max-width: 33%;
  flex-basis: 33%;
  position: relative;
  &.w-vision {
    padding: 0.375rem;
  }
  &.wo-vision {
    padding: 0.25rem;
  }
  @media (min-width: 360px) {
    &.w-vision {
      padding: 0.75rem 0.75rem 0.375rem 0.375rem;
    }
    &.wo-vision {
      padding: 0.5rem;
    }
  }
  @media (min-width: 610px) {
    max-width: 20%;
    flex-basis: 20%;
    &.w-vision {
      padding: 0.5rem;
    }
  }
  @media (min-width: 769px) {
    max-width: 16.66667%;
    flex-basis: 16.66667%;
  }
  @media (min-width: 1025px) {
    max-width: 12.5%;
    flex-basis: 12.5%;
  }
  .badge {
    position: absolute;
    top: 0.125rem;
    right: 0.125rem;
    padding: 4px;
    border-radius: 50%;
    background-color: black;
    box-shadow: 0 0 2px white;
    &.w-cons {
      display: flex;
      border-radius: 1rem;
      padding-left: 6px;
    }
    .cons {
      margin-right: 2px;
      color: ${colorCodes.green};
    }
  }
`;
