import Picker from "../Picker";
import artifacts from "../../data/artifacts";

export default function ArtPicker(props) {
  const { artType } = props;
  const filteredData = [];
  for (let set in artifacts) {
    const { icon } = artifacts[set][artType];
    filteredData.push({ name: set, icon, rarity: "Gold" });
  }
  return (
    <Picker
      needMassAdd={props.needMassAdd}
      data={filteredData}
      dataType={artType}
      close={props.close}
      pick={({ name }) =>
        props.pick(initArtPiece({ type: artType, set: name }))
      }
    />
  );
}

function initArtPiece({ type, set }) {
  return {
    type,
    set,
    level: 0,
    mainSType: type === "flower" ? "HP" : type === "plume" ? "ATK" : "ATK%",
    subS: [
      {
        type: "DEF",
        val: 0
      },
      {
        type: "DEF%",
        val: 0
      },
      {
        type: "CRIT Rate",
        val: 0
      },
      {
        type: "CRIT DMG",
        val: 0
      }
    ]
  };
}
