import characters from "../../data/characters";
import { parseData, initSelfMCs } from "../Calculator/CalcChar/helpers";
import {
  initTeammate,
  getWpRange,
  getSubWpBCs
} from "../Calculator/SetupManager/helpers";
import { deepCopy } from "../../helpers/misc";

export function initTmSetup(setup, tmI, chars, wps, arts) {
  const { name, BCs, DCs } = setup.party[tmI];
  const charI = chars.findIndex((unit) => unit.name === name);
  const { char, wp, art } = parseData(
    charI === -1 ? { name } : chars[charI],
    wps,
    arts
  );
  const selfMCs = initSelfMCs(name);
  for (let ctrl of BCs) {
    const { activated, index } = ctrl;
    if (activated) {
      const buff = characters[name].buffs[index];
      if (buff.affect !== "teammates") {
        const i = selfMCs.BCs.findIndex((ctrl) => ctrl.index === index);
        selfMCs.BCs[i].activated = true;
        if (buff.selfLabels) {
          for (let j = 0; j < buff.selfLabels.length; j++) {
            selfMCs.BCs[i].inputs[j] = ctrl.inputs[j];
          }
        }
      }
    }
  }
  for (let ctrl of DCs) {
    const { activated, index } = ctrl;
    if (activated) {
      const debuff = characters[name].debuffs[index];
      const i = selfMCs.DCs.findIndex((ctrl) => ctrl.index === index);
      selfMCs.DCs[i].activated = true;
      if (debuff.selfLabels) {
        for (let j = 0; j < debuff.selfLabels.length; j++) {
          selfMCs.DCs[i].inputs[j] = ctrl.inputs[j];
        }
      }
    }
  }
  const party = deepCopy(setup.party);
  party[tmI] = initTeammate(setup.char.name);

  const subWpMCs = deepCopy(setup.subWpMCs);
  const oldWpRange = getWpRange(setup.party);
  const newWpRange = getWpRange(party);
  const newTmWp = characters[setup.char.name].weapon;
  if (!oldWpRange[newTmWp])
    subWpMCs.BCs[newTmWp] = getSubWpBCs(newTmWp, wp.name);
  if (!newWpRange[wp.type]) delete subWpMCs.BCs[wp.type];

  const { subBCs, subDCs } = setup.art;
  art.subBCs = deepCopy(subBCs);
  art.subDCs = deepCopy(subDCs);
  if (art.sets[0]?.bonusLv) {
    const ctrlI = subBCs.findIndex((ctrl) => ctrl.set === art.sets[0].name);
    if (ctrlI !== -1) {
      art.subBCs.splice(ctrlI, 1);
      if (subBCs[ctrlI].activated) {
        art.BCs[0].activated = true;
        if (subBCs[ctrlI].inputs) art.BCs[0].inputs = [...subBCs[ctrlI].inputs];
      }
    }
  }

  return {
    char,
    selfMCs,
    weapon: wp,
    subWpMCs,
    art,
    party,
    elmtMCs: { ...setup.elmtMCs, ampRxn: null },
    target: { ...setup.target }
  };
}

export function scroll(ref, mySetups, ID) {
  let count = 0;
  for (let st of mySetups) {
    if (st.ID === ID) {
      ref.current.scrollTop = 326 * count - (count > 0 ? 40 : 0);
      return;
    }
    if (st.type !== "combined") count++;
  }
}
