import data from "./index";

export const buffs = [
  {
    src: "Constellation 1",
    desc: () => data.constellation[0].desc,
    isGranted: (char) => char.constellation >= 1,
    affect: "self",
    addBnes: ({ bonuses }) => {
      bonuses.NA.CR += 0.15;
      bonuses.CA.CR += 0.15;
    }
  }
];
