import weapons from "../data/weapons";
import artifactBuffs from "../data/artifacts/artifactBuffs";
import characters from "../data/characters";
import { tfmRxns, ampRxnTrigElmts } from "../data/elements";
import {
  initiateATTRs,
  addArtStats,
  addWpSubStats,
  addArtBonuses,
  addWpBonuses,
  calcFinalATTRs
} from "./baseStats";
import { getRxnBnesFromEM } from "./helpers";

function addResonanceBuffs(ATTRs, bonuses, resonance) {
  for (let rsn of resonance) {
    if (rsn.activated) {
      if (rsn.name === "Fervent Flames") {
        ATTRs["ATK%"] += 25;
      } else if (rsn.name === "Shattering Ice") {
        ATTRs["CRIT Rate"] += 15;
      } else if (rsn.name === "Enduring Rock") {
        ATTRs["Shield Strength"] += 15;
        bonuses.AllDmg += 15;
      }
    }
  }
}

function addArtBuffs(ATTRs, bonuses, art) {
  for (let ctrl of art.subBCs) {
    if (ctrl.activated) {
      const { addBnes } = artifactBuffs[ctrl.set][ctrl.index];
      addBnes({ ATTRs, bonuses, inputs: ctrl.inputs });
    }
  }
  for (let ctrl of art.BCs) {
    if (ctrl.activated) {
      const { addBnes } = artifactBuffs[art.sets[0].name][ctrl.index];
      addBnes({ ATTRs, bonuses, inputs: ctrl.inputs });
    }
  }
}

function addWpBuffs(ATTRs, bonuses, weapon, subWpBCs, charData) {
  for (let wpType in subWpBCs) {
    for (let ctrl of subWpBCs[wpType]) {
      if (ctrl.activated) {
        const { refinement, inputs } = ctrl;
        const { addBnes } = weapons[wpType][ctrl.name].buffs[ctrl.index];
        addBnes({ ATTRs, bonuses, wpRfm: refinement, inputs, charData });
      }
    }
  }
  for (let ctrl of weapon.BCs) {
    if (ctrl.activated) {
      const { type, name, refinement: wpRfm } = weapon;
      const { addBnes } = weapons[type][name].buffs[ctrl.index];
      if (addBnes) {
        addBnes({ ATTRs, bonuses, wpRfm, inputs: ctrl.inputs, charData });
      }
    }
  }
}

function addTeammateBuffs(party, ATTRs, bonuses, rxnBnes, char, infusion) {
  for (let { name, BCs } of party) {
    for (let ctrl of BCs) {
      const { index, activated, inputs } = ctrl;
      const { addBnes, addFinalBnes } = characters[name].buffs[index];
      const fn = addBnes || addFinalBnes;
      if (activated && fn) {
        fn({
          ATTRs,
          bonuses,
          rxnBnes,
          char,
          charBCs: BCs,
          toSelf: false,
          inputs,
          infusion,
          party
        });
      }
    }
  }
}

function addWpFinalBuffs(ATTRs, weapon) {
  for (let ctrl of weapon.BCs) {
    if (ctrl.activated) {
      const { type, name } = weapon;
      const { addFinalBnes } = weapons[type][name].buffs[ctrl.index];
      if (addFinalBnes) {
        addFinalBnes({ ATTRs, wpRfm: weapon.refinement });
      }
    }
  }
}

function addSelfBuffs(
  isFinal,
  charBCs,
  ATTRs,
  bonuses,
  rxnBnes,
  char,
  infusion,
  party
) {
  for (let ctrl of charBCs) {
    const buff = characters[char.name].buffs[ctrl.index];
    if (buff.isGranted(char) && ctrl.activated) {
      const { addBnes, addFinalBnes } = buff;
      let fn = null;
      if (!isFinal && addBnes) fn = addBnes;
      else if (isFinal && addFinalBnes) fn = addFinalBnes;
      if (fn) {
        fn({
          ATTRs,
          bonuses,
          rxnBnes,
          char,
          charBCs,
          inputs: ctrl.inputs,
          toSelf: true,
          infusion,
          party
        });
      }
    }
  }
}

function calcFinalRxnBnes(ATTRs, rxnBnes, vision, infusion) {
  const { transformative, amplifying } = getRxnBnesFromEM(
    ATTRs["Elemental Mastery"]
  );
  Object.keys(tfmRxns).forEach((rxn) => {
    rxnBnes[rxn] += transformative;
  });
  Object.keys(ampRxnTrigElmts).forEach((rxn) => {
    rxnBnes[rxn] += amplifying;
  });
  const meltBn = rxnBnes.Melt;
  const vapBn = rxnBnes.Vaporize;
  rxnBnes.Melt = (vision === "Pyro" ? 2 : 1.5) * (1 + meltBn / 100);
  rxnBnes.Vaporize = (vision === "Pyro" ? 1.5 : 2) * (1 + vapBn / 100);
  if (infusion.NA !== vision) {
    rxnBnes.naMelt = (infusion.NA === "Pyro" ? 2 : 1.5) * (1 + meltBn / 100);
    rxnBnes.naVaporize = (infusion.NA === "Pyro" ? 1.5 : 2) * (1 + vapBn / 100);
  }
}

export default function getBuffedStats(
  charData,
  char,
  charBCs,
  weapon,
  subWpBCs,
  art,
  resonance,
  party,
  infusion
) {
  const wpData = weapons[weapon.type][weapon.name];
  const ATTRs = initiateATTRs(charData, char, wpData, weapon);
  const artSBnes = addArtStats(art.pieces, ATTRs);
  addWpSubStats(wpData, ATTRs, weapon.level);

  const bonuses = {
    AllDmg: 0,
    Elemental: { DMG: 0, CR: 0, CD: 0, Flat: 0 },
    Physical: { DMG: 0, CR: 0, CD: 0, Flat: 0 },
    NA: { DMG: 0, CR: 0, CD: 0, Flat: 0 },
    CA: { DMG: 0, CR: 0, CD: 0, Flat: 0 },
    PA: { DMG: 0, CR: 0, CD: 0, Flat: 0 },
    ES: { DMG: 0, CR: 0, CD: 0, Flat: 0 },
    EB: { DMG: 0, CR: 0, CD: 0, Flat: 0 }
  };
  const rxnBnes = {
    Superconduct: 0,
    Swirl: 0,
    "Electro-Charged": 0,
    Overloaded: 0,
    Shattered: 0,
    Melt: 0,
    Vaporize: 0
  };
  addArtBonuses(false, art.sets, ATTRs, charData, bonuses, rxnBnes);
  addWpBonuses(
    false,
    wpData,
    ATTRs,
    bonuses,
    weapon.refinement,
    charData,
    party
  );
  addResonanceBuffs(ATTRs, bonuses, resonance);
  addArtBuffs(ATTRs, bonuses, art);
  addWpBuffs(ATTRs, bonuses, weapon, subWpBCs, charData);
  addTeammateBuffs(party, ATTRs, bonuses, rxnBnes, char, infusion);
  addSelfBuffs(false, charBCs, ATTRs, bonuses, rxnBnes, char, infusion, party);

  calcFinalATTRs(ATTRs);
  addArtBonuses(true, art.sets, ATTRs, charData, bonuses, rxnBnes);
  addWpBonuses(
    true,
    wpData,
    ATTRs,
    bonuses,
    weapon.refinement,
    charData,
    party
  );
  addWpFinalBuffs(ATTRs, weapon);
  addSelfBuffs(true, charBCs, ATTRs, bonuses, rxnBnes, char, infusion, party);

  bonuses.Elemental.DMG += ATTRs[charData.vision + " DMG Bonus"];
  bonuses.Physical.DMG += ATTRs["Physical DMG Bonus"];
  calcFinalRxnBnes(ATTRs, rxnBnes, charData.vision, infusion);
  return { ATTRs, bonuses, rxnBnes, artSBnes };
}

