import styled from "styled-components";
import { Box } from "../../../styledCpns/Layout";
import { Select } from "../../../styledCpns/Inputs";
import { Text, LGreen } from "../../../styledCpns/DataDisplay";
import { pctOf } from "../../../helpers/misc";
import { artSSTypes } from "../../../data/artifacts/config";

export function MutableSSLine(props) {
  const { stat } = props;
  return (
    <Box className="mt-2 pt-1 flex align-center" bgColor="cardBg2">
      <Select
        color="white"
        fontSize="1.0625rem"
        className="appearance-none bg-for-icon white-arrow-bg artSS-select"
        value={stat.type}
        onChange={props.changeSSType}
      >
        {artSSTypes.map((type) => (
          <option key={type}>{type}</option>
        ))}
      </Select>
      <Text>+</Text>
      <SSVal value={stat.val} onChange={props.changeSSVal} />
      <span>{pctOf(stat.type)}</span>
    </Box>
  );
}

let SSVal = styled.input`
  margin-left: 3px;
  padding: 3px 8px 3px 0;
  width: 3.25rem;
  border: none;
  background-color: transparent;
  color: white;
  font-size: 1.0625rem;
  text-align: right;
  outline: none;
`;

export function ImmutableSSLine(props) {
  const { stat, ssSpace } = props;
  return (
    <Box
      className={`mt-2 pl-${ssSpace} pt-1 flex align-center`}
      bgColor="cardBg2"
    >
      <p className={"mr-" + ssSpace}>•</p>
      <Text>
        {stat.type}{" "}
        <LGreen>
          +{stat.val}
          {pctOf(stat.type)}
        </LGreen>
      </Text>
    </Box>
  );
}
