import { useContext, useState } from "react";
import { SetDBCt, MyStsCt } from "../../../../App";
import { ModalCarpet, Box } from "../../../../styledCpns/Layout";
import { TextInput } from "../../../../styledCpns/Inputs";
import { Text } from "../../../../styledCpns/DataDisplay";
import { ButtonBar, ShareLayout } from "../../../CommonCpns";
import {
  SelfMCsCt,
  WpRackCt,
  SubWpMCsCt,
  ArtChestCt,
  PartiesCt,
  ElmtMCsCt,
  TargetCt
} from "../../index";
import { useCloseModal } from "../../../../helpers/hooks";

export function SaveCtrl(props) {
  const { char, savedI, close } = props;
  const { name, ID, index } = props.tempSetups[savedI];
  const selfMCs = useContext(SelfMCsCt);
  const wpRack = useContext(WpRackCt);
  const subWpMCs = useContext(SubWpMCsCt);
  const artChest = useContext(ArtChestCt);
  const parties = useContext(PartiesCt);
  const elmtMCs = useContext(ElmtMCsCt);
  const { target } = useContext(TargetCt);
  const { setMySts, setTab, setChosenStID } = useContext(SetDBCt);
  const mySetups = useContext(MyStsCt);
  const existedI = mySetups.findIndex((st) => st.ID === ID);
  const isExisted = existedI >= 0;
  const [input, setInput] = useState(
    isExisted ? mySetups[existedI].name : char.name + " - " + name
  );

  const saveSetup = () => {
    let newID = ID;
    let parentStI;
    if (isExisted && mySetups[existedI].type === "combined") {
      parentStI = mySetups.findIndex(
        (st) => st.type === "complex" && st.allIDs[char.name] === ID
      );
      if (parentStI >= 0) {
        newID = mySetups[parentStI].ID;
      }
    }
    setMySts((prev) => {
      const sts = [...prev];
      if (parentStI >= 0) {
        sts[parentStI].shownID = ID;
      }
      const newSetup = {
        name: input,
        ID,
        type: isExisted ? sts[existedI].type : "original",
        char,
        selfMCs: selfMCs[index],
        weapon: wpRack[index],
        subWpMCs: subWpMCs[index],
        art: artChest[index],
        party: parties[index],
        elmtMCs: elmtMCs[index],
        target
      };
      if (isExisted) sts[existedI] = newSetup;
      else sts.unshift(newSetup);
      return sts;
    });
    props.closeSettings();
    setChosenStID(newID);
    setTab("Setups");
  };

  const ref = useCloseModal(close);
  return (
    <ModalCarpet>
      <Box
        ref={ref}
        className="p-4 flex-col white-glow-shadow"
        borderRadius="0.5rem"
        bgColor="cardBg1"
        style={{ maxWidth: "90%" }}
      >
        <Text
          className="mb-2 mx-auto"
          variant="h5"
          color="orange"
          fontWeight={700}
        >
          {existedI >= 0 ? "Modify OLD" : "Save NEW"} Setup
        </Text>
        <TextInput
          className="mt-1 mb-5 px-3 pt-3 pb-2"
          type="text"
          borderRadius="0.25rem"
          fontSize="1.5rem"
          align="center"
          value={input}
          onChange={(e) => {
            const { value } = e.target;
            if (value.length <= 32) setInput(e.target.value);
          }}
        />
        <ButtonBar
          texts={["Cancel", "Confirm"]}
          handlers={[
            close,
            () => {
              saveSetup();
              close();
            }
          ]}
        />
      </Box>
    </ModalCarpet>
  );
}

export function SharedCtrl(props) {
  const { char, sharedI, close } = props;
  const { name, index } = props.tempSetups[sharedI];
  const selfMCs = useContext(SelfMCsCt);
  const wpRack = useContext(WpRackCt);
  const subWpMCs = useContext(SubWpMCsCt);
  const artChest = useContext(ArtChestCt);
  const parties = useContext(PartiesCt);
  const elmtMCs = useContext(ElmtMCsCt);
  const { target } = useContext(TargetCt);
  const data = {
    char,
    selfMCs: selfMCs[index],
    weapon: wpRack[index],
    subWpMCs: subWpMCs[index],
    art: artChest[index],
    party: parties[index],
    elmtMCs: elmtMCs[index],
    target
  };
  return <ShareLayout setupName={name} data={data} close={close} />;
}
