import { useContext } from "react";
import { MyStsCt, SetDBCt } from "../../App";
import { deepCopy } from "../../helpers/misc";
import Setup from "./Setup";

export default function ComplexSetup(props) {
  const { setup } = props;
  const mySetups = useContext(MyStsCt);
  const { setMySts, setChosenStID } = useContext(SetDBCt);
  const shownSetup = mySetups.find((st) => st.ID === setup.shownID);

  const index = mySetups.findIndex((st) => st.ID === setup.ID);
  const uncombine = () => {
    setMySts((prev) => {
      const sts = deepCopy(prev);
      for (let member in setup.allIDs) {
        sts[sts.findIndex((st) => st.ID === setup.allIDs[member])].type =
          "original";
      }
      return sts;
    });
    setTimeout(() => {
      setMySts((prev) => {
        const sts = deepCopy(prev);
        sts.splice(index, 1);
        return sts;
      });
      setChosenStID(setup.shownID);
    }, 100);
  };

  return (
    <Setup
      setup={shownSetup}
      stName={setup.name}
      chosen={props.chosen}
      choose={props.choose}
      edit={() => props.setImportedSt(shownSetup)}
      share={() => props.setSharedID(setup.shownID)}
      remove={uncombine}
      add={props.add}
      buildTmSt={(tmI) => props.buildTmSt(shownSetup, tmI)}
      allIDs={setup.allIDs}
      switchMember={(memberID) =>
        setMySts((prev) => {
          const sts = deepCopy(prev);
          sts[index].shownID = memberID;
          return sts;
        })
      }
      result={props.result}
    />
  );
}
