import { LGreen, Green, Red } from "../../../../styledCpns/DataDisplay";
import actvTalents from "./talents";
import { getXtraTalentLv } from "../../../../calculators/helpers";

export const buffs = [
  {
    src: "Elemental Burst",
    desc: ({ toSelf, char, party, inputs }) => (
      <>
        Omen increases{" "}
        <Green>{buffs[0].bnValue(toSelf, char, party, inputs)}%</Green>{" "}
        <LGreen>DMG taken</LGreen> by opponents. (This is actually a DMG bonus.)
      </>
    ),
    isGranted: () => true,
    affect: "party",
    labels: ["Elemental Burst Level"],
    inputs: [1],
    inputTypes: ["text"],
    maxs: [13],
    addBnes: ({ bonuses, toSelf, char, party, inputs }) => {
      bonuses.AllDmg += buffs[0].bnValue(toSelf, char, party, inputs);
    },
    bnValue: (toSelf, char, party, inputs) => {
      const level = toSelf
        ? char["Elemental Burst"] + getXtraTalentLv(actvTalents[2], char, party)
        : inputs[0];
      return Math.min(40 + level * 2, 60);
    }
  },
  {
    src: "Ascension 4 Passive Talent",
    desc: ({ ATTRs }) => (
      <>
        Increases Mona's <LGreen>Hydro DMG Bonus</LGreen> by a degree equivalent
        to <Green>20%</Green> of her <LGreen>Energy Recharge</LGreen> rate.{" "}
        <Red>Hydro DMG Bonus: {buffs[1].bnValue(ATTRs)}%.</Red>
      </>
    ),
    isGranted: (char) => char.ascension >= 4,
    affect: "self",
    addBnes: ({ ATTRs }) => {
      ATTRs["Hydro DMG Bonus"] += buffs[1].bnValue(ATTRs);
    },
    bnValue: (ATTRs) => Math.round(ATTRs["Energy Recharge"] * 2) / 10
  },
  {
    src: "Constellation 1",
    desc: () => (
      <>
        When any of your own party members hits an opponent affected by an Omen,{" "}
        <LGreen>Electro-Charged</LGreen>, <LGreen>Vaporize</LGreen>, and{" "}
        <LGreen>Hydro Swirl DMG</LGreen> increases by <Green>15%</Green> for 8s.
      </>
    ),
    isGranted: (char) => char.constellation >= 1,
    affect: "party",
    addBnes: ({ rxnBnes }) => {
      rxnBnes["Electro-Charged"] += 15;
      rxnBnes.Swirl += 15;
      rxnBnes.Vaporize += 15;
    }
  },
  {
    src: "Constellation 4",
    desc: () => (
      <>
        When any party member attacks an opponent affected by an Omen, their{" "}
        <LGreen>CRIT Rate</LGreen> is increased by <Green>15%</Green>.
      </>
    ),
    isGranted: (char) => char.constellation >= 4,
    affect: "party",
    addBnes: ({ ATTRs }) => {
      ATTRs["CRIT Rate"] += 15;
    }
  },
  {
    src: "Constellation 6",
    desc: () => (
      <>
        Upon entering Illusory Torrent, Mona gains a <Green>60%</Green>{" "}
        <LGreen>DMG increase</LGreen> of her next{" "}
        <LGreen>Charged Attack</LGreen> per second of movement (up to{" "}
        <Green>180%</Green>) for 8s.
      </>
    ),
    isGranted: (char) => char.constellation >= 6,
    affect: "self",
    selfLabels: ["Stacks"],
    inputs: [1],
    inputTypes: ["select"],
    maxs: [3],
    addBnes: ({ bonuses, inputs }) => {
      bonuses.CA.DMG += 60 * inputs[0];
    }
  }
];
