import { Text, Img } from "../../../styledCpns/DataDisplay";
import { Box } from "../../../styledCpns/Layout";
import { InfoSign } from "../../CommonCpns";
import { wikia } from "../../../data/misc";
import characters from "../../../data/characters";

export default function Overview(props) {
  const { beta, icon, constellation } = characters[props.charName];
  return (
    <div className={"overview " + (props.atDetails ? "close" : "open")}>
      <Box className="full-h flex-col justify-between hide-sb">
        {constellation.map((cons, i) => {
          const available = props.consLv >= i + 1 ? "" : " unavailable";
          return (
            <div key={i} className={"flex" + (i ? " mt-2" : "")}>
              <div className="flex-center">
                <Img
                  className={"my-1 mr-2 pointer" + available}
                  width="3.25rem"
                  src={wikia + (beta ? icon : cons.image) + ".png"}
                  alt=""
                  draggable={false}
                  onClick={() => props.handleClick(i)}
                />
              </div>
              <div
                className={"px-2 grow-1 flex align-center contain-info_sign"}
                onClick={() => props.toDetails(i)}
              >
                <Text
                  className={"mr-2" + available}
                  variant="h6"
                  fontWeight={700}
                >
                  {cons.name}
                </Text>
                <div className="ml-auto">
                  <InfoSign />
                </div>
              </div>
            </div>
          );
        })}
      </Box>
    </div>
  );
}
