import { Section, SetterCheckbox } from "../../../Calculator/Modifiers/CmnCpns";
import { Text } from "../../../../styledCpns/DataDisplay";

export default function ModReader(props) {
  return (
    <Section
      immutable={true}
      heading={"• " + props.heading}
      desc={props.desc}
      setters={
        <SetterSection
          labels={props.labels}
          inputTypes={props.inputTypes}
          inputs={props.inputs}
        />
      }
    />
  );
}

function SetterSection(props) {
  if (!props.labels) {
    return null;
  }
  return props.labels.map((label, i) => {
    const isCheck = props.inputTypes[i] === "check";
    return (
      <div key={i} className="mt-1 flex justify-end align-center">
        <Text className={isCheck ? "mr-3" : "mr-2"} align="right">
          {label}
        </Text>
        {isCheck ? (
          <SetterCheckbox type="checkbox" checked={true} readOnly />
        ) : (
          <Text fontWeight={700} color="orange">
            {props.inputs[i]}
          </Text>
        )}
      </div>
    );
  });
}
