import { useState, useContext } from "react";
import { MyArtsCt } from "../../App";
import { Box, ModalCarpet, ModalInner } from "../../styledCpns/Layout";
import { StyledHeading, Cell, Card } from "../../styledCpns/Database";
import { IconBtn } from "../../styledCpns/Inputs";
import { ButtonBar, EquippedChar, CloseBtn, NoItems } from "../CommonCpns";
import ArtFilter from "./ArtFilter";
import Thumb from "./Thumb";
import MyArtCard from "./MyArtCard";
import artifacts from "../../data/artifacts";
import { artNames } from "../../data/misc";
import { getArts } from "../MyArtifacts";
import { useCloseModal } from "../../helpers/hooks";
import { initArtStatFilter } from "../../helpers/misc";

export default function ArtInventory(props) {
  const { artType } = props;
  const [filterOn, setFilterOn] = useState(false);

  const myArtifacts = useContext(MyArtsCt);
  let filteredData = myArtifacts.filter((art) => art.type === artType);
  const [stats, setStats] = useState(initArtStatFilter());
  const [sets, setSets] = useState([]);
  filteredData = getArts(filteredData, [], sets, stats);

  const [chosenI, setChosenI] = useState(0);
  const chosenArt = filteredData[chosenI];

  const ref = useCloseModal(props.close);
  return (
    <ModalCarpet>
      <ModalInner ref={ref} className="no-select">
        <Box height="10%" className="p-2">
          <StyledHeading>
            {window.innerWidth > 380 && (
              <p className="heading_text">{artNames[artType]}</p>
            )}
            <CloseBtn className="close_btn" onClick={props.close} />
            <IconBtn
              icon="fas fa-filter"
              className={"filter_btn glow-on-hover" + (filterOn ? " on" : "")}
              onClick={() => setFilterOn(!filterOn)}
            />
            <ArtFilter
              filterOn={filterOn}
              arts={filteredData}
              artType={artType}
              filter={{ stats, setStats, sets, setSets }}
              close={() => setFilterOn(false)}
            />
          </StyledHeading>
        </Box>
        <Box height="90%" className="pt-2 pr-3 pb-3 pl-2">
          <div className="full-h flex hide-sb">
            {filteredData.length ? (
              <Rack
                data={filteredData}
                chosenI={chosenI}
                setChosenI={setChosenI}
              />
            ) : (
              <Box className="grow-1" minWidth="18.75rem">
                <NoItems type={artNames[artType]} />
              </Box>
            )}
            <div className="flex-col justify-between">
              <Card>
                <Box className="hide-sb" width="16rem" height="24rem">
                  {chosenArt ? (
                    <MyArtCard art={chosenArt} mutable={false} ssSpace={3} />
                  ) : null}
                </Box>
                {chosenArt && chosenArt.user !== props.owner ? (
                  <ButtonBar
                    className="mt-4"
                    variants={["positive"]}
                    texts={[props.btnText]}
                    handlers={[
                      () => {
                        props.btnClick(chosenArt);
                        props.close();
                      }
                    ]}
                  />
                ) : null}
              </Card>
              {chosenArt?.user ? <EquippedChar user={chosenArt.user} /> : null}
            </div>
          </div>
        </Box>
      </ModalInner>
    </ModalCarpet>
  );
}

function Rack(props) {
  const [clickedI, setClickedI] = useState(null);
  return (
    <Box
      className="pr-2 grow-1 full-h overflow-auto hide-sb"
      minWidth="18.75rem"
    >
      <div className="flex-wrap">
        {props.data.map((art, i) => {
          const { icon, rarity = "Gold" } = artifacts[art.set][art.type];
          return (
            <Cell key={i}>
              <Thumb
                item={art}
                icon={icon}
                rarity={rarity}
                chosen={i === props.chosenI}
                clicked={i === clickedI}
                handleMouseDown={() => setClickedI(i)}
                choose={() => {
                  setClickedI(null);
                  props.setChosenI(i);
                }}
              />
            </Cell>
          );
        })}
      </div>
    </Box>
  );
}
