import { useContext } from "react";
import { SetDBCt } from "../../../App";
import { Box } from "../../../styledCpns/Layout";
import { Card } from "../../../styledCpns/Database";
import TalentList from "../../Readers/TalentList";
import { deepCopy } from "../../../helpers/misc";

export default function SectionTalent(props) {
  const { setMyChars } = useContext(SetDBCt);
  const upgradeTl = (type, level) =>
    setMyChars((prev) => {
      const newChars = deepCopy(prev);
      newChars[props.charI][type] = level;
      return newChars;
    });
  return (
    <Card className="ml-2">
      <Box className="px-1" width="18.75rem" height="28.35rem">
        <TalentList char={props.char} upgradeTl={upgradeTl} />
      </Box>
    </Card>
  );
}
